import React, { Component } from "react";
import HomeBox from "./../homeBox/HomeBox";
import MinOrderNotification from "./../minOrderNotification/MinOrderNotification";
import "./heroBanner.scss";

export default class HeroBanner extends Component {
  state = {
    minOrder: sessionStorage.getItem("minimumOrderAmount"),
  };
  render() {
    const { customerDetails } = this.props;
    let redirectUrl = sessionStorage.getItem("Zipcode") ? '/productlist' : '/home';
    const userName =
      customerDetails &&
      customerDetails.accountContacts &&
      customerDetails.accountContacts.length > 0 &&
      customerDetails.accountContacts[0].firstName;
    return (
      <>
        {/* <MinOrderNotification message={this.state.minOrder} /> */}
        <div className="hero-banner">
          <img
            className="hero-background"
            src="./images/Homepag_banner_5.png"
            alt="hero background"
            role="presentation"
          />
          <img
            className="hero-left"
            src="./images/popcorn_left.png"
            alt="hero left"
            role="presentation"
          />
          <img
            className="hero-right"
            src="./images/cookies_rt.png"
            alt="hero right"
            role="presentation"
          />

          <div className="home-box-wrapper">
            <div className="home-box-wrapper-inner">
              <h1 className="user-name">
                <span className="welcome-msg">welcome back,</span>
                <span className="name">{userName}</span>
              </h1>
              <HomeBox
                heading="Show My Previous Orders"
                description="Shop by viewing what you've ordered before. Let your history help build your box!"
                viewAllText="View Now"
                viewAllLink="/profile?order"
                customClass="previous-orders"
              />
              
              <HomeBox
                heading="Show Me Everything"
                description="Well show you all the snacks available in your area. Build your box with no limits!"
                viewAllText="View Now"
                viewAllLink={redirectUrl}
                customClass="all-products"
              />
            </div>
          </div>
        </div>
      </>
    );
  }
}
