import React, { Component } from "react";
import Truncate from "react-truncate";
import {
  Grid,
  Card,
  CardMedia,
  Tooltip,
  Button,
  IconButton,
} from "@material-ui/core";
import StarIcon from "@material-ui/icons/Star";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import QuantityCount from "../commonComponents/quantityCount/QuantityCount";
import "./productTout.scss";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Color, { Palette } from "color-thief-react";
import { hexToRgbA } from "./../Utils";

export default class ProductTout extends Component {
  state = {
    colors: [],
    selectedProductId: "",
    isFavourite: false,
    showSizeList: false,
  };
  onImageClick = () => {
    const { detailedInfo, productInfo, index } = this.props;
    detailedInfo(productInfo, index);
  };

  selectedProduct = (productInfo) => {
    this.setState({
      selectedProductId: productInfo.productId,
    });
  };

  getQuantitySection = () => {
    const { warningMsgFunc, productInfo, selectedItems } = this.props;

    return productInfo.map((product, index) => (
      <QuantityCount
        primaryProduct={productInfo[0]}
        productInfo={product}
        key={index}
        warningMsgFunc={warningMsgFunc}
        selectedItems={selectedItems}
        sizeName={product.size}
        price={product.price}
      />
    ));
  };

  render() {
    const { productKey, productInfo } = this.props;
    const { showSizeList, selectedProductId } = this.state;
    const {
      productId,
      brand,
      productDescription,
      shortDescription,
      image,
      availableQty,
    } = productInfo[0];

    const prodDesc = productDescription
      ? productDescription.replace(/(<([^>]+)>)/gi, "").replace("&nbsp;", " ")
      : shortDescription.replace(/(<([^>]+)>)/gi, "").replace("&nbsp;", " ");

    const selectedProduct =
      selectedProductId && selectedProductId === productId;

    return (
      <Color key={productKey} src={image} crossOrigin="*" format="hex">
        {({ data, loading }) => {
          const rgbaColor = data ? hexToRgbA(data) : "#000000";
          return (
            <Card
              className={`product-box-bundle-card bundle-card ${
                selectedProduct ? "active" : ""
              }`}
              key={productId}
              style={{
                backgroundImage: selectedProduct
                  ? `linear-gradient(to bottom, ${rgbaColor} ,${data})`
                  : "transparent",
              }}
            >
              <Grid
                container
                spacing={0}
                justify="space-between"
                className="product-options"
              >
                <Grid item>
                  <IconButton
                    className="star"
                    id="toggleFavourite"
                    aria-label="add to favourite"
                    onClick={() =>
                      this.setState({ isFavourite: !this.state.isFavourite })
                    }
                  >
                    {!this.state.isFavourite ? (
                      <StarBorderIcon />
                    ) : (
                      <StarIcon />
                    )}
                  </IconButton>
                </Grid>
                <Grid item>
                  <IconButton
                    className="more-vert"
                    id="moreOption"
                    aria-label="product options"
                  >
                    <MoreVertIcon />
                  </IconButton>
                </Grid>
              </Grid>
              <Button
                id={`productDetailDialog-${productId}`}
                name={`productDetailDialog-${productId}`}
                aria-label="click to open product detail dialog"
                className="media-card-wrapper"
                onClick={this.onImageClick}
              >
                <CardMedia
                  component="img"
                  alt={prodDesc}
                  image={
                    typeof image === "string"
                      ? image
                      : "../images/product-placeholder.png"
                  }
                />
              </Button>
              <div className="shadow"></div>
              {!selectedProduct && (
                <>
                  <div className="product-brand">{brand}</div>
                  <Tooltip placement="top" title={prodDesc}>
                    <div className="product-heading">
                      <Truncate lines={2} ellipsis="...">
                        {prodDesc}
                      </Truncate>
                    </div>
                  </Tooltip>
                  <div className="product-brand">
                    {`${productInfo.length} sizes available`}
                  </div>
                </>
              )}
              {selectedProduct && availableQty > 0 && (
                <>
                  <div className="product-brand">
                    {`select sizes (${productInfo.length} available)`}
                  </div>
                  <div className={`quantity-box ${showSizeList ? "show" : ""}`}>
                    {this.getQuantitySection()}
                  </div>
                  {productInfo.length > 2 && !showSizeList && (
                    <ExpandMoreIcon
                      aria-label="more sizes"
                      fontSize="large"
                      className="sizes-arrow-down"
                      onClick={() => this.setState({ showSizeList: true })}
                    />
                  )}
                </>
              )}
              {selectedProduct && availableQty <= 0 && (
                <div className="out-of-stock">Out of stock</div>
              )}
              {!selectedProduct && (
                <div className="product-action">
                  <Button
                    onClick={() => this.selectedProduct(productInfo[0])}
                    className="selected-product"
                  >
                    select
                  </Button>
                </div>
              )}
            </Card>
          );
        }}
      </Color>
    );
  }
}
