import React, { Component } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Typography,
} from "@material-ui/core";
import { connect } from "react-redux";
import {
  actiongetProductDetail,
  actionCheckProductAvailability,
} from "../../actions/ProductAction";
import Spinner from "../Spinner";
import QuantityCount from "./../commonComponents/quantityCount/QuantityCount";
import SnacksAlertMessage from "./../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import Color, { Palette } from "color-thief-react";
import { hexToRgbA } from "./../Utils";
import { EventNames, trackEvent } from "../../appinsights/EventTrack";
import "./ProductDetail.scss";
import { DISPLAY_HEADING } from "config/constants/content.constant";

class ProductDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props,
      isLoaded: false,
      products: "",
      productItems: {},
      productItemsCopy: {},
      selectedItems: {},
      msg: null,
    };
  }

  /**
   *
   * @description loading the product detail api.
   * @memberof ProductDetail
   */
  componentDidMount() {
    const { productData, items } = this.props;
    let productItems = items;
    const productItemsCopy = { ...items };
    this.setState({ productItems, productItemsCopy });
    if (productData && productData[0]) {
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DETAILS,
        EventNames.Event.EVENT_PAGE_VIEW,
        window.location.origin,
        window.location.pathname,
        { product: productData[0] }
      );
      const payload = {
        prodId: productData[0].id,
        locationNumber: sessionStorage.getItem("_lo_No") || null,
      };
      this.props.actiongetProductDetail(payload).then((response) => {
        console.log("resp", response);
        if (response.length > 0) {
          response[0].availableQty = productData[0].availableQty;
          response[0].quantity = productData[0].quantity;
          if (parseInt(productData[0].availableQty) <= 0) {
            response[0].isOutofstock = 0;
          } else {
            response[0].isOutofstock = 1;
          }
          this.setState({ products: response[0] });
        }
      });
    }
  }

  UNSAFE_componentWillReceiveProps({ someProp }) {
    this.setState({ ...this.state, someProp });
  }

  getQuantitySection = () => {
    const {
      warningMsgFunc,
      productData,
      selectedItems,
      closeAndUpdate,
    } = this.props;

    return productData.map((product, index) => (
      <QuantityCount
        primaryProduct={productData[0]}
        productInfo={product}
        key={index}
        //callFromDetail
        closeAndUpdate={closeAndUpdate}
        warningMsgFunc={warningMsgFunc}
        selectedItems={selectedItems}
        sizeName={product.size}
        price={product.price}
      />
    ));
  };
  render() {
    const { products, msg } = this.state;
    return (
      <React.Fragment>
        {msg && <SnacksAlertMessage msg={msg} />}
        {products ? (
          <Color
            src={products.image.Product_Image}
            crossOrigin="Anonymous"
            format="hex"
          >
            {({ data, loading }) => {
              const rgbaColor = data ? hexToRgbA(data) : "#000000";
              return (
                <div
                  className="product-detail-wrapper"
                  style={{
                    backgroundImage: `linear-gradient(to bottom, ${rgbaColor} ,${data})`,
                  }}
                >
                  <Typography
                    variant="h1"
                    className="heading-hidden"
                    aria-hidden="true"
                  >
                    Product Detail
                  </Typography>
                  <Grid container className="product-info-container">
                    <Grid item sm="6">
                      <div className="product-detail-image-outer">
                        <div className="product-detail-image">
                          <img
                            src={products.image.Product_Image}
                            alt="Product"
                          />
                          <div className="shadow"></div>
                        </div>
                      </div>
                      <Accordion className="accordion-wrapper">
                        <AccordionSummary
                          expandIcon={
                            <svg
                              width="21"
                              height="10"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <g
                                stroke="#fff"
                                stroke-width="4"
                                fill="none"
                                fill-rule="evenodd"
                                stroke-linecap="square"
                                stroke-linejoin="round"
                              >
                                <path d="M20.562.292l-9.625 6.416M.438.292l9.625 6.416" />
                              </g>
                            </svg>
                          }
                          aria-controls="panel2a-content"
                          id="panel2a-header"
                          className="accordion-header"
                        >
                          <Typography variant="h2">description</Typography>
                        </AccordionSummary>
                        <AccordionDetails className="accordion-body">
                          <div className="product-desc">
                            {products.description}
                          </div>
                        </AccordionDetails>
                      </Accordion>
                    </Grid>
                    <Grid item sm="6" className="product-info-div">
                      <div className="product-brand">{products.brand}</div>
                      <div className="product-name">
                        {products.shortDescription}
                      </div>
                      <div className="product-detail-actions">
                        {products.availableQty <= 0 && (
                          <div className="out-of-stock">
                            {DISPLAY_HEADING.OUT_OF_STOCK}
                          </div>
                        )}
                        {products.availableQty > 0 && this.getQuantitySection()}
                      </div>

                      <Accordion className="accordion-wrapper">
                        <AccordionSummary
                          expandIcon={
                            <svg
                              width="21"
                              height="10"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <g
                                stroke="#fff"
                                stroke-width="4"
                                fill="none"
                                fill-rule="evenodd"
                                stroke-linecap="square"
                                stroke-linejoin="round"
                              >
                                <path d="M20.562.292l-9.625 6.416M.438.292l9.625 6.416" />
                              </g>
                            </svg>
                          }
                          aria-controls="panel3a-content"
                          id="panel3a-header"
                          className="accordion-header"
                        >
                          <Typography variant="h2">nutrition</Typography>
                        </AccordionSummary>
                        <AccordionDetails className="accordion-body">
                          <img
                            src={products.image.Nutrition_Image}
                            alt="Nutrition Facts"
                          />
                        </AccordionDetails>
                      </Accordion>
                      <Accordion className="accordion-wrapper">
                        <AccordionSummary
                          expandIcon={
                            <svg
                              width="21"
                              height="10"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <g
                                stroke="#fff"
                                stroke-width="4"
                                fill="none"
                                fill-rule="evenodd"
                                stroke-linecap="square"
                                stroke-linejoin="round"
                              >
                                <path d="M20.562.292l-9.625 6.416M.438.292l9.625 6.416" />
                              </g>
                            </svg>
                          }
                          aria-controls="panel1a-content"
                          id="panel1a-header"
                          className="accordion-header"
                        >
                          <Typography variant="h2">ingredients</Typography>
                        </AccordionSummary>
                        <AccordionDetails className="accordion-body">
                          {/* <Typography>{products.ingredients}</Typography> */}
                          <img
                            src={products.image.Ingredients_Image}
                            alt="Ingredients"
                          />
                        </AccordionDetails>
                      </Accordion>
                    </Grid>
                  </Grid>
                </div>
              );
            }}
          </Color>
        ) : (
          <Grid container justify="center">
            <Grid item>
              {products === undefined ? (
                <h1 className="noproducts-text">Technical Issue</h1>
              ) : (
                <Spinner />
              )}
            </Grid>
          </Grid>
        )}
      </React.Fragment>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    products: state.reducer.products,
    productInventory: state.reducer.productInventory,
  };
};
export default connect(mapStateToProps, {
  actiongetProductDetail,
  actionCheckProductAvailability,
})(ProductDetail);
