import React, { Component } from "react";
import { connect } from "react-redux";
import Spinner from "../../Spinner";
import moment from "moment";
import DialogBox from "./../../commonComponents/dialogBox/DialogBox";
import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  Typography,
  Select,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { fetchOrderDetails } from "./../../../actions/ProductAction";
import ProductImages from "./ProductImages";
import {
  trackEvent,
  EventNames
} from '../../../appinsights/EventTrack';

class OrderHistory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orderHistoryDetails: [],
      spinner: false,
      showItemInfo: -1,
      isShowItemInfo: false,
      orderImages: [],
      dialogOpen: false,
      cartContent: "",
      filterBy: "7",
      selectedOrderId: 0,
      selectedOrder: sessionStorage.getItem("selectedOrder"),
    };
  }

  componentDidMount() {
    let filterValue = this.state.filterBy;
    this.setState({spinner:true})
    this.props.fetchOrderDetails(filterValue).then(res => {
      this.setState({spinner:false})
    }).catch(err => {
      this.setState({spinner:false})
   });
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (
      newProps.orderHistory &&
      newProps.orderHistory.length > 0 &&
      localStorage.getItem("accessToken")
    ) {
      this.setState({
        orderHistoryDetails: newProps.orderHistory,
        spinner: false,
      });
    } else {
      this.setState({ spinner: false });
    }
  }

  updateCartItems = (items) => {
    sessionStorage.setItem("cartItems", JSON.stringify(items));
    sessionStorage.setItem("selectedOrder", this.state.selectedOrderId);

    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (zipcodeVerified) {
      window.location.href = "/cart";
    }
  };

  confirmUpdateCart = (items, orderId) => {
    const cartItems = sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    if (cartItems.length === 0) {
      this.setState({ selectedOrderId: orderId }, () => {
        this.updateCartItems(items);
      });
    } else {
      this.setState({
        selectedOrderId: orderId,
        dialogOpen: true,
        cartContent: items,
      });
    }
  };

  dialogClose() {
    this.setState({ dialogOpen: false, cartContent: "" });
  }

  dialogCloseConfirm() {
    this.updateCartItems(this.state.cartContent);
    this.dialogClose();
  }

  showItemDetail = (index) => {
    this.setState(
      {
        isShowItemInfo:
          index != this.state.showItemInfo ? true : !this.state.isShowItemInfo,
      },
      () =>
        this.setState({
          showItemInfo: index,
          // orderImages:order.items
        })
    );
  };

  handleOrderFIlter(value) {
    trackEvent(EventNames.Action.ACTION_ORDER_HISTORY_FILTER, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, {filterBy: value})
    this.setState({ filterBy: value, spinner: true });
    this.props
      .fetchOrderDetails(value)
      .then((res) => {
        this.setState({ spinner: false });
      })
      .catch((err) => {
        this.setState({ spinner: false });
      });
  }

  render() {
    const { dialogOpen } = this.state;
    return (
      <div className="profile-info order-history">
        {this.state.spinner && (
          <div>
            <Spinner />
          </div>
        )}

        <DialogBox
          dialogClose={() => this.dialogClose()}
          dialogOpen={dialogOpen}
          dialogHeading="Are you sure you want to Replace your Cart?"
          dialogWarning
          dialogCloseConfirm={() => this.dialogCloseConfirm()}
          ariaLabelYes={"Yes i would like to Replace my cart"}
          ariaLabelCancel={"No I don't want to replace my cart"}
        />

        <Grid container spacing={6} justify="space-between">
          {/* Biling Address */}
          <Grid item xs={12} sm={12}>
            <Grid
              container
              spacing={2}
              alignItems="center"
              className="title-mini"
            >
              <Grid item xs={12} sm={8}>
                <Typography variant="h2" component="h2">
                  Total Orders ({this.state.orderHistoryDetails.length})
                </Typography>
              </Grid>
              <Grid item xs={12} sm={4} className="text-right">
                <FormControl
                  fullWidth
                  className="form-group phone-select phone-select-full"
                  variant="outlined"
                >
                  <InputLabel className="form-label" htmlFor="filter">
                    Filter
                  </InputLabel>
                  <Select
                    native
                    id="filter"
                    name="filter"
                    onChange={(e) => this.handleOrderFIlter(e.target.value)}
                  >
                    <option value="7">Last 7 days</option>
                    <option value="30">Last 30 days</option>
                    <option value="all">All Results</option>
                  </Select>
                </FormControl>
              </Grid>
            </Grid>

            <div className="order-history-content">
              {this.state.orderHistoryDetails.length > 0 ? (
                this.state.orderHistoryDetails.map((order, index) => (
                  // const formattedDate = moment(order).format("MM/DD/YY");
                  <>
                    <Grid
                      container
                      spacing={2}
                      alignItems="center"
                      className="profile-card history-card"
                    >
                      <Grid item xs={12} sm={6}>
                        <div className="profile-card-single">
                          <span className="key">Order Number: </span>
                          <span className="val">{order.orderId}</span>
                        </div>
                        <div className="profile-card-single">
                          <span className="key">Date Ordered: </span>
                          <span className="val">
                            {moment(order.orderDate).format("MM/DD/YY")}
                          </span>
                        </div>
                        <div className="profile-card-single">
                          <span className="key">Shipping To: </span>
                          <span className="val">
                            {`${order.shipping.shippingFirstname}  
                              ${order.shipping.shippingLastname}  
                              ${order.shipping.shippingAddressline1}, ${order.shipping.shippingCity}, 
                              ${order.shipping.shippingState},
                              ${order.shipping.shippingZip}`}
                          </span>
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={2}>
                        <div className="highlighed">
                          <Button
                            variant="contained"
                            color="primary"
                            className="btn-mui btn-blue btn-link"
                            id="reorder"
                            aria-label={
                              order.items.length + " items in the order"
                            }
                            aria-pressed={this.state.isShowItemInfo}
                            onClick={() => this.showItemDetail(index)}
                          >
                            ({order.items.length}) Items
                            {this.state.showItemInfo == index &&
                            this.state.isShowItemInfo ? (
                              <i className="fa fa-angle-up"></i>
                            ) : (
                              <i className="fa fa-angle-down"></i>
                            )}
                          </Button>
                        </div>
                        <div className="key">Total: ${order.totalPrice}</div>
                      </Grid>
                      <Grid item xs={12} sm={2}>
                        <div className="key">Tracking:</div>
                        <div>
                          <a
                            className="highlighed"
                            target="blank"
                            href={`${"https://www.ups.com/track?loc=en_US&tracknum="} ${
                              order.trackingId
                            } ${"&requester=WT/trackdetails"}`}
                            aria-label={`${"Tracking Id"} ${
                              order.trackingId ? order.trackingId : "N/A"
                            }`}
                          >
                            {order.trackingId ? order.trackingId : "N/A"}
                          </a>
                        </div>
                      </Grid>
                      <Grid item xs={12} sm={2}>
                        <div className="key">
                          Status:{" "}
                          <span className="order-status">
                            {order.orderSate}
                          </span>
                        </div>
                        <div>
                          <Button
                            variant="contained"
                            color="primary"
                            className="btn-mui btn-blue btn-sm"
                            id="reorder"
                            aria-label="Reorder"
                            onClick={() =>
                              this.confirmUpdateCart(order.items, order.orderId)
                            }
                          >
                            Reorder
                          </Button>
                        </div>
                      </Grid>
                    </Grid>

                    {this.state.showItemInfo == index ? (
                      <div
                        className={
                          this.state.isShowItemInfo
                            ? "ordered-cart open"
                            : "ordered-cart"
                        }
                      >
                        <ProductImages orderImages={order.items} />
                      </div>
                    ) : (
                      ""
                    )}
                  </>
                ))
              ) : (
                <div className="no-items">No Order History Available</div>
              )}
            </div>

            <div className="order-history-action">
              <Link
                className="btn-mui btn-outlined"
                id="ContinueShopping"
                aria-label="Continue Shopping"
                to="/productlist"
              >
                Continue Shopping
              </Link>
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    orderHistory: state.reducer.orderHistory,
  };
};

export default connect(mapStateToProps, { fetchOrderDetails })(OrderHistory);
