import React, { Component } from "react";
import { Grid, Button } from "@material-ui/core";
import { Link,Redirect } from "react-router-dom";
import "./homeBox.scss";
import {
  trackEvent, EventNames
} from '../../../appinsights/EventTrack';

export default class HomeBox extends Component {
  render() {
    
    const {
      customClass,
      heading,
      description,
      viewAllText,
      viewAllLink,
    } = this.props;
    
    let reload = () => {
      trackEvent(viewAllLink === '/profile?order' ? EventNames.Action.ACTION_SHOW_PREVIOUS_ORDERS : EventNames.Action.ACTION_SHOW_EVERYTHING, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname)
      window.location = viewAllLink;
    }
    return (
      <Grid
        item
        xs={12}
        lg={6}
        sm={6}
        className={`home-box-item ${customClass || ""}`}
      >
        <Link
          className="home-box-item-link"
          to={viewAllLink}
          aria-label={`${heading}`}
          //aria-describedby={`${description}`}
          onClick={reload}
        >
          <h2 className="heading">{heading}</h2>
          <p className="description">{description}</p>
          <Button
            disabled
            className="link"
            aria-label={`${viewAllText} ${customClass}`}
          >
            {viewAllText} <i className="fa fa-angle-right"></i>
          </Button>
        </Link>
      </Grid>
    );
  }
}
