import React, { Component } from "react";
import BottomScrollListener from "react-bottom-scroll-listener";
import {
  Dialog,
  DialogContent,
  Button,
  Grid,
  Container,
} from "@material-ui/core";
import SectionHeading from "../commonComponents/sectionHeading/SectionHeading";
import UrlConstants from "../../config/UrlConstants";
import APIUtil from "../../config/APIUtil";
import CircularProgress from "@material-ui/core/CircularProgress";
import ProductTout from "../ProductTout/ProductTout";
import SnacksCarousel from "./../commonComponents/snacksCarousel/SnacksCarousel";
import SnacksAlertMessage from "./../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import ProductDetail from "./../getStarted/ProductDetail";

import "./popularProducts.scss";

export default class PopularProducts extends Component {
  state = {
    popularProductsList: [],
    isProductLoading: true,
    locationNumber: sessionStorage.getItem("_lo_No") || null,
    offset: 0,
    limit: UrlConstants.popularProductsLimit,
    showAllProducts: false,
    showAllVisible: false,
    totalProducts: 0,
    lazyLoading: false,
    showProductDetail: false,
    productDetailIndex: "",
    msg: "",
    prodId: 0,
    selectedItems: sessionStorage.getItem("cartItems") || [],
  };

  productDetail = (id, index) => {
    this.setState({
      showProductDetail: true,
      prodId: id,
      productDetailIndex: index,
    });
  };

  popularProductsAPICall = (lazy) => {
    const { offset, limit, locationNumber } = this.state;
    const payload = {
      offset,
      limit,
      locationNumber,
    };

    APIUtil.postMethod(UrlConstants.PopularProducts, payload, true)
      .then((response) => {
        this.setState({
          popularProductsList: response.data.data,
          isProductLoading: false,
          lazyLoading: false,
          totalProducts: response.data && response.data.totalProducts,
          showAllVisible: lazy
            ? false
            : response.data.data && response.data.data.length > 4,
        });
      })
      .catch((error) => {
        console.error("Popular Products Api", error);
      });
  };

  handleOnDocumentBottom = () => {
    const { limit, totalProducts } = this.state;
    if (totalProducts > limit) {
      this.setState(
        {
          lazyLoading: true,
          limit: limit + 8,
        },
        () => {
          setTimeout(() => {
            this.popularProductsAPICall("lazy");
          }, 500);
        }
      );
    }
  };

  componentDidMount() {
    this.popularProductsAPICall();
  }

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  showAllProducts = () => {
    this.setState({
      showAllVisible: false,
      showAllProducts: true,
    });
  };

  getAllProducts = (popularProducts) => {
    return popularProducts.map((productInfo, index) => (
      <ProductTout
        key={productInfo.productId}
        productInfo={productInfo}
        index={index}
        detailedInfo={this.productDetail}
        warningMsgFunc={this.warningMsgFunc}
      />
    ));
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  closeAndUpdate = (filteredCartItems) => {
    this.setState({
      showProductDetail: false,
    });
    this.selectedCartItems(filteredCartItems);
  };

  getPopularProductList = (popularProducts) => {
    if (popularProducts && popularProducts.length > 0) {
      return (
        <SnacksCarousel
          content={this.getAllProducts(popularProducts)}
          carouselCustomClass="popular-products-slider"
        />
      );
    }

    return (
      <Grid container justify="center" className="noproducts-grid">
        <Grid item>
          <h3 className="noproducts-text">No Popular Products</h3>
        </Grid>
      </Grid>
    );
  };

  render() {
    const {
      popularProductsList,
      isProductLoading,
      showAllVisible,
      showAllProducts,
      lazyLoading,
      msg,
      showProductDetail,
      prodId,
      selectedItems,
    } = this.state;

    return (
      <Container className="popular-products">
        <div className="popular-products-container">
          <div className="popular-products-container-inner">
            {msg && <SnacksAlertMessage fullWidth msg={msg} />}
            <SectionHeading
              classField="popular-products"
              heading="Popular Products"
              ariaLabelHeading="User specific Popular Products"
              showAllTitle="Show All"
              ariaLabelShowAll="Show All popular products"
              showAllPosition="left"
              isShowAllVisible={showAllVisible}
              isFunction={this.showAllProducts}
            />
            <Dialog
              open={showProductDetail}
              onClose={() => {
                this.setState({ showProductDetail: false });
              }}
              maxWidth={false}
              scroll={"body"}
              classes={{
                paper: "modal-big modal-product-detail modal-paper",
                root: "modal-stack-top",
              }}
            >
              <DialogContent>
                <Button
                  className="icn-close"
                  id="productDetailClose"
                  aria-label="productDetail Close"
                  onClick={() => {
                    this.setState({ showProductDetail: false });
                  }}
                >
                  <i className="fa fa-times"></i>
                </Button>
                <ProductDetail
                  productData={prodId}
                  items={selectedItems}
                  warningMsgFunc={this.warningMsgFunc}
                  selectedItems={this.selectedCartItems}
                  closeAndUpdate={this.closeAndUpdate}
                />
              </DialogContent>
            </Dialog>
            <div className="popular-products-list">
              {isProductLoading && (
                <Grid container justify="center" style={{ minHeight: "100px" }}>
                  <Grid item>
                    <CircularProgress />
                  </Grid>
                </Grid>
              )}
              {showAllProducts && (
                <BottomScrollListener
                  debounce={200}
                  offset={700}
                  onBottom={this.handleOnDocumentBottom}
                />
              )}
              {!isProductLoading &&
                !showAllProducts &&
                this.getPopularProductList(popularProductsList)}

              {!isProductLoading && showAllProducts && (
                <div className="popular-products-show-all">
                  {this.getAllProducts(popularProductsList)}
                </div>
              )}
            </div>

            {lazyLoading && (
              <Grid container justify="center" style={{ minHeight: "100px" }}>
                <Grid item>
                  <CircularProgress />
                </Grid>
              </Grid>
            )}
          </div>
        </div>
      </Container>
    );
  }
}
