import {
  Container,
  Grid,
  Step,
  StepLabel,
  Stepper,
  Typography,
  withStyles,
} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import { Check } from "@material-ui/icons";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  actionLabelList,
  actionGetMinAmount,
  actionCheckProductAvailability,
  actionGetCustomer,
} from "../../actions/ProductAction";
import ZipCode from "../zipcode/ZipCode";
import CartSummary from "./../pages/cartSummary/CartSummary";
import CheckOut from "./checkout/CheckOut";
import ProductList from "./../pages/productList/ProductList";
import UrlConstants from "../../config/UrlConstants";
import Spinner from "../Spinner";
import APIUtil from "../../config/APIUtil";
import { trackEvent, EventNames } from "../../appinsights/EventTrack";
import { updatePrice } from "./../Utils";
import { DISPLAY_HEADING } from "./../../config/constants/content.constant";


const styles = (theme) => ({
  paper: {
    marginTop: "0 !important",
    marginBottom: "0 !important",
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    minHeight: "150px",
    backgroundColor: "transparent",
    alignItems: "center",
    "@media (max-width: 991px)": {
      padding: theme.spacing(0, 1),
      minHeight: "90px",
    },
  },
  stepperLabel: {
    fontSize: "16px",
    lineHeight: "16px",
    width: "100px",
    textAlign: "left",
    color: "#333",
    fontWeight: "500",
    "@media (max-width: 991px)": {
      textAlign: "center",
      display: "none",
    },
  },
  stepperIcon: {
    width: "74px",
    marginRight: "16px",
    verticalAlign: "middle",
    "@media (max-width: 991px)": {
      marginRight: "0px",
      width: "40px",
    },
  },
});

class SignUpSteps extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: "productList",
      isPopup: 0,
      zipcodeValidData: null,
      inventoryData: [],
      loader: true,
      activeStep: 0,
      message: "add to cart is empty",
      errorMsg: sessionStorage.getItem("errorMsg")
        ? sessionStorage.getItem("errorMsg")
        : "",
      cartLength: 0,
      total: 0,
      isEdit: false,
      editIndex: undefined,
      label: [],
      stepImage: [],
      dailogOpen: false,
      isThanku: "",
      isbtnDisabled: false,
      count: "",
      shippingCharges: "",
      totalPrice: 0,
      minPrice: 0,
      spinner: false,
      showLoginModal: false,
      userDetails: this.props.customerDetails,
    };
    this.props.history.listen((location, action) => {
      const urlParams = window.location.href.substr(
        window.location.href.lastIndexOf("/") + 1
      );
      if (urlParams == "cart") {
        this.setState({ activeStep: 1, isThanku: "" }, () =>
          this.checkQtyAvailable("error", false, "1")
        );
      } else if (urlParams == "productlist") {
        this.setState({ activeStep: 0, isThanku: "" });
      } else if (urlParams == "checkout") {
        this.setState({ activeStep: 2, isThanku: "" }, () =>
          this.moveToNextStep("selectedCategory", true, "2")
        );
      }
    });
    this.App = global.app;
  }

  /**
   *
   * @description for loading the zipcode at home page and calling the label list api.
   * @memberof SignUpSteps
   */
  componentWillMount() {
    this.checkRouting();
  }

  checkRouting() {
    let validatedZipCode = sessionStorage.getItem("Zipcode");
    if (validatedZipCode == null) {
      this.props.history.push("/home");
      this.setState({ dailogOpen: true });
    }
    const urlParams = window.location.href.substr(
      window.location.href.lastIndexOf("/") + 1
    );
    if (this.props.match.params.cancel == "cancel") {
      this.props.history.push("/cart");
    } else if (urlParams == "productlist") {
      this.getlabelList();
      this.setState({ activeStep: 0 });
    } else if (urlParams == "cart") {
      this.setState({ activeStep: 1 }, () =>
        this.checkQtyAvailable("error", false, "1")
      );
    } else if (urlParams == "checkout") {
      this.setState({ activeStep: 2 }, () =>
        this.moveToNextStep("selectedCategory", true, "2")
      );
    } else {
      if (
        this.props.match.params == this.props.match.params.order ||
        this.props.match.params.failure
      ) {
        this.setState({ activeStep: 3 });
      } else {
        this.getlabelList();
        this.setState({ activeStep: 0 });
      }
    }
  }

  /**
   * @description Steps defiend for wizzard.
   * @param {*} step : steps for moving to one page to another step by step.
   * @returns
   * @memberof SignUpSteps
   */
  GetStartedSignUp(step) {
    switch (step) {
      case 0:
        return (
          <ProductList
            history={this.props.history}
            selectedCategory={this.state.selectedCategory}
            moveToNextStep={() => this.checkQtyAvailable(0)}
            //moveToPrevStep={this.moveToPrevStep}
            cartItemForEdit={this.state.cartItemForEdit}
            isEdit={this.state.isEdit}
            editIndex={this.state.editIndex}
            goToRegistration={() => {
              this.setState({ activeTab: "Pick Your Count" });
            }}
          />
        );
      case 1:
        return (
          <div className="summary-main">
            <Typography
              variant="h1"
              className="page-heading"
              aria-hidden="true"
            >
              {DISPLAY_HEADING.REVIEW_YOUR_CART}
            </Typography>

            <CartSummary
              showLoginModal={this.state.showLoginModal}
              hideLoginModal={this.hideLoginModal}
              history={this.props.history}
              setLength={(e) => {
                this.setState({ cartLength: e });
              }}
              isOrderInventory={(data) => this.checkCartValidation(data)}
              handleclose={() => {
                this.setState({ errorMsg: null });
                document.getElementById("body").classList.remove("has-toast");
              }}
              cartDetails={(totalprice, minprice, error) => {
                this.setState({
                  errorMsg: error,
                  totalPrice: totalprice,
                  minPrice: minprice,
                });
              }}
              errorMsg={this.state.errorMsg}
              inventoryData={this.state.inventoryData}
              editMix={this.editMix}
              taxCalculation={this.taxCalculation}
              spinner={this.state.spinner}
            />
          </div>
        );
      case 2:
        return (
          <CheckOut
            history={this.props.history}
            zipcodeValidData={this.state.zipcodeValidData}
            shippingCharges={this.state.shippingCharges}
            openDialoge={(data) => this.setState({ isPopup: 1 })}
            moveToPrevStep={() => this.moveToPrevStep()}
            isThanku={() => this.isThanku()}
            goToProductList={() => {
              this.setState({ activeTab: "View Summary" });
            }}
            taxCalculation={this.taxCalculation}
            spinner={this.state.spinner}
          />
        );
      default:
        throw new Error("Unknown step");
    }
  }

  checkCartValidation(data) {
    var cartItems = sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    let inventoryData = this.state.inventoryData;
    var count = 0;
    if (!!cartItems && cartItems.length > 0) {
      cartItems.forEach((item) => {
        if (inventoryData[item.productId]) {
          if (parseInt(inventoryData[item.productId].availableQty) <= 0) {
            count++;
          } else if (
            parseInt(item.quantity) >
            parseInt(inventoryData[item.productId].availableQty)
          ) {
            count++;
          }
        }
      });
    }
    if (count <= 0) {
      this.setState({ isbtnDisabled: data });
    }
  }

  /**
   * @description Add tax to given price
   * @param {*} price
   */
  taxCalculation = (price) => {
    let taxPercent = 8.5; // static tax(in %)
    return Number((price * taxPercent) / 100);
  };

  /**
   * @description For updating the cart items in local storage.
   * @memberof SignUpSteps
   */
  getlabelList() {
    let labelName = [];
    let labelStep = ["Registration"];
    let imageStep = [];
    this.props
      .actionLabelList()
      .then(() => {
        this.props.labelList["Get Started"] !== undefined &&
          this.props.labelList["Get Started"].forEach((labels) => {
            labels.Header.forEach((signLabels) => {
              signLabels.labelData.forEach((data) => {
                labelName.push(data.label);
                imageStep.push(data.labelImage);
                return this.setState({
                  label: labelName.concat(labelStep),
                  stepImage: imageStep,
                });
              });
            });
          });
      })
      .catch(() => {
        return <p>some Technical Issue</p>;
      });
  }

  /**
   * @description for idetifying the url and redirecting to the specific step
   * @memberof SignUpSteps
   */
  componentDidMount() {
    this.setState({ showLoginModal: false });
    let prod = this;
    document.addEventListener("cartitem", function (e) {
      let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
      let count = 0;
      cartItems &&
        cartItems.map((item) => {
          count = count + item.quantity;
          return count;
        });
      prod.setState({ count: count });
      // Rerendering the component
      prod.forceUpdate();
    });
    // console.log('Test_123');
    //this.checkQtyAvailable('error');
    if (!!sessionStorage.getItem("errorMsg")) {
      setTimeout(() => {
        this.setState({ errorMsg: null }, () =>
          sessionStorage.removeItem("errorMsg")
        );
      }, 6000);
    }
  }

  hideLoginModal = () => {
    this.setState({
      showLoginModal: false,
    });
  };

  /**
   * @description to check the quantity that is there in cart eith respect to there product.
   * @param {*} selectedCategory
   * @memberof SignUpSteps
   */
  checkQtyAvailable(selectedCategory, isdata, isload) {
    //alert("dfdf");
    var cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
    var productIDs = [];
    if (cartItems && cartItems.length > 0) {
      this.setState({ spinner: true });
      cartItems.map((item) => {
        productIDs.push(item.productId);
        return true;
      });
      var obj = {
        productId: productIDs,
        locationId: !!sessionStorage.getItem("_lo_No")
          ? sessionStorage.getItem("_lo_No")
          : null,
      };
      this.props
        .actionCheckProductAvailability(obj)
        .then(() => {
          var count = 0;
          let inventoryData = this.state.inventoryData;
          if (
            this.props.productInventory &&
            this.props.productInventory.length > 0
          ) {
            this.props.productInventory.forEach((e, index) => {
              e.msg = null;
              inventoryData[e.productId] = e;
            });
            cartItems.forEach((item) => {
              if (inventoryData[item.productId]) {
                if (parseInt(inventoryData[item.productId].availableQty) <= 0) {
                  inventoryData[item.productId].msg = "Out of stock";
                  count++;
                } else if (
                  parseInt(item.quantity) >
                  parseInt(inventoryData[item.productId].availableQty)
                ) {
                  count++;
                  let string = UrlConstants.qtyAvailableMsg;
                  inventoryData[item.productId].msg = string.replace(
                    /##QTY##/g,
                    function () {
                      return parseInt(
                        inventoryData[item.productId].availableQty
                      );
                    }
                  );
                }
              }
            });
            if (count > 0) {
              this.setState({ isbtnDisabled: true, inventoryData });
            } else {
              this.setState({ isbtnDisabled: false, inventoryData });
            }
            if (selectedCategory !== "error") {
              this.moveToNextStep(selectedCategory, isdata, isload);
            } else {
              this.setState({ spinner: false });
            }
          } else {
            this.setState({ spinner: false });
          }
        })
        .catch((productData) => {
          this.setState({ spinner: false });
        });
    }
  }

  minAmountValidation = (selectedCategory, isload) => {
    var totalData = sessionStorage.getItem("total")
      ? sessionStorage.getItem("total")
      : 0;
    let shippingChargeAmount = this.props.minPrice
      ? this.props.minPrice.shippingCharges
      : sessionStorage.getItem("shippingCharges");
    this.setState({ shippingCharges: shippingChargeAmount });

    var minOrder = this.props.minPrice
      ? this.props.minPrice.minimumOrderAmount
      : sessionStorage.getItem("minimumOrderAmount");
    if (
      parseFloat(totalData) >= parseFloat(minOrder) ||
      this.state.activeStep == 0
    ) {
      if (!!selectedCategory) {
        this.setState({ selectedCategory: selectedCategory, isEdit: false });
      }

      if (this.state.activeStep === 1 || !!isload) {
        if (UrlConstants.isTaxFree) {
          if (isload == "2" || isload == "1") {
            this.checkZipCodeValidation(isload);
            this.setState({
              errorMsg: null,
              spinner: false,
              zipcodeValidData: null,
            });
          } else {
            if (this.state.activeStep <= 2) {
              if (this.props.customerDetails) {
                this.setState({
                  errorMsg: null,
                  spinner: false,
                  activeStep: this.state.activeStep + 1,
                  zipcodeValidData: null,
                  showLoginModal: false,
                });
                this.props.history.push("/checkout");
              } else {
                sessionStorage.setItem("prevUrl", "/cart-preview");
                trackEvent(
                  EventNames.Action.ACTION_CHECKOUT_LOGIN,
                  EventNames.Event.EVENT_BUTTON_PRESS,
                  window.location.origin,
                  window.location.pathname
                );
                this.setState({
                  errorMsg: null,
                  spinner: false,
                  zipcodeValidData: null,
                  showLoginModal: true,
                });
              }
            }
          }
        } else {
          this.checkZipCodeValidation(isload);
        }
      } else {
        this.setState({ errorMsg: null, spinner: false });
        if (this.state.activeStep <= 2) {
          if (this.state.activeStep <= 2) {
            this.setState({ activeStep: this.state.activeStep + 1 });
            this.props.history.push("/cart");
          }
        }
      }
    } else {
      this.setState(
        {
          activeStep: 1,
          spinner: false,
          errorMsg:
            "$" +
            parseFloat(this.state.minPrice).toFixed(2) +
            " " +
            "Minimum Order Requirement.",
        },
        () => {
          document.getElementById("body").classList.add("has-toast");
          this.props.history.push("/cart");
        }
      );
    }
  };
  /**
   * @description for moving to next step/page.
   * @memberof SignUpSteps
   */
  moveToNextStep(selectedCategory, isData, isload) {
    let minAmount = sessionStorage.getItem("minimumOrderAmount");
    //  if(isData){this.setState({spinner:true})}

    if (!minAmount) {
      this.props
        .actionGetMinAmount("", "", "", "")
        .then(() => {
          this.minAmountValidation(selectedCategory, isload);
        })
        .catch((productData) => {
          this.setState({ spinner: false });
        });
    } else {
      this.minAmountValidation(selectedCategory, isload);
    }
  }

  /**
   * @description for checking the zip code and validating it.
   * @memberof SignUpSteps
   */
  checkZipCodeValidation(isload) {
    var cartItems = !!sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    var formData = !!sessionStorage.getItem("formValues")
      ? JSON.parse(sessionStorage.getItem("formValues"))
      : null;
    var shippingAddressLine1 =
      !!formData && !!formData.shippingAddressLine1
        ? formData.shippingAddressLine1
        : null;
    var shippingAddressLine2 =
      !!formData && !!formData.shippingAddressLine2
        ? formData.shippingAddressLine2
        : null;
    let loggedInUser = localStorage.getItem("accessToken");
    if (loggedInUser) {
      var shippingCountry =
        !!formData && !!formData.shippingCountry
          ? formData.shippingCountry
          : null;
      var shippingState =
        !!formData && !!formData.shippingState ? formData.shippingState : null;
      var shippingCity =
        !!formData && !!formData.shippingCity ? formData.shippingCity : null;
      var zipCode =
        !!formData && !!formData.shippingZipCode
          ? formData.shippingZipCode
          : null;

      //Address Data
      let addObj = {};
      addObj["postalArea"] = !!sessionStorage.getItem("Zipcode")
        ? sessionStorage.getItem("Zipcode")
        : null;
      addObj["stateOrProvinceName"] =
        !!formData && !!formData.shippingState ? formData.shippingState : null;
      //Line Items
      let arrData = [];
      cartItems &&
        cartItems.length > 0 &&
        cartItems.forEach((e) => {
          let lineItemsObj = {};
          lineItemsObj["lineItemNumber"] = cartItems.length;
          lineItemsObj["product"] = {
            productClass: !!e.productClass ? e.productClass : null,
            productID: !!e.productId ? e.productId : null,
          };
          lineItemsObj["quantity"] = !!e.quantity ? parseInt(e.quantity) : 0;
          lineItemsObj["unitPrice"] = !!e.price ? parseFloat(e.price) : 0;
          arrData.push(lineItemsObj);
        });
      let addressData = {};
      addressData["Region"] =
        (!!shippingCity ? shippingCity : "") +
        (!!shippingState ? ", " + shippingState : "") +
        (!!zipCode ? ", " + zipCode : "");
      addressData["CountryCode"] = !!shippingCountry ? shippingCountry : "";
      addressData["AddressLine"] = [
        (!!shippingAddressLine1 ? shippingAddressLine1 : "") +
          " " +
          (!!shippingAddressLine2 ? shippingAddressLine2 : ""),
      ];
      // Tax post data Object
      let objData = {};
      if (
        !!shippingAddressLine1 &&
        !!shippingCountry &&
        !!shippingState &&
        !!shippingCity &&
        !!zipCode
      ) {
        objData["isAddress"] = true;
      }
      objData["destination"] = addObj;
      objData["Address"] = addressData;
      objData["documentDate"] = new Date();
      objData["lineItems"] = arrData;
      objData["transactionType"] = "SALE";
      objData["sourceLocationId"] = !!sessionStorage.getItem("_lo_No")
        ? sessionStorage.getItem("_lo_No")
        : null;
      let url = UrlConstants.orderTax;
      if (shippingAddressLine1 !== null) {
        APIUtil.postMethod(url, objData, true)
          .then((response) => {
            if (
              response.data.success === true &&
              response.data.zipSuccess === true
            ) {
              response.data.zipcode = sessionStorage.getItem("Zipcode");
              sessionStorage.setItem("taxFree", response.data.taxFree);

              if (response.data.taxSuccess === true) {
                response.data.totalTaxAmount = response.data.data.tax
                  ? response.data.data.tax.totalTaxAmount
                  : "";
                updatePrice(response);
              }
              if (isload == "2" || isload == "1") {
                this.setState({
                  errorMsg: null,
                  spinner: false,
                  zipcodeValidData: response,
                });
              } else {
                if (this.state.activeStep <= 2) {
                  this.setState({
                    errorMsg: null,
                    spinner: false,
                    activeStep: this.state.activeStep + 1,
                    zipcodeValidData: response,
                  });
                  this.props.history.push("/checkout");
                }
              }
            } else {
              if (isload == "2" || isload == "1") {
                response.data.zipcode = sessionStorage.getItem("Zipcode");
                response.data.totalTaxAmount = 0;
                this.setState({
                  errorMsg: null,
                  spinner: false,
                  zipcodeValidData: response,
                });
              } else {
                if (this.state.activeStep <= 2) {
                  response.data.zipcode = sessionStorage.getItem("Zipcode");
                  response.data.totalTaxAmount = 0;
                  this.setState({
                    errorMsg: null,
                    spinner: false,
                    activeStep: this.state.activeStep + 1,
                    zipcodeValidData: response,
                  });
                  this.props.history.push("/checkout");
                }
              }
            }
          })
          .catch((response) => {
            this.setState({ errorMsg: null, spinner: false });
          });
      } else {
        if (this.props.customerDetails && this.props.customerDetails !== null) {
          let primaryShippingAddress = this.props.customerDetails.accountAddresses.filter(
            (data) => data.primaryAddr === true
          );
          this.setState({
            errorMsg: null,
            spinner: false,
            activeStep: 2,
            zipcodeValidData: primaryShippingAddress[0],
          });
          window.location = "/checkout";
        }
      }
    } else {
      if (this.props.history.location.pathname === "/cart") {
        sessionStorage.setItem("prevUrl", "/cart-preview");
        this.setState({
          errorMsg: null,
          spinner: false,
          zipcodeValidData: sessionStorage.getItem("Zipcode"),
          showLoginModal: true,
        });
      } else {
        let zipCode = sessionStorage.getItem("Zipcode");
        this.setState({
          errorMsg: null,
          spinner: false,
          zipcodeValidData: sessionStorage.getItem("Zipcode"),
          showLoginModal: false,
        });
      }
    }
  }

  redirectToTarget = () => {
    this.props.history.push(`/checkout`);
  };

  /**
   * @description for moving to prev step/page.
   * @memberof SignUpSteps
   */
  moveToPrevStep = () => {
    if (this.state.activeStep === 2) {
      this.setState({ activeStep: 0 });
    } else {
      this.setState({ activeStep: this.state.activeStep - 1 });
    }
  };

  /**
   * @description for hide wizard icons on thanku page
   * @memberof SignUpSteps
   */
  isThanku = () => {
    this.setState({
      isThanku: "hide",
    });
  };

  /**
   * @description edit cart tems
   * @param {*} cartItem
   * @param {*} index
   */
  editMix = (cartItem, index) => {
    this.setState(
      {
        selectedCategory: cartItem.category,
        cartItemForEdit: cartItem,
        isEdit: true,
        editIndex: index,
        activeStep: 1,
      },
      () => {
        this.setState({
          activeStep: 1,
        });
      }
    );
  };

  /**
   * @description for hiding the dialuge .
   * @memberof SignUpSteps
   */
  hideDialog() {
    this.setState({ dailogOpen: false });
  }

  QontoStepIcon(props) {
    const { completed } = props;

    return (
      <div>
        {completed ? (
          <div className="check-circle checked">
            <Check />
          </div>
        ) : (
          <div className="check-circle unchecked">
            <Check />
          </div>
        )}
      </div>
    );
  }

  render() {
    const steps = this.state.label;
    const { classes } = this.props;
    let activeStep = this.state.activeStep;

    return (
      <React.Fragment>
        <CssBaseline />
        {this.state.spinner ? <Spinner /> : ""}
        <div className="stepup-container">
          <div className="content-main">
            <div>
              <Grid container spacing={0}>
                <Grid item className="stepper-col">
                  <div className={"stepper-col-header " + this.state.isThanku}>
                    <Container className="container-root">
                      <Stepper
                        alternativeLabel
                        activeStep={this.state.activeStep}
                        className={classes.stepper + " stepper-head "}
                      >
                        {steps.map((label, i) => (
                          <Step key={label}>
                            <StepLabel
                              className="step-head"
                              StepIconComponent={this.QontoStepIcon}
                            ></StepLabel>
                          </Step>
                        ))}
                      </Stepper>
                    </Container>
                  </div>

                  <React.Fragment>
                    {this.GetStartedSignUp(this.state.activeStep)}
                    <div className="text-right wizard-buttons">
                      {activeStep !== 0 && activeStep !== 2 && (
                        <a
                          onClick={() => {
                            trackEvent(
                              EventNames.Action.ACTION_KEEP_SHOPPING,
                              EventNames.Event.EVENT_BUTTON_PRESS,
                              window.location.origin,
                              window.location.pathname
                            );
                            this.setState({ activeStep: 0 }, () =>
                              this.props.history.push("/productlist")
                            );
                          }}
                          className={"btn-mui btn-red"}
                          id="ContinueShopping"
                          aria-label="Keep Shopping"
                          tabIndex={0}
                        >
                          Keep Shopping
                        </a>
                      )}

                      {activeStep === 0 || activeStep === 2 ? (
                        ""
                      ) : (
                        <Button
                          disabled={
                            this.state.totalPrice > 0 &&
                            this.state.totalPrice >= this.state.minPrice &&
                            !this.state.isbtnDisabled
                              ? false
                              : true
                          }
                          variant="contained"
                          color="primary"
                          onClick={(e) => this.moveToNextStep(e, true)}
                          className={" btn-mui btn-yellow "}
                          id="ContinueCheckout"
                          aria-label="Checkout"
                        >
                          Checkout
                        </Button>
                      )}
                    </div>
                  </React.Fragment>
                </Grid>
              </Grid>
            </div>
          </div>
          <ZipCode
            onClose={() => this.hideDialog()}
            dailogOpen={this.state.dailogOpen}
          />
        </div>
      </React.Fragment>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    labelList: state.reducer.labelList || [],
    minPrice: state.reducer.minAmount,
    productInventory: state.reducer.productInventory,
    customerDetails: state.reducer.customerDetails,
  };
};
export default connect(mapStateToProps, {
  actionLabelList,
  actionGetMinAmount,
  actionCheckProductAvailability,
  actionGetCustomer,
})(withStyles(styles)(SignUpSteps));
