import React, { Component } from "react";
import {
  Button,
  FormControl,
  Card,
  Grid,
  InputLabel,
  OutlinedInput,
  Typography,
  Snackbar,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import { ErrorMessage, Formik,Form } from "formik";
import * as Yup from 'yup';
import APIUtil from "../../../config/APIUtil";
import UrlConstants from '../../../config/UrlConstants';
import Spinner from "../../Spinner";
import {
  trackEvent,
  EventNames
} from '../../../appinsights/EventTrack';

class ChangePassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      initialValues:{
        currentPassword:null,
        password:null,
        confirmPassword:null,
        submissionMessage: "",
        successMessage: '',
        submissionStatus: false,
        isSpinner:false
      }
    };
  }

  render() {
    return (
      <div className="profile-info" >
        {this.state.isSpinner ? <Spinner /> : ""}
        {/* {this.state.submissionStatus ? ( <Snackbar open={true} anchorOrigin={{ vertical: "top", horizontal: "center" }} className="snackbar snackbar-full" > <MuiAlert variant="filled" onClose={this.handleClose} severity="error" > {this.state.submissionStatus} </MuiAlert> </Snackbar> ) : ( "" )} */}
        {this.state.submissionStatus && <Snackbar
                      open={this.state.submissionStatus}
                      anchorOrigin={{ vertical: "top", horizontal: "center" }}
                      className="snackbar snackbar-full"
                    >
                      <MuiAlert
                        variant="filled"
                        onClose={() => {
                          this.setState({ submissionStatus: false }, () => {
                            document.getElementById('body').classList.remove("has-toast")
                          });
                        }}
                        severity={
                          this.state.submissionMessage === "success"
                            ? "success"
                            : "error"
                        }>
                        {this.state.successMessage}
                      </MuiAlert>
                    </Snackbar>
          }       
            <Formik
                enableReinitialize
                initialValues={this.state.initialValues}
                validationSchema={Yup.object().shape({
                    currentPassword: Yup.string().nullable()
                      .required("Current Password is required."),
                    password: Yup.string().nullable()
                      .required("New Password is required.")
                      .required("New Password is required.")
                      .min(8, 'New Password should be minimum 8 character')
                      .max(15, 'New Password should be maximum 15 character'),
                    confirmPassword: Yup.string().nullable()
                      .required("Repeat New Password is required")
                      .test('passwords-match', 'New Password or Repeat New Password must be match', function(value) {
                          return this.parent.password === value;
                    }),
                })}
                onSubmit={(values, { resetForm }) => {
                    var passObj = {
                        "oldPassword": { "value": values.currentPassword },
                        "newPassword": { "value": values.password }
                    };
                    APIUtil.putMethod(UrlConstants.changePassword, passObj, true).then(response => {
                        
                        if (!!response.data && !!response.data.success) {
                            resetForm({currentPassword:"",password:"",confirmPassword:""});
                            this.setState({successMessage:response.data.message, submissionMessage:'success',submissionStatus: true,isSpinner: false},
                            () => setTimeout(() => { this.setState({ successMessage: null,submissionStatus: false }); document.getElementById("body").classList.remove("has-toast"); }, 3000)
                            );
                            
                        } else {
                            this.setState({successMessage:response.data.message, submissionMessage:'error',submissionStatus: true,isSpinner: false},
                            () => setTimeout(() => { this.setState({ successMessage: null,submissionStatus: false, }); document.getElementById("body").classList.remove("has-toast"); }, 3000)
                            );

                        }
                    }).catch((data) => {
                        let errorMsg = UrlConstants.errorMsg;
                        this.setState({successMessage:errorMsg, submissionMessage:'error',submissionStatus: true,isSpinner: false},
                        () => setTimeout(() => { this.setState({ successMessage: null,submissionStatus: false }); document.getElementById("body").classList.remove("has-toast"); }, 3000)
                       );
                        this.setState({ isSpinner: false });
                    });


                    resetForm(this.state.initialValues);
                   // setSubmitting(false)
                }}>
                {({
                    resetForm,
                    setFieldValue,	
                    setFieldTouched,
                    errors,
                    values,
                    touched,
                    handleChange
                }) => (
                  <Form
                    id="changePassword"
                    name="changePassword"
                    autoComplete="changePassword"
                  >
                    <Grid container spacing={6} justify="space-between">
                      <Grid item xs={12} sm={12}>
                        <Grid container spacing={2} alignItems="center" className="title-mini">
                          <Grid item xs container>
                            <Typography variant="h2" component="h2">Change Password</Typography>
                          </Grid>
                          
                        </Grid>
                        <label htmlFor="prevent_autofill_Password" className="heading-hidden">Auto Fill</label>
                        <input type="text" name="prevent_autofill_Password" id="prevent_autofill_Password" value="" style={{ display: 'none' }} />
                        {/* Contact info */}
                        <Card className="profile-card profile-card-form">
                          <Grid container spacing={6} className="form-card-container">
                            {/* Current Password */}
                            <Grid item xs={12} sm={12}>
                              <Grid container spacing={4}>
                                <Grid item xs={12} sm={6}>
                                  <FormControl
                                    fullWidth
                                    className="form-group"
                                    variant="outlined"
                                  >
                                    <InputLabel
                                      className="form-label"
                                      htmlFor="currentPassword"
                                    >
                                      Current Password<span className="text-danger">*</span>
                                  </InputLabel>
                                    <OutlinedInput
                                      autoComplete="currentPassword"
                                      id="currentPassword"
                                      name="currentPassword"
                                      inputProps={{ 'aria-required': 'true' , 'role':'alert' }}
                                      type="password"
                                      value={values.currentPassword || ''}
                                      onChange={handleChange}
                                      onBlur={(e) => { setFieldTouched(`currentPassword`, true) }}
                                      error={ Boolean(touched.currentPassword && errors.currentPassword) }
                                    />
                                    <ErrorMessage component="div" name={`currentPassword`} className="text-danger" />
                                  </FormControl>
                                </Grid>
                              </Grid>
                            </Grid>

                            {/* New Password */}
                            <Grid item xs={12} sm={6}>
                                <FormControl
                                  fullWidth
                                  className="form-group"
                                  variant="outlined"
                                >
                                  <InputLabel
                                    className="form-label"
                                    htmlFor="password"
                                  >
                                    New Password<span className="text-danger">*</span>
                                </InputLabel>
                                  <OutlinedInput
                                    autoComplete="password"
                                    id="password"
                                    name="password"
                                    inputProps={{ 'aria-required': 'true' , 'role':'alert' }}
                                    value={values.password || ''}
                                    type="password"
                                    onChange={handleChange}
                                    onBlur={(e) => { setFieldTouched(`password`, true) }}
                                    error={ Boolean(touched.password && errors.password) }
                                  />
                                  <ErrorMessage component="div" name={`password`} className="text-danger" />
                                </FormControl>
                              </Grid>

                              {/* New Password */}
                              <Grid item xs={12} sm={6}>
                                <FormControl
                                  fullWidth
                                  className="form-group"
                                  variant="outlined"
                                >
                                  <InputLabel
                                    className="form-label"
                                    htmlFor="confirmPassword"
                                  >
                                    Repeat New Password<span className="text-danger">*</span>
                                </InputLabel>
                                  <OutlinedInput
                                    autoComplete="confirmPassword"
                                    id="confirmPassword"
                                    name="confirmPassword"
                                    inputProps={{ 'aria-required': 'true' , 'role':'alert' }}
                                    value={values.confirmPassword || ''}
                                    type="password"
                                    onChange={handleChange}
                                    onBlur={(e) => { setFieldTouched(`confirmPassword`, true) }}
                                    error={ Boolean(touched.confirmPassword && errors.confirmPassword) }
                                  />
                                  
                                  <ErrorMessage component="div" name={`confirmPassword`} className="text-danger" />
                                 
                                </FormControl>
                              </Grid>
                              <Grid item xs={12} sm={12}>
                                  <p className="my-0">Password requirements: atleast 8 characters, a lower case letters, an upper case letter , a number, a symbol, no parts of your username.
                                      Your password can not be any of your last 4 passwords
                                  </p>
                              </Grid>
                          </Grid>
                        </Card>
                        <Grid item>
                            <div className="text-right">
                              <Button
                          type="submit"
                          variant="contained"
                          color="primary"
                          className="btn-mui btn-blue btn-sm"
                          id="savePassword"
                          aria-label="savePassword"
                          onClick={() => {
                            trackEvent(EventNames.Action.ACTION_CHANGE_PASSWORD, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname)
                            this.setState({ isSpinner: true })
                          }}
                              > Save </Button>
                            </div>
                          </Grid>
                      </Grid>
                    </Grid>
                  </Form>)}
            </Formik>
      </div>
    );
  }
}


export default ChangePassword;