import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  OutlinedInput,
  Typography,
  InputAdornment,
  IconButton,
} from "@material-ui/core";
import { ErrorMessage, Form, Formik } from "formik";
import React from "react";
import { Link, withRouter } from "react-router-dom";
import * as Yup from "yup";
import { LoginAuth } from "../../actions/ProductAction";
import { connect } from "react-redux";
import Spinner from "../Spinner";
import OktaApiUtil from "../../config/OktaApiUtil";
import APIUtil from "../../config/APIUtil";
import UrlConstants from "config/UrlConstants";
import MuiAlert from "@material-ui/lab/Alert";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { trackEvent, EventNames } from "../../appinsights/EventTrack";

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      spinner: false,
      isLoginComponent: this.props.fromForgot ? false : true,
      msg: null,
      msgType: "success",
      password: "",
      email: "",
      submitDisabled: false,
      errorMessage: "",
      showPassword: false,
    };
    this.nameFieldValidation.bind(this);
  }

  componentDidMount() {
    trackEvent(
      EventNames.Action.ACTION_LOGIN_POPUP,
      EventNames.Event.EVENT_PAGE_VIEW,
      window.location.origin,
      window.location.pathname,
      {
        from:
          sessionStorage.getItem("prevUrl") !== null
            ? sessionStorage.getItem("prevUrl")
            : "home",
      }
    );
    setTimeout(function () {
      window.scrollTo(0, 0);
    }, 10);
  }

	handleLogin = (fields) => {
		this.removeMsg();
		this.setState({ spinner: true, msg: '', msgType: 'success' });
		trackEvent(EventNames.Action.ACTION_LOGIN, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { email: fields.email })
		OktaApiUtil.userLogin(fields.email, fields.password)
			.then((res) => {
				this.setState({ spinner: false });
				if (res.data !== undefined) {
					this.props.LoginAuth(res);
					if(sessionStorage.getItem('taxZipCode') !== null){
						sessionStorage.removeItem('taxZipCode');
					}
					setTimeout(() => {
						if (res.data.status === 'SUCCESS' && this.props.isAuthenticated) {
							const sessionToken = res.sessionToken;
							localStorage.setItem('token', 'Bearer ' + sessionToken);
							OktaApiUtil.getCode(sessionToken);
						} else if (res.data.status === 'LOCKED_OUT') {
							this.setState({ msg: UrlConstants.userAccountLocked, msgType: 'error' });
						}
					}, 1500);
				} else if (res.status === "404") {
					this.setState({ msg: "We are facing some technical issue. Please try again after sometime.", msgType: 'error' });
				} else {
					this.setState({ msg: res.toString(), msgType: 'error' });
				}
			})
			.catch((err) => {
				this.setState({ spinner: false });
				this.setState({ msg: err.toString(), msgType: 'error' }, () => this.removeMsg());
				console.log('Found an error', err);
			});
	};
	removeMsg() {
		let msgTimeOut = parseInt(sessionStorage.getItem('msgTime') ? sessionStorage.getItem('msgTime') : 10000);
		setTimeout(() => {
			this.setState({ msg: null, msgType: 'success' });
		}, msgTimeOut);
	}
	handleClickShowPassword = () => {
		this.setState({
			showPassword: !this.state.showPassword
		})
	};
	handleMouseDownPassword = (event) => {
		event.preventDefault();
	};

  handleClose = () => {
    this.setState({ ZipfailMessage: "" });
    document.getElementById("body").classList.remove("has-toast");
  };

  nameFieldValidation = (e, setFieldValue) => {
    this.setState({ [e.target.id]: "" });

    if (e.target.value || e.target.value === "") {
      setFieldValue([e.target.id], e.target.value);
    }
  };

  handleLogin = (fields) => {
    this.removeMsg();
    this.setState({ spinner: true, msg: "", msgType: "success" });
    trackEvent(
      EventNames.Action.ACTION_LOGIN,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname,
      { email: fields.email }
    );
    OktaApiUtil.userLogin(fields.email, fields.password)
      .then((res) => {
        this.setState({ spinner: false });
        if (res.data !== undefined) {
          this.props.LoginAuth(res);
          setTimeout(() => {
            if (res.data.status === "SUCCESS" && this.props.isAuthenticated) {
              const sessionToken = res.sessionToken;
              localStorage.setItem("token", "Bearer " + sessionToken);
              OktaApiUtil.getCode(sessionToken);
            } else if (res.data.status === "LOCKED_OUT") {
              this.setState({
                msg: UrlConstants.userAccountLocked,
                msgType: "error",
              });
            }
          }, 1500);
        } else if (res.status === "404") {
          this.setState({
            msg:
              "We are facing some technical issue. Please try again after sometime.",
            msgType: "error",
          });
        } else {
          this.setState({ msg: res.toString(), msgType: "error" });
        }
      })
      .catch((err) => {
        this.setState({ spinner: false });
        this.setState({ msg: err.toString(), msgType: "error" }, () =>
          this.removeMsg()
        );
        console.log("Found an error", err);
      });
  };
  removeMsg() {
    let msgTimeOut = parseInt(
      sessionStorage.getItem("msgTime")
        ? sessionStorage.getItem("msgTime")
        : 10000
    );
    setTimeout(() => {
      this.setState({ msg: null, msgType: "success" });
    }, msgTimeOut);
  }
  handleClickShowPassword = () => {
    this.setState({
      showPassword: !this.state.showPassword,
    });
  };
  handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  render() {
    return (
      <div>
        {this.props.spinner ? <Spinner /> : ""}
        {this.state.spinner ? <Spinner /> : ""}
        {!!this.state.msg ? (
          <MuiAlert
            className={"snackbar-inline " + this.state.msgType}
            severity={this.state.msgType}
          >
            {this.state.msg}
          </MuiAlert>
        ) : (
          ""
        )}

        <div className="has-guest">
          <div className="title">Sign in to Checkout</div>
          <div className="signin-text">
            Sign in to checkout or
            <Button
              color="primary"
              className="btn-link"
              id="SignIn"
              aria-label="sign in"
              onClick={() => {
                this.props.closeLoginDialog();
                if (window.location.pathname !== "/create-account") {
                  this.props.history.push("/create-account");
                }
              }}
            >
              Create an Account
            </Button>
          </div>
          {/* <p className="desc">Sign in to checkout or <Link to="/create-account">Create an Account</Link></p> */}
        </div>

        {this.state.isLoginComponent ? (
          <Formik
            initialValues={{ email: "", password: "" }}
            enableReinitialize={true}
            validationSchema={Yup.object().shape({
              email: Yup.string()
                .email("Please enter a valid email.")
                .required("Email is required."),
              password: Yup.string()
                .required("Password is required.")
                .min(8, "Password must be at least 8 characters."),
            })}
            onSubmit={(fields) => this.handleLogin(fields)}
          >
            {(props) => {
              const {
                values,
                touched,
                errors,
                handleChange,
                handleBlur,
                handleSubmit,
                setFieldValue,
              } = props;

              return (
                <form
                  id="startSubscriptionForm"
                  name="form1"
                  onSubmit={handleSubmit}
                  //onChange={sessionStorage.setItem('formValues', JSON.stringify(values))}
                  className="login-form custom-form"
                >
                  <div className="info0" aria-label="sign in">
                    Sign in
                  </div>
                  {/** Login information */}
                  <fieldset className="form-card">
                    <Grid container spacing={2} className="form-card-container">
                      {/** Email  */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel className="form-label" htmlFor="email">
                            Email
                          </InputLabel>
                          <OutlinedInput
                            id="email"
                            value={values.email}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.email && Boolean(errors.email)}
                            // autoFocus={true}
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                          />
                          <ErrorMessage
                            role="alert"
                            component="div"
                            name="email"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>

                      {/** Password */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel className="form-label" htmlFor="password">
                            Password
                          </InputLabel>
                          <OutlinedInput
                            id="password"
                            // name = "First Name"
                            value={values.password}
                            // onChange={handleChange}
                            type={this.state.showPassword ? "text" : "password"}
                            onChange={(e) =>
                              this.nameFieldValidation(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            error={touched.password && Boolean(errors.password)}
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            endAdornment={
                              <InputAdornment position="end">
                                <IconButton
                                  aria-label="toggle password visibility"
                                  onClick={this.handleClickShowPassword}
                                  onMouseDown={this.handleMouseDownPassword}
                                  edge="end"
                                >
                                  {this.state.showPassword ? (
                                    <Visibility />
                                  ) : (
                                    <VisibilityOff />
                                  )}
                                </IconButton>
                              </InputAdornment>
                            }
                          />

                          {this.state.password ? (
                            <div className="text-danger">
                              {this.state.password}
                            </div>
                          ) : (
                            <ErrorMessage
                              role="alert"
                              component="div"
                              name="password"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                    </Grid>
                  </fieldset>

                  <div className="button-group text-center">
                    <Button
                      type="submit"
                      disabled={this.state.submitDisabled}
                      variant="contained"
                      color="primary"
                      className="btn-mui signin-btn"
                      id="SignIn"
                      aria-label="Sign In"
                    >
                      Sign In
                    </Button>
                  </div>
                  <div className="signin-text">
                    Forgot password?
                    <Button
                      className="btn-link"
                      onClick={() =>
                        this.setState({
                          isLoginComponent: false,
                        })
                      }
                      aria-label="click here to reset password"
                    >
                      Click here
                    </Button>
                  </div>
                </form>
              );
            }}
          </Formik>
        ) : (
          <div>
            <Grid>
              <Formik
                enableReinitialize
                initialValues={{ email: "" }}
                validationSchema={Yup.object().shape({
                  email: Yup.string()
                    .email("Email must be a valid email.")
                    .required("Email is required."),
                })}
                onSubmit={(values, { resetForm }) => {
                  trackEvent(
                    EventNames.Action.ACTION_FORGOT_PASSWORD,
                    EventNames.Event.EVENT_BUTTON_PRESS,
                    window.location.origin,
                    window.location.pathname,
                    { email: values.email }
                  );
                  let val = {
                    username: values.email,
                  };
                  APIUtil.postMethod(
                    UrlConstants.forgotpassword,
                    val,
                    true
                  ).then((response) => {
                    if (!!response.data.success) {
                      resetForm({ email: "" });
                      this.setState(
                        { msg: response.data.message, msgType: "success" },
                        () => this.removeMsg()
                      );
                    } else {
                      this.setState(
                        { msg: UrlConstants.errorMsg, msgType: "error" },
                        () => this.removeMsg()
                      );
                    }
                  });
                }}
              >
                {({
                  setFieldValue,
                  handleChange,
                  handleBlur,
                  errors,
                  values,
                  touched,
                }) => (
                  <Form className="loginForm login-form custom-form">
                    <div className="info0">Forgot Password</div>
                    <fieldset className="form-card">
                      <Grid
                        container
                        spacing={2}
                        className="form-card-container"
                      >
                        <Grid item xs={12} sm={12}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel className="form-label" htmlFor="email">
                              Email Address
                            </InputLabel>
                            <OutlinedInput
                              id="email"
                              value={values.email}
                              onChange={handleChange}
                              onBlur={handleBlur}
                              error={touched.email && Boolean(errors.email)}
                              // autoFocus={true}
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                            />
                            <ErrorMessage
                              role="alert"
                              component="div"
                              name="email"
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>
                      </Grid>
                    </fieldset>
                    <div className="button-group text-center">
                      <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        className="btn-mui signin-btn"
                        id="ForgotPassword"
                        aria-label="Forgot Password"
                      >
                        Forgot Password
                      </Button>
                    </div>
                    <div className="signin-text">
                      Back to Sign in?
                      <Button
                        className="btn-link"
                        onClick={() => {
                          this.setState({
                            isLoginComponent: true,
                          });
                          this.props.changeForgotToLogin();
                        }}
                        aria-label="click here to go back to Sign in"
                      >
                        Click here
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </Grid>
          </div>
        )}
        <div className="custom-form guest-fieldset has-guest">
          <div className="separator">or</div>
          <div className="title">Checkout as Guest</div>
          <Button
            variant="contained"
            color="primary"
            className="btn-mui btn-blue"
            id="GuestCheckout"
            aria-label="Guest Checkout"
            onClick={() => {
              let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
              if (cartItems !== null) {
                this.props.closeLoginDialog();
                sessionStorage.setItem("checkoutMode", "Guest");
                this.props.history.push("/checkout");
              } else {
                this.setState({
                  msg: "Please add items in cart to checkout",
                  msgType: "error",
                });
              }
            }}
          >
            Guest Checkout
          </Button>
        </div>
        <div className="has-account">
          <div className="separator">or</div>
          <div className="desc">
            Don't have an account?
            <Button
              color="primary"
              className="btn-link"
              id="SignIn"
              aria-label="Create account"
              onClick={() => {
                this.props.closeLoginDialog();
                if (window.location.pathname !== "/create-account") {
                  this.props.history.push("/create-account");
                }
              }}
            >
              Create an account
            </Button>
            {/* <Link aria-label="create an account" to="/create-account">Create an Account</Link> */}
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    zipResponse: state.reducer.zipResponse,
    isAuthenticated: state.reducer.isAuthenticated,
  };
};
export default connect(mapStateToProps, { LoginAuth })(withRouter(Login));
