import { Container, Grid, Snackbar, Typography, Button, Tabs, Tab } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { actionCreateCustomer, actionGetCustomer } from '../../../actions/ProductAction';
import OrderSummary from '../OrderSummary';
import Payment from '../Payment';
import Registration from './Registration';
import ThankYouMessage from '../ThankYou';
import Spinner from '../../Spinner';
import Login from '../../login/login';
import CheckoutDetails from './CheckoutDetails';
import UrlConstants from '../../../config/UrlConstants';
import APIUtil from '../../../config/APIUtil';
import PropTypes from 'prop-types';
import {
	trackEvent,
	EventNames
} from '../../../appinsights/EventTrack';

function TabPanel(props) {
	const { children, value, index, ...other } = props;

	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`checkout-tabpanel-${index}`}
			aria-labelledby={`checkout-tabpanel-${index}`}
			{...other}
		>
			{value === index && <div>{children}</div>}
		</div>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired,
};

// function a11yProps(index) {
// 	return {
// 		id: `checkout-tab-${index}`,
// 		'aria-controls': `checkout-tabpanel-${index}`,
// 	};
// }

class check extends React.Component {
	constructor(props) {
		super(props);
		this.checkoutMode = sessionStorage.getItem('checkoutMode')
		if (localStorage.getItem('token') !== null) {
			this.checkoutMode = null
		}
		this.state = {
			fromForgot: false,
			spinner: false,
			isPayment: 0,
			keyData: 0,
			showLogin: this.checkoutMode === null ? ((localStorage.getItem('token') === null) ? true : false) : (this.checkoutMode === 'Login' ? true : false),
			showRegister: this.checkoutMode !== null && this.checkoutMode === 'Register' ? true : ((localStorage.getItem('token') === null) ? false : true),
			showGuest: this.checkoutMode !== null && this.checkoutMode === 'Guest' ? true : (localStorage.getItem('token') === null ? true : false),
			loggedinReg: false,
			errorMessage: false,
			summuryTaxData: {},
			isSummuryLoader: false,
			userInfo: this.props.customerDetails, //this.props.user
			billingSameAsShipping: false,
			error: '',
			emailValid: true,
			tabIndex: (localStorage.getItem('token') !== null) ? 1 : 0,
			hasMinTotal: true,
			cartItems: JSON.parse(sessionStorage.getItem("cartItems"))
				? JSON.parse(sessionStorage.getItem("cartItems"))
				: [],
			// tabIndex: parseInt(sessionStorage.getItem("chk_tabIndex") ? sessionStorage.getItem("chk_tabIndex") : 0),
			//zipcodeValidData:null,
		};
		sessionStorage.setItem('newUserCheckout', false);
	}

	componentDidMount() {
		if (sessionStorage.getItem("cartItems") !== null)
			trackEvent(EventNames.Action.ACTION_CART_ON_CHECKOUT, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname,
				{
					cart: JSON.parse(sessionStorage.getItem("cartItems")),
					userLoggedIn: localStorage.getItem('token') !== null ? true : false
				})
		var that = this;
		document.addEventListener('showLogin', function (e) {
			let showLogin = sessionStorage.getItem('showLogin');
			if (!!showLogin) {
				// that.setState({ showLogin: true, showRegister: false });
			}
			// Rerendering the component
			that.forceUpdate();
		});
		setTimeout(function () {
			window.scrollTo(0, 0);
		}, 10);

		// registration filled information
		// event listener for the change in local storage
		document.addEventListener('tokenLogin', function (e) {
			let cartItems = sessionStorage.getItem('token');

			if (cartItems) {
				that.setState({ loggedinReg: true });
			}
			// Rerendering the component
			that.forceUpdate();
		});
		this.getUserDetails();
		//let checkoutMode = sessionStorage.getItem('checkoutMode')

		if (this.checkoutMode !== null) {
			if (localStorage.getItem('token') === null)
				this.handleChange(null, 2)
			else
				this.handleChange(null, this.checkoutMode === 'Login' ? 0 : (this.checkoutMode === 'Register' ? 1 : 2))
		} else {
			if (localStorage.getItem('token') !== null && (this.props.customerDetails && this.props.customerDetails !== null &&
				(this.props.customerDetails.accountAddresses === null || this.props.customerDetails.accountAddresses.length === 0))) {

				this.handleChange(null, 1)
			}
		}
		if (sessionStorage.getItem('errorPaymentMsg')) {
			this.setState({
				errorMessage: true, error: sessionStorage.getItem('errorPaymentMsg'),
			}, () =>
				document.getElementById('body').classList.add('has-toast'))
		}
		sessionStorage.removeItem('errorPaymentMsg');
		var totalPrice = 0;
		if (!!this.props.minPrice && Object.keys(this.props.minPrice).length > 0) {
			if (this.state.cartItems.length > 0) {
				this.state.cartItems.map(items => {
					var total = ((items.price) * (items.quantity));
					totalPrice = totalPrice + total;
					return true;
				});
			}
			if (totalPrice < this.props.minPrice.minimumOrderAmount) {
				this.setState({
					hasMinTotal: false,
					errorMessage: true,
					error: "$" + parseFloat(this.props.minPrice.minimumOrderAmount).toFixed(2) + " " + "Minimum Order Requirement."
				}, () => {
					document.getElementById('body').classList.add("has-toast");
				})
			} else {
				this.setState({
					hasMinTotal: true
				})
			}
		}

	}

	componentWillUnmount() {
		sessionStorage.setItem('checkoutMode', this.state.showLogin ? 'Login' : (this.state.showRegister ? 'Register' : 'Guest'))
		//alert(this.state.showLogin ? "Login" : (this.state.showRegister ? "Register" : "Guest"))
	}

	getUserDetails = () => {
		if (localStorage.getItem('token') !== null && localStorage.getItem('accessToken')) {
			this.props.actionGetCustomer();
		}
	};

	/**
	 * @description for submiting the order and move to payment page.
	 * @param {*} fileds
	 * @memberof check
	 */
	ordersubmission(fileds) {
		this.props.submitOrder(fileds).then((res) => {
			if (this.props.submitResponse.success === true) {
				this.showPayment();
			} else {
				this.setState({ errorMessage: true });
			}
		});
	}

	emailValidate = (fields, callContact) => {
		let emailPostData = {
			email: fields.email,
		};
		let url = UrlConstants.EmailValidate;
		this.setState({ spinner: false, error: '', errorMessage: false });
		APIUtil.postMethod(url, emailPostData, true)
			.then((response) => {
				if (response.status === 200 && response.data.success) {
					if (callContact) this.createCustomer(fields, false);
					this.setState({ error: '', spinner: false, errorMessage: false, emailValid: true }, () => document.getElementById("body").classList.remove("has-toast"));
				} else {
					this.setState({
						error: response.data.message !== null ? response.data.message : '',
						spinner: false,
						errorMessage: true,
						emailValid: false,
					}, () => document.getElementById("body").classList.add("has-toast"));
				}
			})
			.catch((err) => {
				this.setState({ error: err.message, spinner: false, errorMessage: true, emailValid: false });
			});
	};
	/**
	 * @description for filling up the registration page and move to payment page.
	 * @param {*} fileds
	 * @memberof check
	 */
	createCustomer(fileds, isForFormSubmit, fromUpdateContact) {
		let isForCustomer = localStorage.getItem('accountId') === null || isForFormSubmit === false;
		this.setState({ spinner: isForCustomer && !fromUpdateContact ? false : true, error: '', errorMessage: false });
		this.props
			.actionCreateCustomer(fileds, this.state.showGuest, isForFormSubmit)
			.then((res) => {
				if (this.props.createCustomerDetail.success === true) {
					this.setState({ spinner: false, errorMessage: false, error: '' }, () => {

						if (!isForCustomer) {
							this.showPayment();
						} else if (this.props.createCustomerDetail.data.id && this.props.createCustomerDetail.data.accountContacts) {
							localStorage.setItem('accountId', this.props.createCustomerDetail.data.id);
							if (this.props.createCustomerDetail.data.accountContacts.length > 0) {
								localStorage.setItem('contactId', this.props.createCustomerDetail.data.accountContacts[0].id);
							}
							if (this.props.createCustomerDetail.data.accountAddresses !== null) {
								this.props.createCustomerDetail.data.accountAddresses.map((item) => {
									if (item.addressType === 'SHIPPING_ADDRESS')
										localStorage.setItem('shippingId', item.id);
									else if (item.addressType === 'BILLING_ADDRESS')
										localStorage.setItem('billingId', item.id);
								});
							}
							if (this.state.showGuest) {
								this.showPayment();
							}
						} else if (fromUpdateContact) {
							trackEvent(EventNames.Action.ACTION_UPDATE_CUSTOMER, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, fileds)
							localStorage.removeItem('accountId');
							localStorage.removeItem('contactId');
							this.getUserDetails()
						}
					});
					this.setState({
						spinner: false,
					});
				} else {
					this.setState({
						errorMessage: true,
						spinner: false,
						error: this.props.createCustomerDetail.message,
					});
				}
			})
			.catch((productData) => {
				this.setState({ spinner: false });
			});
	}

	/**
	 * @description Add tax to given price
	 * @param {*} price
	 */
	taxCalculation = (price) => {
		let taxPercent = 8.5; // static tax(in %)
		return Number((price * taxPercent) / 100);
	};

	/**
	 * @description show Login Accordion
	 * @memberof check
	 *
	 */
	showLoginAccordion() {
		sessionStorage.setItem('newUserCheckout', false);
		if (!this.state.showLogin) {
			localStorage.removeItem('locationId');
			localStorage.removeItem('shippingId');
			localStorage.removeItem('billingId');
		}
		this.setState({
			showLogin: true,
			showRegister: false,
			showGuest: false,
			errorMessage: false
		}, () => document.getElementById("body").classList.remove("has-toast"));
	}

	/**
	 * @description show Register Accordion
	 * @memberof check
	 *
	 */
	showRegisterAccordion() {
		sessionStorage.setItem('newUserCheckout', false);
		if (!this.state.showRegister) {
			localStorage.removeItem('locationId');
			localStorage.removeItem('shippingId');
			localStorage.removeItem('billingId');
		}
		sessionStorage.setItem('billingInvalid', 'no');
		sessionStorage.setItem('shippingInvalid', 'no');
		this.setState({
			showRegister: true,
			showLogin: false,
			showGuest: false,
		});
	}

	/**
	 * @description show Register Accordion
	 * @memberof check
	 *
	 */
	showGuestAccordion() {
		sessionStorage.setItem('newUserCheckout', false);
		if (!this.state.showGuest) {
			localStorage.removeItem('locationId');
			localStorage.removeItem('shippingId');
			localStorage.removeItem('billingId');
		}
		this.setState({
			showRegister: false,
			showLogin: false,
			showGuest: true,
		});
	}

	/**
	 * @description show payment screen
	 * @memberof check
	 */
	showPayment() {
		window.scrollTo(0, 0);
		this.setState({
			isPayment: 1,
		});
	}

	/**
	 * @description show login/registration accordion
	 * @memberof check
	 */
	goToLogin() {
		this.setState({
			isPayment: 0,
		});
	}

	/**
	 *
	 * @description show Thanku screen
	 * @memberof check
	 */
	OrderPlaced() {
		this.setState({
			isPayment: 2,
		});
		this.props.isThanku();
	}

	/**
	 *
	 *@description for getting the field values that have been filled in the registration form.
	 * @param {*} fields
	 * @memberof check
	 */
	getFields(fields) {
		this.setState({ formData: fields });
	}

	handleClose = () => {
		this.setState({ errorMessage: false, error: '' });
		document.getElementById('body').classList.remove('has-toast');
	};

	onLoginSuccess = (response) => {
		this.getUserDetails();
	};

	handleChange = (event, newValue) => {
		if (newValue === 0) this.showLoginAccordion();
		else if (newValue === 1) {
			this.showRegisterAccordion()
			if (this.state.fromForgot) {
				this.setState({
					fromForgot: false
				})
			}
		} else if (newValue === 2) {
			this.showGuestAccordion()
			if (this.state.fromForgot) {
				this.setState({
					fromForgot: false
				})
			}
		}

		this.setState({
			tabIndex: newValue,
			errorMessage: false,
			error: ''
		});
		sessionStorage.setItem("chk_tabIndex", parseInt(newValue));
	};

	makeHref() {
		this.setState({
			fromForgot: true
		})
		this.handleChange(null, 0)

	}

	render() {
		var link = <a href={this.makeHref} onClick={() => this.makeHref()}> <u>click here</u> </a>;
		return (
			<div>
				<div>
					{this.state.spinner  ? <Spinner /> : ''}

					<div className="wizard-step">
						<Typography variant="h1" className="heading-hidden" aria-hidden="true">
							Checkout
						</Typography>
						<div className="step-header shipping-details">
							<div className="step-header-type"></div>
						</div>

						<Container className="container-root checkout-container">
							{this.state.isPayment === 0 ? (
								<Grid container spacing={2}>
									{localStorage.getItem('token') === null ||
										localStorage.getItem('token').length === 0 ||
										(localStorage.getItem('token') !== null && (this.props.customerDetails && this.props.customerDetails !== null &&
											(this.props.customerDetails.accountAddresses === null || this.props.customerDetails.accountAddresses.length === 0))) ? (
											<Grid
												item
												xs={12}
												sm={6}
												className="checkout-form-wrapper registration-col tab-col"
											>
												{/* <Tabs
													value={this.state.tabIndex}
													onChange={this.handleChange}
													aria-label="Checkout Tabs"
													classes={{
														root: 'checkout-root',
														flexContainer: 'checkout-flexContainer',
														indicator: 'checkout-indicator',
													}}
												>
													<Tab label="Log in" {...a11yProps(0)} />
													<Tab label="Register" {...a11yProps(1)} />
													<Tab label="Guest Checkout" {...a11yProps(2)} />
												</Tabs> */}
												<TabPanel value={this.state.tabIndex} index={0}>
													<div className="checkout-content-head">
														<h2>Log in to checkout</h2>
													</div>
													<div className="checkout-content-body">
														<div className="checkout-content-body-inner">
															<Login
																changeForgotToLogin={() => this.setState({
																	fromForgot: false
																})}
																closeLoginDialog={() => { }}
																fromForgot={this.state.fromForgot}
																loginClicked={(response) => this.onLoginSuccess(response)}
															/>
														</div>
													</div>
												</TabPanel>
												<TabPanel value={this.state.tabIndex} index={1}>
													<div className="checkout-content-head">
														{localStorage.getItem('token') === null ? <h2>New User? Register to checkout</h2>
															: <h2>Checkout information</h2>}
													</div>
													<div className="checkout-content-body">
														<Registration
															hasMinTotal={this.state.hasMinTotal}
															emailValid={this.state.emailValid}
															guestCheckout={this.state.showGuest}
															userInfo={this.props.customerDetails}
															emailValidate={(email, callContact, fields) =>
																this.emailValidate(email, callContact, fields)
															}
															zipcodeValidData={this.props.zipcodeValidData}
															moveToPrevStepCheckout={() => this.props.moveToPrevStep()}
															getTaxtLoader={(data, summuryData) =>
																this.setState(
																	{
																		isSummuryLoader: data,
																		summuryTaxData: summuryData,
																	},
																	() =>
																		this.setState({
																			summuryTaxData: summuryData,
																		})
																)
															}
															createCustomer={(fileds, isForFormSubmit) => {
																if (this.props.customerDetails && this.props.customerDetails.accountContacts) {
																	localStorage.setItem('accountId', this.props.customerDetails.id);
																	if (this.props.customerDetails.accountContacts.length > 0) {
																		localStorage.setItem('contactId', this.props.customerDetails.accountContacts[0].id);
																	}
																}
																this.createCustomer(fileds, isForFormSubmit, false)
															}}
															spinner={this.props.spinner}
															createAccount={fileds => {
																localStorage.setItem('accountId', this.props.customerDetails.id);
																if (this.props.customerDetails.accountContacts.length > 0) {
																	localStorage.setItem('contactId', this.props.customerDetails.accountContacts[0].id);
																}
																this.createCustomer(fileds, false, true)
															}}
														/>
													</div>
												</TabPanel>
												<TabPanel value={this.state.tabIndex} index={2}>
													<div className="checkout-content-head">
														<h2>Guest checkout</h2>
													</div>
													<div className="checkout-content-body">
														<Registration
															hasMinTotal={this.state.hasMinTotal}
															emailValid={this.state.emailValid}
															userInfo={this.props.customerDetails}
															guestCheckout={true}
															emailValidate={(email) => this.emailValidate(email)}
															zipcodeValidData={this.props.zipcodeValidData}
															moveToPrevStepCheckout={() => this.props.moveToPrevStep()}
															getTaxtLoader={(data, summuryData) =>
																this.setState(
																	{
																		isSummuryLoader: data,
																		summuryTaxData: summuryData,
																	},
																	() =>
																		this.setState({
																			summuryTaxData: summuryData,
																		})
																)
															}
															createAccount={fileds => {
																localStorage.setItem('accountId', this.props.customerDetails.id);
																if (this.props.customerDetails.accountContacts.length > 0) {
																	localStorage.setItem('contactId', this.props.customerDetails.accountContacts[0].id);
																}
																this.createCustomer(fileds, false, true)
															}}
															createCustomer={(fileds, isForFormSubmit) => {
																if (this.props.customerDetails && this.props.customerDetails.accountContacts) {
																	localStorage.setItem('accountId', this.props.customerDetails.id);
																	if (this.props.customerDetails.accountContacts.length > 0) {
																		localStorage.setItem('contactId', this.props.customerDetails.accountContacts[0].id);
																	}
																}
																this.createCustomer(fileds, isForFormSubmit, false)
															}}
															spinner={this.props.spinner}
														/>
													</div>
												</TabPanel>
												<Snackbar
													open={this.state.errorMessage}
													anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
													className="snackbar snackbar-full"
												>
													{this.state.error != null && this.state.error.length > 0 && this.state.error.includes('is already a registered user') ?
														<MuiAlert variant="filled" onClose={this.handleClose} severity="error">
															{this.state.error}
															{' Please'} {link} {' for the assistance'}

														</MuiAlert> :
														<MuiAlert variant="filled" onClose={this.handleClose} severity="error">
															{this.state.error != null && this.state.error.length > 0
																? this.state.error
																: 'Some Technical Issue'}

														</MuiAlert>}
												</Snackbar>

												{this.state.isPayment === 2 ? <ThankYouMessage /> : ''}
											</Grid>
										) : (
											<Grid item xs={12} sm={6} className="checkout-form-wrapper registration-col">
												<div className={'checkout-content-wrapper'}>
													{this.props.customerDetails && (
														<CheckoutDetails
															hasMinTotal={this.state.hasMinTotal}
															getCustomer={() => this.props.actionGetCustomer()}
															userInfo={this.props.customerDetails}
															guestCheckout={this.state.showGuest}
															getTaxtLoader={(data, summuryData) =>
																this.setState({
																	isSummuryLoader: data,
																	summuryTaxData: summuryData,
																})
															}
															spinner={this.props.spinner}
															showPayment={() => this.showPayment()}
															createCustomer={fileds => this.createCustomer(fileds, false, true)}
														/>
													)}
												</div>
												<Snackbar
													open={this.state.errorMessage}
													anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
													className="snackbar snackbar-full"
												>
													<MuiAlert variant="filled" onClose={this.handleClose} severity="error">
														{this.state.error.length > 0
															? this.state.error
															: 'Some Technical Issue'}
													</MuiAlert>
												</Snackbar>
											</Grid>
										)}

									{/* order summary */}
									<Grid item xs={12} sm={6} className="summary-col">
										<OrderSummary
											summuryTaxData={this.state.summuryTaxData}
											isSummuryLoader={this.state.isSummuryLoader}
											shippingCharges={this.props.shippingCharges}
											taxCalculation={this.taxCalculation}
										/>
									</Grid>
								</Grid>
							) : (
									''
								)}

							<Grid container spacing={2}>
								{this.state.isPayment === 1 ? (
									<Grid item xs={12} className="payment-grid">
										{/* Payment */}
										<Payment
											onRef={(ref) => (this.payment = ref)}
											payload={(e) => {
												this.setState({ payload: e });
											}}
											fields={this.state.formData}
											showPayment={() => this.showPayment()}
											goToLogin={() => this.goToLogin()}
											OrderPlaced={() => this.OrderPlaced()}
										/>
									</Grid>
								) : (
										''
									)}
							</Grid>
						</Container>
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		submitResponse: state.reducer.submit,
		createCustomerDetail: state.reducer.createCustomer,
		isAuthenticated: state.reducer.isAuthenticated,
		user: state.reducer.loginResponse.user,
		customerDetails: state.reducer.customerDetails,
		minPrice: state.reducer.minAmount
	};
};

export default connect(mapStateToProps, { actionCreateCustomer, actionGetCustomer })(withRouter(check));
