import React, { Component } from "react";
import { Button, Typography } from "@material-ui/core";
import { Link } from "react-router-dom";
import CustomEvent from "custom-event";
import {
  trackEvent,
  EventNames
} from '../../appinsights/EventTrack';

class ThankYou extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orderId: "",
      orderNumber: "",
      OrderData: [],
    };
  }

  componentDidMount() {
    if (sessionStorage.getItem("_sc_re")) {
      var event = new CustomEvent("cartitem");
      document.dispatchEvent(event);
      this.setState({ OrderData: (sessionStorage.getItem("_sc_re") ? JSON.parse(sessionStorage.getItem("_sc_re")) : []) });
    }
  }

  goToHome = () => {
    this.props.history.push("/home");
  };

  render() {
    sessionStorage.removeItem('taxZipCode');
    const OrderData = this.state.OrderData;
    return (
      <div className="wizard-step thankyou-wizard">
        <Typography variant="h1" className="heading-hidden" aria-hidden="true">Thank You</Typography>
        <div className="step-header thank-you">
          <div className="step-header-type"></div>
        </div>

        {JSON.parse(sessionStorage.getItem("newUserCheckout")) === true ?
         <div className="activation-info">
          <div className="activation-info-inner">
            <Typography variant="h2">Thank you for registering! An email with a link and directions for how to setup your password and become a registered member has been sent.</Typography>
            {/* <Button
              className="icn-close"
              id="closeActivation"
              aria-label="close password activation" >
              <i className="fa fa-times"></i>
            </Button> */}
          </div>
        </div> 
        : "" }

        <div className="thankyou-container">
          <div className="thankyou">
            <div className="thankyou-header">
              <Typography variant="h2">
                Thank you for your order! (#{OrderData.orderId})
              </Typography>
            </div>
            <div className="thankyou-content">
              <div className="thankyou-image"></div>
              <div className="thankyou-line1 text-center">Order Number: #{OrderData.orderId}, <span>You will receive your order in 2-3 business days.</span></div>
              <div className="thankyou-line1">A confirmation email and receipt has been sent to the email provided. Thank you for shopping with Frito Lay!</div>
              <div className="thankyou-line2">
                If you have any questions or need to get a hold of customer support, please contact <Link to="/contactus" aria-label="contact us">here</Link>, or email customersupport@fritolay.com
              </div>
              <div className="thankyou-note">
                Note: We are currently experiencing shipping delays due to COVID-19. All orders are processed in the order that they are received, and we are doing our best to ship all orders on time. Thank you for your patience.
              </div>
            </div>
          </div>
        </div>
        <div className="thankyou-action">
          <Button
            className="btn-mui btn-yellow"
            id="goToHome"
            aria-label="go To Home"
            onClick={() => {
              trackEvent(EventNames.Action.ACTION_THANK_YOU, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname)
              this.goToHome();
            }}
          >
            Done
            </Button>
        </div>
      </div>
    );
  }
}
export default ThankYou;