import React, { Component } from "react";
import Carousel, { consts } from "react-elastic-carousel";
import PropTypes from "prop-types";
import { Button } from "@material-ui/core";
import { BreakPoints } from "../../../config/constants/breakpoints.constant";
import "./snacksCarousel.scss";

export default class SnacksCarousel extends Component {
  /*CUSTOMIZE ARROW*/
  myArrow({ type, onClick, isEdge }) {
    const pointer = type === consts.PREV ? "Prev" : "Next";
    const classField =
      type === consts.PREV ? "rec-arrow-left" : "rec-arrow-right";
    return (
      <Button
        className={`rec rec-arrow rec ${classField}`}
        aria-label={pointer}
        onClick={onClick}
        disabled={isEdge}
      >
        {/* {pointer} */}
      </Button>
    );
  }
  render() {
    const { content, carouselCustomClass } = this.props;
    return (
      <Carousel
        renderArrow={this.myArrow}
        itemPadding={[20, 10]}
        itemsToScroll={1}
        breakPoints={BreakPoints}
        pagination={false}
        className={`snacks-slider ${carouselCustomClass}`}
      >
        {content ? content : ""}
      </Carousel>
    );
  }
}

SnacksCarousel.propTypes = {
  content: PropTypes.array.isRequired,
};
