import React, { Component } from "react";
import "./sectionHeading.scss";
import { Button } from "@material-ui/core";

export default class SectionHeading extends Component {
  getShowAllContent = () => {
    const {
      showAllTitle,
      showAllPosition,
      isLink,
      isFunction,
      ariaLabelShowAll,
    } = this.props;
    if (isFunction) {
      return (
        <Button
          onClick={isFunction}
          aria-label={ariaLabelShowAll || showAllTitle}
          className={`show-all align-${showAllPosition}`}
        >
          {showAllTitle}
        </Button>
      );
    }
    if (isLink) {
      return (
        <a
          href={isLink}
          aria-label={ariaLabelShowAll || showAllTitle}
          className={`show-all align-${showAllPosition}`}
        >
          {showAllTitle}
        </a>
      );
    }
    return (
      <span className={`show-all align-${showAllPosition}`}>
        {showAllTitle}
      </span>
    );
  };
  render() {
    const {
      classField,
      heading,
      ariaLabelHeading,
      isShowAllVisible,
      subHeading,
      ariaLabelSubHeading,
    } = this.props;

    return (
      <div className={`${classField}-heading heading`}>
        {heading && <h3 aria-label={ariaLabelHeading || heading}>{heading}</h3>}
        {subHeading && (
          <p aria-label={ariaLabelSubHeading || subHeading}>{subHeading}</p>
        )}
        {isShowAllVisible && this.getShowAllContent()}
      </div>
    );
  }
}
