import React from "react";
import { ReactTitle } from "react-meta-tags";
import { Route } from "react-router-dom";
import PageNotFound from "../components/404Page/PageNotFound";
import Footer from "./commonComponents/footer/Footer";
import NavBar from "./commonComponents/header/header";
const RouteRapper = ({ component: Component, title, ...rest }) => (
  <Route
    {...rest}
    render={(props) => (
      <React.Fragment>
        <ReactTitle title={title} />
        <NavBar />
        <main
          className={
            "main-contain" +
            " " +
            (window.location.hash === "#/home" || window.location.hash === "#/"
              ? "main-contain-home"
              : "")
          }
        >
          <div className="main-contain-inner">
            {!!sessionStorage.getItem("_ss_i") &&
            !!sessionStorage.getItem("_rr_n_dts") &&
            !!sessionStorage.getItem("_en_cs") ? (
              <Component {...props} />
            ) : (
              <PageNotFound />
            )}
          </div>
        </main>
        <Footer />
      </React.Fragment>
    )}
  />
);
export default RouteRapper;
