	shouldSubmitDisabled() {
		return (
			this.state.submitDisabled ||
			this.state.intialAddress.firstname.length < 2 ||
			this.state.intialAddress.lastname.length < 2 ||
			//this.state.intialAddress.contact.length < 10 ||
			this.state.intialAddress.email.length === 0 ||
			!this.validateEmail(this.state.intialAddress.email) ||
			!this.props.emailValid
		);
	}

	billingFieldDisable(values) {
		//console.log(values);
		//if(!values.billingZipCode && !values.billingCity && !values.billingAddressLine1 && !values.billingAddressLine2){
		this.setState({ isDisabledBillingfield: true });
		//}
	}

	getAdressLine(e, index) {
		// var lableData = Array.isArray(e.AddressLine)?(!!e.AddressLine?e.AddressLine:"")+(!!e.AddressLine?e.AddressLine:"")
		var city = '';
		var state = '';
		var country = '';
		var zipcode = '';
		var addressLine = !!e.AddressLine ? e.AddressLine : '';
		if (Array.isArray(e.AddressLine)) {
			addressLine =
				(!!e.AddressLine[0] ? e.AddressLine[0] : '') + (!!e.AddressLine[1] ? ', ' + e.AddressLine[1] : '');
		}
		if (!!e.CountryCode) {
			country = e.CountryCode;
		}
		if (!!e.PoliticalDivision1) {
			state = e.PoliticalDivision1;
		}
		if (!!e.PoliticalDivision2) {
			city = e.PoliticalDivision2;
		}
		if (!!e.PostcodePrimaryLow) {
			zipcode = e.PostcodePrimaryLow;
		}
		const addressLabel =
			(!!addressLine ? addressLine : '') +
			(!!city ? ' ' + city : '') +
			(!!state ? ' ' + state : '') +
			(!!country ? ', ' + country : '') +
			(!!zipcode ? ' ' + zipcode : '');
		return (
			<FormControlLabel
				onChange={(e) => this.setState({ selectedAddress: index })}
				value={index}
				key={`${'address1' + index}`}
				control={<Radio />}
				label={addressLabel}
			/>
		);
	}

	manageSuggestedAddress() {
		const addressData = !!this.state.addressVerifyData[this.state.selectedAddress]
			? this.state.addressVerifyData[this.state.selectedAddress].AddressKeyFormat
			: '';
		var intialAddress = !!sessionStorage.getItem('formValues')
			? JSON.parse(sessionStorage.getItem('formValues'))
			: {};
		if (this.state.isAddress != 'billing') {
			if (Array.isArray(addressData.AddressLine)) {
				intialAddress.shippingAddressLine1 = addressData.AddressLine[0];
				intialAddress.shippingAddressLine2 =
					addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : '';
			} else {
				intialAddress.shippingAddressLine1 = addressData.AddressLine;
				intialAddress.shippingAddressLine2 = '';
			}
			
			intialAddress.shippingCountry = !!addressData.CountryCode ? addressData.CountryCode : '';
			intialAddress.shippingState = !!addressData.PoliticalDivision1 ? addressData.PoliticalDivision1 : '';
			intialAddress.shippingCity = !!addressData.PoliticalDivision2 ? addressData.PoliticalDivision2 : '';
			intialAddress.shippingZipCode = !!addressData.PostcodePrimaryLow ? addressData.PostcodePrimaryLow : '';
			sessionStorage.setItem('taxZipCode',intialAddress.shippingZipCode);
		}
		if (this.state.billingSameAsShipping || this.state.isAddress == 'billing') {
			if (Array.isArray(addressData.AddressLine)) {
				intialAddress.billingAddressLine1 = addressData.AddressLine[0];
				intialAddress.billingAddressLine2 =
					addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : '';
			} else {
				intialAddress.billingAddressLine1 = addressData.AddressLine;
				intialAddress.billingAddressLine2 = '';
			}
			intialAddress.billingCountry = !!addressData.CountryCode ? addressData.CountryCode : '';
			intialAddress.billingState = !!addressData.PoliticalDivision1 ? addressData.PoliticalDivision1 : '';
			intialAddress.billingCity = !!addressData.PoliticalDivision2 ? addressData.PoliticalDivision2 : '';
			intialAddress.billingZipCode = !!addressData.PostcodePrimaryLow ? addressData.PostcodePrimaryLow : '';
		}
		this.setState({ intialAddress, selectedAddress: 0, addrKey: this.state.addrKey + 1, addressModal: false }, () =>
			this.checkTaxDetails(this.state.intialAddress, true, this.state.isAddress)
		);
	}

	handleClose = () => {
		this.setState({ ZipfailMessage: '' });
		document.getElementById('body').classList.remove('has-toast');
	};

	nameFieldValidation = (e, setFieldValue) => {
		// handleBlur(e);

		this.setState({ [e.target.id]: '' });
		var re = /^[a-zA-Z '.]*$/;
		if (!re.test(e.target.value)) {
			if (
				e.target.id === 'firstname' ||
				e.target.id === 'shippingFirstname' ||
				e.target.id === 'billingFirstname'
			) {
				const eventValue = e.target.id;
				this.setState({ [e.target.id]: 'First Name must be only alphabets.' }, () => {
					setTimeout(() => {
						this.setState({ [eventValue]: null }, () => { });
					}, 3000);
				});
			} else {
				const eventValue = e.target.id;
				this.setState({ [e.target.id]: 'Last Name must be only alphabets.' }, () => {
					setTimeout(() => {
						this.setState({ [eventValue]: null }, () => { });
					}, 3000);
				});
			}
			if (e.target.value === '') {
				setFieldValue([e.target.id], e.target.value);
				this.setState({ [e.target.id]: '' });
			}
		} else if (e.target.value || e.target.value === '') {
			if (this.state.billingSameAsShipping) {
				if (e.target.id === 'shippingFirstname') {
					setFieldValue('billingFirstname', e.target.value);
				}
				if (e.target.id === 'shippingLastname') {
					setFieldValue('billingLastname', e.target.value);
				}
			}
			setFieldValue([e.target.id], e.target.value);
		}
	};

	addressFieldValidation = (e, values, setFieldValue, handleBlur) => {
		this.setState({ [e.target.id]: '' });
		var re = /^[a-zA-Z0-9 '.#,/'-]*$/;
		if (!re.test(e.target.value)) {
			if (e.target.id === 'shippingAddressLine1' || e.target.id === 'billingAddressLine1') {
				const eventValue = e.target.id;
				this.setState({ [e.target.id]: 'Address Line 1 must be only alphabets.' }, () => {
					setTimeout(() => {
						this.setState({ [eventValue]: null });
					}, 3000);
				});
			} else if (e.target.id === 'shippingAddressLine2' || e.target.id === 'billingAddressLine2') {
				const eventValue = e.target.id;
				this.setState({ [e.target.id]: 'Address Line 2 must be only alphabets.' }, () => {
					setTimeout(() => {
						this.setState({ [eventValue]: null });
					}, 3000);
				});
			} else if (e.target.id === 'shippingCity' || e.target.id === 'billingCity') {
				const eventValue = e.target.id;
				this.setState({ [e.target.id]: 'City must be only alphabets.' }, () => {
					setTimeout(() => {
						this.setState({ [eventValue]: null });
					}, 3000);
				});
			}
			if (e.target.value === '') {
				setFieldValue([e.target.id], e.target.value);
				this.setState({ [e.target.id]: '' });
			}
		} else if (e.target.value || e.target.value === '') {
			if (this.state.billingSameAsShipping) {
				if (e.target.id === 'shippingAddressLine1') {
					setFieldValue('billingAddressLine1', e.target.value);
				}
				if (e.target.id === 'shippingAddressLine2') {
					setFieldValue('billingAddressLine2', e.target.value);
				}
				if (e.target.id === 'shippingCity') {
					setFieldValue('billingCity', e.target.value);
				}
				//setFieldValue( "billingCity",  e.target.value);
			}
			if (e.target.id === 'shippingAddressLine1') {
				values.shippingAddressLine1 = e.target.value;
			}
			if (e.target.id === 'shippingCity') {
				values.shippingCity = e.value;
				this.checkTaxDetails(e, handleBlur, values, true);
			}
			setFieldValue([e.target.id], e.target.value);
		}
	};

	validateEmail = (email) => {
		const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(String(email).toLowerCase());
	};

