/**
 *
 * @description action performed to get customer details.
 * @let {*} url: For setting the url for get customer .
 * @returns to get customer details response.
 *
 */
export function actionGetCustomer() {
  let url = UrlConstants.GetCustomer;
  return function (dispatch) {
    if (localStorage.getItem("accessToken")) {
      return APIUtil.getMethod(url, true, true).then((response) => {
        let res = response.data;
        if (!!res && !!res.message && res.message === "Expired token") {
          localStorage.removeItem("token");
          localStorage.removeItem("accessToken");
          localStorage.removeItem("idToken");
          localStorage.removeItem("user");
          dispatch({
            type: types.LOGOUT,
          });
        } else if (response.status === 200 && res.success) {
          dispatch({
            type: types.CUSTOMERDETAILS,
            customerDetails:
              response.data && response.data.data
                ? response.data.data
                : response.data,
          });
        } else {
          dispatch({
            type: types.CUSTOMERDETAILS,
            customerDetails: null,
          });
        }
        return "";
      });
    } else {
      /* dispatch({
				type: types.CUSTOMERDETAILS,
				customerDetails: null,
			}); */
      localStorage.removeItem("token");
      localStorage.removeItem("idToken");
      localStorage.removeItem("user");
      dispatch({
        type: types.LOGOUT,
      });
    }
  };
}

