import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  OutlinedInput,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import { ErrorMessage, Formik } from "formik";
import React, { Component } from "react";
import { connect } from "react-redux";
import * as Yup from "yup";
import {
  actionContactUs,
  actionGetMinAmount,
} from "../../actions/ProductAction";
import Spinner from "../Spinner";

class ContactUs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      subject: "",
      firstname: "",
      email: "",
      contact: "",
      orderId: "",
      description: "",
      error: false,
      countryCode: "+1",
      submissionStatus: false,
      submissionMessage: "",
      firstName: "",
      Contact: "",
      orderNumber: "",
      successMessage: "",
      contactsubData: [],
      showSpinner: false,
    };
  }
  componentDidMount() {
    if (!!this.props.contactSub) {
      const { contactUsSubject } = this.props.contactSub;
      const contactsubData = contactUsSubject.split(",");
      this.setState({ contactsubData });
    } else {
      try {
        this.props.actionGetMinAmount().then(() => {
          const { contactUsSubject } = this.props.contactSub;
          const contactsubData =
            contactUsSubject && contactUsSubject.split(",");

          this.setState({
            contactsubData: contactsubData ? contactsubData : [],
          });
        });
      } catch (error) {
        document.getElementById("body").classList.add("has-toast");
      }
    }
  }

  componentWillUnmount() {
    document.getElementById("body").classList.remove("has-toast");
  }

  orderNumberCheck = (e, setFieldValue) => {
    this.setState({ orderNumber: "" });
    var re = /[^\w\s]/gi;
    if (re.test(e.target.value)) {
      this.setState(
        { orderNumber: "Special characters are not allowed" },
        () => {
          setTimeout(() => {
            this.setState({ orderNumber: "" });
          }, 3000);
        }
      );
      if (e.target.value === "") {
        setFieldValue("orderId", "");
        this.setState({ orderNumber: "" });
        //errors.firstname = "Firstname is Required"
      }
    } else if (e.target.value || e.target.value === "") {
      setFieldValue("orderId", e.target.value);
    }
  };

  nameCheck = (e, setFieldValue) => {
    this.setState({ firstName: "" });
    var re = /^[a-zA-Z '.]*$/;
    if (!re.test(e.target.value)) {
      this.setState({ firstName: "Name must be only alphabets" }, () => {
        setTimeout(() => {
          this.setState({ firstName: "" });
        }, 3000);
      });

      if (e.target.value === "") {
        setFieldValue("firstname", "");
        this.setState({ firstName: "" });
      }
    } else if (e.target.value || e.target.value === "") {
      setFieldValue("firstname", e.target.value);
    }
  };
  contactCheck = (e, setFieldValue) => {
    this.setState({ Contact: "" });
    var re = /^[0-9\b]+$/;
    if (!re.test(e.target.value)) {
      this.setState({ Contact: "Phone Number must be a number type" }, () => {
        setTimeout(() => {
          this.setState({ Contact: "" });
        }, 3000);
      });
      if (e.target.value === "") {
        setFieldValue("contact", "");
        this.setState({ Contact: "" });
      }
    } else if (
      (e.target.value || e.target.value === "") &&
      e.target.value.length <= 10
    ) {
      setFieldValue("contact", e.target.value);
    } else if (e.target.value > 10) {
      this.setState({ Contact: "Phone Number must be 10 digits" }, () => {
        setTimeout(() => {
          this.setState({ Contact: "" });
        }, 3000);
      });
    }
  };
  render() {
    return (
      <div className="common-box">
        <div className="common-box-header">
          <Typography variant="h1">Contact Us</Typography>
        </div>
        <Formik
          initialValues={this.state}
          /* Validations for Form */
          validationSchema={Yup.object().shape({
            contact: Yup.string()
              .nullable()
              .trim()
              .min(10, "Phone Number must be 10 digits")
              .max(10, "Phone Number must be 10 digits")
              // .transform(value => (isNaN(value) ? undefined : value))
              .required("Phone Number is required")
              .test("contact", "Phone Number must be a number type", function (
                value
              ) {
                if (value === null || value === "") {
                  return true; // As it is not required.
                }
                var regexp = /((\([0-9]{3}\))|[0-9]{3})[\s-]?[\0-9]{3}[\s-]?[0-9]{4}$/;
                if (!regexp.test(value)) {
                  return false;
                }
                return true;
              }),
            email: Yup.string()
              .trim()
              .email("Enter a valid email")
              .required("Email is required"),
            subject: Yup.string().required("Subject is required"),
            firstname: Yup.string()
              .nullable()
              .trim()
              .min(2, "Name sholud be atleast 2 characters")
              // .transform(value => (isNaN(value) ? undefined : value))
              .required("Name is required"),
            // .test("contact", "Name must be only alphabet", function (
            //   value
            // ) {
            //   if (value === null || value === "") {
            //     return true; // As it is not required.
            //   }
            //   var regexp = /^[a-zA-Z]+$/;
            //   if (!regexp.test(value)) {
            //     return false;
            //   }
            //   return true;
            // })
            orderId: Yup.string().trim(),
            description: Yup.string()
              .required("Description is required")
              .trim(),
            //countryCode: Yup.string().required("CountryCode Cant be Empty"),
          })}
          onSubmit={(fields, { resetForm }) => {
            let postdata = {
              emailType: "contactUs",
              name: fields.firstname,
              email: fields.email,
              phoneNo: fields.countryCode + "-" + fields.contact,
              orderNo: fields.orderId,
              subject: fields.subject,
              description: fields.description,
            };
            this.setState({ showSpinner: true });
            this.props.actionContactUs(postdata).then(() => {
              if (this.props.contactResponse.status === 200) {
                if (
                  this.props.contactResponse &&
                  this.props.contactResponse.data.success === true &&
                  this.props.contactResponse.data.message
                ) {
                  this.setState(
                    {
                      successMessage:
                        this.props.contactResponse.data &&
                        this.props.contactResponse.data?.message,
                      submissionMessage: "success",
                      submissionStatus: true,
                      showSpinner: false,
                    },
                    () => {
                      document
                        .getElementById("body")
                        .classList.add("has-toast");
                    }
                  );
                  resetForm(this.state);
                } else {
                  this.setState(
                    {
                      successMessage:
                        this.props.contactResponse.data &&
                        this.props.contactResponse.data?.message,
                      submissionMessage: "failure",
                      submissionStatus: true,
                      showSpinner: false,
                    },
                    () => {
                      document
                        .getElementById("body")
                        .classList.add("has-toast");
                    }
                  );
                }
              } else {
                this.setState({
                  error: true,
                  successMessage:
                    "We are facing some technical issue. Your request does not submit at this moment. Please try again",
                  submissionMessage:
                    "We are facing some technical issue. Your request does not submit at this moment. Please try again",
                  submissionStatus: true,
                  showSpinner: false,
                });
              }
            });
          }}
        >
          {(props) => {
            const {
              values,
              touched,
              errors,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
            } = props;

            return (
              <form
                id="startSubscriptionForm"
                name="form1"
                onSubmit={handleSubmit}
              >
                <>
                  <div className="common-box-body">
                    <Snackbar
                      open={this.state.submissionStatus}
                      anchorOrigin={{ vertical: "top", horizontal: "center" }}
                      className="snackbar snackbar-full"
                    >
                      <MuiAlert
                        variant="filled"
                        onClose={() => {
                          this.setState({ submissionStatus: false }, () => {
                            document
                              .getElementById("body")
                              .classList.remove("has-toast");
                          });
                        }}
                        severity={
                          this.state.submissionMessage === "success"
                            ? "success"
                            : "error"
                        }
                      >
                        {this.state.successMessage}
                      </MuiAlert>
                    </Snackbar>
                    {this.state.showSpinner ? <Spinner /> : ""}
                    <Grid container spacing={3}>
                      {/* Subject */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group phone-select phone-select-full"
                          variant="outlined"
                        >
                          <InputLabel className="form-label" htmlFor="subject">
                            Subject<span className="text-danger">*</span>
                          </InputLabel>
                          <Select
                            native
                            id="subject"
                            name="subject"
                            value={values.subject}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                              "aria-label": "contact us subject",
                            }}
                            error={touched.subject && Boolean(errors.subject)}
                          >
                            <option aria-label="None" value="">
                              None
                            </option>
                            {this.state.contactsubData.length > 0 &&
                              this.state.contactsubData.map((subject) => {
                                return (
                                  <option key={subject} value={subject}>
                                    {subject}
                                  </option>
                                );
                              })}

                            {/*<option aria-label="None" value="" />
                            <option value="Payment Refunds">
                              Payment Refunds
                          </option>
                            <option value="Returns">Returns</option>
                            <option value="Login issues">Login issues</option>
                            <option value="Payment issues">
                              Payment issues
                          </option>
                            <option value="Problems with order submit">
                              Problems with order submit
                          </option>*/}
                          </Select>
                        </FormControl>
                        <ErrorMessage
                          component="div"
                          name="subject"
                          className="text-danger"
                        />
                      </Grid>

                      {/* Name */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="firstname"
                          >
                            Name<span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="firstname"
                            value={values.firstname}
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) => this.nameCheck(e, setFieldValue)}
                            onBlur={handleBlur}
                            error={
                              touched.firstname && Boolean(errors.firstname)
                            }
                          />
                        </FormControl>
                        {this.state.firstName ? (
                          <div className="text-danger">
                            {this.state.firstName}
                          </div>
                        ) : (
                          <ErrorMessage
                            component="div"
                            name="firstname"
                            className="text-danger"
                          />
                        )}
                      </Grid>

                      {/* Email */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel className="form-label" htmlFor="email">
                            Email<span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="email"
                            name="email"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            value={values.email}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.email && Boolean(errors.email)}
                          />
                        </FormControl>
                        <ErrorMessage
                          component="div"
                          name="email"
                          className="text-danger"
                        />
                      </Grid>

                      {/** Phone Number */}
                      <Grid item xs={12} sm={12}>
                        <Grid container spacing={0}>
                          <Grid item>
                            <FormControl
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label phone-select-label"
                                htmlFor="countryCode"
                              >
                                <span>code</span>
                              </InputLabel>
                              <Select
                                native
                                className="phone-select"
                                name="countryCode"
                                inputProps={{
                                  required: "required",
                                  "aria-required": "true",
                                  "aria-label": "country code",
                                  role: "alert",
                                }}
                                id="countryCode"
                                value={values.countryCode}
                                onChange={handleChange}
                                onBlur={handleBlur}
                              >
                                <option value="+1">+1</option>
                              </Select>
                            </FormControl>
                          </Grid>
                          <Grid item sm xs container>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor="contact"
                              >
                                Phone Number
                                <span className="text-danger">*</span>
                              </InputLabel>
                              <OutlinedInput
                                className="phone-input"
                                id="contact"
                                name="contact"
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                value={values.contact}
                                onChange={(e) =>
                                  this.contactCheck(e, setFieldValue)
                                }
                                onBlur={handleBlur}
                                error={
                                  touched.contact && Boolean(errors.contact)
                                }
                              />
                            </FormControl>
                            {this.state.Contact ? (
                              <div className="text-danger">
                                {this.state.Contact}
                              </div>
                            ) : (
                              <ErrorMessage
                                component="div"
                                name="contact"
                                className="text-danger"
                              />
                            )}
                          </Grid>
                        </Grid>
                      </Grid>

                      {/* Order ID */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel className="form-label" htmlFor="orderId">
                            Order ID
                          </InputLabel>
                          <OutlinedInput
                            id="orderId"
                            name="orderId"
                            inputProps={{
                              "aria-required": "false",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            value={values.orderId}
                            onChange={(e) =>
                              this.orderNumberCheck(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            error={touched.orderId && Boolean(errors.orderId)}
                          />
                        </FormControl>
                        {this.state.orderNumber ? (
                          <div className="text-danger">
                            {this.state.orderNumber}
                          </div>
                        ) : (
                          <ErrorMessage
                            component="div"
                            name="orderId"
                            className="text-danger"
                          />
                        )}
                      </Grid>

                      {/* Description */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <TextField
                            id="description"
                            name="description"
                            label="Description"
                            multiline
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            rows="4"
                            variant="outlined"
                            className="textarea"
                            value={values.description}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={
                              touched.description && Boolean(errors.description)
                            }
                          />
                        </FormControl>
                        <ErrorMessage
                          component="div"
                          name="description"
                          className="text-danger"
                        />
                      </Grid>
                    </Grid>
                  </div>

                  <div className="button-group text-right">
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      className="btn-mui btn-blue"
                      id="submitContact"
                      aria-label="submit Contact"
                    >
                      Submit
                    </Button>
                  </div>
                </>
              </form>
            );
          }}
        </Formik>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    contactResponse: state.reducer.contactResponse,
    contactSub: state.reducer.minAmount,
  };
};

export default connect(mapStateToProps, {
  actionContactUs,
  actionGetMinAmount,
})(ContactUs);
