import { Button, Dialog, DialogContent, Typography, OutlinedInput, InputLabel, FormControl } from "@material-ui/core";
import { ErrorMessage, Formik } from "formik";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import * as Yup from "yup";
import { actionZipcodeVerification } from "../../actions/ProductAction";
import {
  trackEvent,
  EventNames
} from '../../appinsights/EventTrack';

class ZipCode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errorMessage: ""
    };
  }

  componentWillUnmount() {
    this.setState({ errorMessage: null })
  }

  /**
   *  
   * @description for redirecting to signupsteps page after successful validation of Zipcode 
   * @param {*} zipcode
   * 
   */
  redirect(zipcode, response) {
    if (response.success) {
      if (!!response.data) {
        sessionStorage.setItem("_lo_No", response.data.locationId);
      }

    }
    sessionStorage.setItem("Zipcode", zipcode);
    this.props.onClose();
    let clickedBtn = this.props.clickedBtn
    let loggedInUser = localStorage.getItem("accessToken");
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (clickedBtn === "getStarted" && clickedBtn !== "cartIcon") {
      if(loggedInUser && zipcodeVerified===null){
        this.props.history.push("/home");
      }else{
        this.props.history.push("/productlist");
      }
    }
    else {
      this.props.history.push("/cart");

    }
  }

  dailogClose() {
    this.props.onClose()
    this.setState({ errorMessage: "" })
  }

  // Zipcode value set and made a validation.
  handleChange = (zipValue, setFieldValue) => {
    var regexp = /^[0-9\b]+$/;
    zipValue = zipValue.target.value;
    if(zipValue.length<=5){
      if (zipValue === "") {
        this.setState({ errorMessage: "", })
        setFieldValue("zipcode", "")
      } else if(!regexp.test(zipValue)) {
        this.setState({ errorMessage: "Zip Code must be a number type." });
        setTimeout(() => { this.setState({ errorMessage: null }) }, 2000);
      }  else {
        this.setState({ errorMessage: "" })
        setFieldValue("zipcode", zipValue);
        return false;
      }
    }
  }
  makeLogin(e){
    this.props.onClose();
    var event = new CustomEvent('showLoginDialog');
    document.dispatchEvent(event);
  }

  componentDidMount() {
    trackEvent(EventNames.Action.ACTION_ZIP_CODE_POPUP, EventNames.Event.EVENT_PAGE_VIEW, window.location.origin, window.location.pathname)
  }

  render() {

    return (
      <Dialog
        open={this.props.dailogOpen}
        onClose={() => this.dailogClose()}
        maxWidth={false}
        scroll={"body"}
        classes={{
          paper: "modal-container modal-zip modal-paper",
          root: "modal-stack-top"
        }}
        aria-labelledby="zipCode-dialogBox"
      >
        {/* Dialog Content */}
        <DialogContent>
          <div className="zipcode-div">
            <Formik
              validateOnBlur={false}
              enableReinitialize={true}
              initialValues={{ zipcode: "" }}
              // verifies for the zipcode entered by user and submits to api & based on response it redirects or shows error message  
              onSubmit={async values => {
                this.props.actionZipcodeVerification(values).then(() => {
                  let zipResponse = this.props.zipResponse;
                  if (!!zipResponse) {
                    if (zipResponse.data.success === true && zipResponse.status === 200) {
                      this.redirect(values.zipcode, zipResponse.data);
                    } else if (!!zipResponse.data.message) {
                      this.setState({ errorMessage: zipResponse.data.message })
                    }
                    else {
                      this.props.history.push("/invalidzip");
                      this.props.onClose();
                    }
                  }
                });
              }}
              //validations for zipcode
              validationSchema={Yup.object().shape({
                zipcode: Yup.string().required("Zip Code Required")
                  .min(5, "Zip Code must be 5 digits")
                  .max(5, "Zip Code must be 5 digits"),
              })}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  handleBlur,
                  setFieldValue,
                  handleSubmit,
                  setFieldError
                } = props;


                return (
                  <form onSubmit={handleSubmit} className="custom-form" id="zipForm">
                    {/* Zipcode input  */}
                    <div className="zip-info">
                      <h2 aria-label="Where will you be shipping these snacks?" className="info0" id="zipCode-dialogBox">Where will you be shipping these snacks?</h2>
                      <div className="info1">Your ship-to zip code lets us show you which snacks are available in your area!</div>
                    </div>
                      {/* <OtpInput
                            style={{
                              width: "3rem",
                              height: "3rem",
                              margin: "0 1rem",
                              fontSize: "2rem",
                              borderRadius: 4,
                              border: "1px solid rgba(0,0,0,0.3)"
                            }}
                            name="zipcode"
                            numInputs={5}
                          //  isDisabled={false}
                            onChange={(zipValue) =>
                              this.handleChange(zipValue,setFieldValue,setFieldError)
                              //setFieldValue("zipcode", zipValue)
                            }
                            onBlur={handleBlur}
                            isDisabled={this.state.isDisabled}
                            separator={<span>-</span>}
                            value={values.zipcode}                            
                          //  isInputNum={true}
                            shouldAutoFocus
                            className={
                              errors.zipcode && touched.zipcode
                                ? "text-input error"
                                : "text-input"
                            }
                          /> */}
                      <FormControl
                        fullWidth
                        className="form-group zip-input"
                        variant="outlined"
                      >
                        <InputLabel className="form-label" htmlFor="zipcode">
                          enter zip
                          <span className="text-danger">*</span>
                        </InputLabel>
                        <OutlinedInput
                          value={values.zipcode} 
                          name="zipcode"
                          id="zipcode"
                          inputProps={{ "aria-required": "true",role: "alert","aria-invalid":"false"}}
                          onChange={(zipValue) => this.handleChange(zipValue,setFieldValue,setFieldError)}
                          onBlur={handleBlur}
                          autoFocus={true}
                        />

                          {this.state.errorMessage ? <div className="text-danger font-14 mt-10" role="alert">
                            {this.state.errorMessage}</div> : <ErrorMessage
                            role="alert"
                            component="div"
                            name="zipcode"
                            className="text-danger font-14 mt-10"
                          />}
                      </FormControl>

                      <div className="submit-div">
                        <Button
                          type="submit"
                          variant="contained"
                          id="VerifyZip"
                        onClick={() => {
                          trackEvent(EventNames.Action.ACTION_ADD_ZIPCODE, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname)
                          this.setState({ errorMessage: "" })
                        }}
                          className="btn-mui btn-red verify-button"
                        >
                          lets get shopping
                        </Button>
                      </div>

                    {localStorage.getItem('token') === null &&
                      <div className="signin-text">
                        Have an Account?
                        <Button
                          type="button"
                          variant="contained"
                          id="signin"
                          className="btn-link"
                          onClick={(event) => this.makeLogin(event)}
                        >
                          Sign In
                        </Button>
                      </div>
                    }
                  </form>
                );
              }}
            </Formik>

            {localStorage.getItem('token') === null &&
              <div className="has-account">
                <div className="separator">
                  or
              </div>
                <div className="desc">Don't have an account?
              <Button
                    color="primary"
                    className="btn-link"
                    id="SignIn"
                    aria-label="Create account"
                    onClick={() => {

                      this.dailogClose()
                      if (window.location.pathname !== '/create-account') {

                        this.props.history.push('/create-account')
                      }
                    }}
                  >
                    Create an account
                  </Button>
                </div>
              </div>
            }
          </div>
          <Button
            className="icn-close"
            id="zipcodeClose"
            aria-label="zipcode Dialog Close"
            onClick={() => {
              this.dailogClose()
            }}
          >
            <i className="fa fa-times"></i>
          </Button>
        </DialogContent>
      </Dialog>
    );
  }
}

const mapStateToProps = state => {
  return {
    zipResponse: state.reducer.zipResponse,
    validationMessage: state.reducer.validationMessage
  };
};

export default withRouter(connect(mapStateToProps, { actionZipcodeVerification })(ZipCode));
