import React, { Component } from "react";
import {
    Button,
    Grid,
    Typography,
    Card,
    Radio,
    FormControl,
    FormControlLabel,
    FormLabel
} from "@material-ui/core";
import DialogBox from "./../../commonComponents/dialogBox/DialogBox";

class ViewShipping extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showDeletPrompt: false,
            deleteClicked: false,
        }
    }

    getAddress(shippingAddressData){
        var shippingAddress = "";
        shippingAddress += !!shippingAddressData && !!shippingAddressData.shippingAddressline1?shippingAddressData.shippingAddressline1:"";
        shippingAddress += !!shippingAddressData && !!shippingAddressData.shippingAddressline2?", "+shippingAddressData.shippingAddressline2:"";
        shippingAddress += !!shippingAddressData && !!shippingAddressData.shippingCity?", "+shippingAddressData.shippingCity:"";
        shippingAddress += !!shippingAddressData && !!shippingAddressData.shippingState?", "+shippingAddressData.shippingState:"";
        shippingAddress += !!shippingAddressData && !!shippingAddressData.shippingZip?", "+shippingAddressData.shippingZip:"";
        return shippingAddress;
    }

    render() {
        const shippingAddressData = this.props.customerDetails;
        return (
            
            <div className="profile-info">
                <DialogBox
                    dialogClose={() => this.setState({ showDeletPrompt: false, deleteClicked: false })}
                    dialogOpen={this.state.showDeletPrompt}
                    dialogHeading={"Are you sure you want to delete the shipping address?"}
                    dialogWarning
                    dialogCloseConfirm={() => {
                        this.props.removeShippingAddress(this.props.index);
                        this.setState({showDeletPrompt: false, deleteClicked: false})
                    }}
                    ariaLabelYes={"Yes I want to delete the shipping address"}
                    ariaLabelCancel={"No I don't want to delete the shipping address"}
                />
                <>
                <Grid container spacing={6} justify="space-between">
                {/* Biling Address */}
                    <Grid item xs={12} sm={12}>
                        <Grid container spacing={2} alignItems="center" className="title-mini">
                            <Grid item xs container>
                                <Typography variant="h2" component="h2">Shipping Address {this.props.index>0?(this.props.index+1):""}</Typography>
                            </Grid>
                            <Grid item className="text-right">
                                <Button 
                                    variant="contained"
                                    color="primary"
                                    className='btn-link'
                                    id={"editShippingAddress"+(this.props.index === 0 ? '' : this.props.index+1)}
                                    aria-label={"edit Shipping Address"+(this.props.index === 0 ? '' : this.props.index+1)}
                                    onClick={() => this.props.setShipping(false,this.props.index)}
                                >
                                    Edit
                                </Button>
                                {!shippingAddressData.isPrimary?
                                <Button 
                                    variant="contained"
                                    color="primary"
                                    className='btn-link'
                                    id={"removeShippingAddress"+(this.props.index === 0 ? '' : this.props.index+1)}
                                    aria-label={"remove Shipping Address"+(this.props.index === 0 ? '' : this.props.index+1)}
                                    // onClick={() => this.props.removeShippingAddress(this.props.index)}
                                    onClick={() => this.setState({ showDeletPrompt: true, deleteClicked: true })}
                                >
                                    Remove
                                </Button>:""}
                            </Grid>
                        </Grid>
                        <Card className="profile-card">
                            <div className="profile-card-single">
                                <div className="key">{shippingAddressData?(shippingAddressData.shippingFirstname+" "+shippingAddressData.shippingLastname):""}</div>
                                <div className="val">{this.getAddress(shippingAddressData)}</div>
                                <div>
                                    <FormControl component="fieldset">
                                        <FormLabel component="legend" className="heading-hidden">Primary shipping address</FormLabel>
                                        <FormControlLabel
                                            onChange={() => this.props.setPrimaryAddress(shippingAddressData,this.props.index)}
                                            value={shippingAddressData.id}
                                            key={`${'address1' + shippingAddressData.id}`}
                                            control={<Radio />}
                                            label={'Primary shipping address'}
                                        />
                                    </FormControl>
                                </div>
                            </div>
                        </Card>
                    </Grid>
                </Grid>
                </>
            </div>
        );
    }
}
export default ViewShipping;