import React, { Component } from "react";
import { Button } from "@material-ui/core";
import UrlConstants from "../../../config/UrlConstants";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import RemoveCircleOutlineIcon from "@material-ui/icons/RemoveCircleOutline";
import DeleteIcon from "@material-ui/icons/Delete";
import CustomEvent from "custom-event";
import { trackEvent, EventNames } from "../../../appinsights/EventTrack";
import "./quantityCount.scss";

export default class QuantityCount extends Component {
  state = {
    quantity: 0,
    warningMsg: "",
    disableIncrement: false,
  };

  componentDidMount() {
    this.updateExistingQuantity();
  }

  UNSAFE_componentWillReceiveProps() {
    this.updateExistingQuantity();
  }

  updateExistingQuantity = () => {
    const { productInfo } = this.props;
    const cartItems = sessionStorage.getItem("cartItems") || [];

    let cartArray = typeof cartItems === "string" ? JSON.parse(cartItems) : [];

    const indexExist = cartArray.findIndex(
      (item) => item.productId === productInfo.productId
    );
    if (indexExist === -1) {
      this.setState({
        quantity: 0,
      });
    }
    if (indexExist !== -1) {
      this.setState({
        quantity: cartArray[indexExist].quantity,
      });
    }
  };

  /*
    Update the Object
  */
  updateObject = (id, cartItems, addtocart) => {
    const { quantity } = this.state;
    const objIndex = cartItems.findIndex((obj) => obj.productId === id);

    const updatedObj = {
      ...cartItems[objIndex],
      quantity: quantity,
    };

    const updatedCartItems = [
      ...cartItems.slice(0, objIndex),
      updatedObj,
      ...cartItems.slice(objIndex + 1),
    ];

    const filteredCartItems = updatedCartItems.filter(
      (item) => item.quantity !== 0
    );

    this.props.selectedItems && this.props.selectedItems(filteredCartItems);
    addtocart && this.props.closeAndUpdate(filteredCartItems);

    sessionStorage.setItem("cartItems", JSON.stringify(filteredCartItems));
    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
  };

  /*
    Add or Update the product in the cartItems.
  */
  updateQuantity = (qty, addtocart) => {
    const {
      productInfo,
      warningMsgFunc,
      callFromDetail,
      primaryProduct,
    } = this.props;
    const cartItems = sessionStorage.getItem("cartItems") || [];
    let cartArray = typeof cartItems === "string" ? JSON.parse(cartItems) : [];

    const indexExist = cartArray.findIndex(
      (item) => item.productId === productInfo.productId
    );

    if (indexExist === -1) {
      cartArray.push({ ...primaryProduct, ...productInfo });
    }

    if (qty && qty > productInfo.availableQty) {
      this.setState({ warningMsg: UrlConstants.qtyAvailableMsg }, () => {
        warningMsgFunc(this.state.warningMsg, productInfo.availableQty);
      });
    }

    if (qty && qty <= productInfo.availableQty) {
      this.setState({ quantity: parseInt(qty) }, () => {
        !callFromDetail && this.updateObject(productInfo.productId, cartArray);
        addtocart &&
          this.updateObject(productInfo.productId, cartArray, addtocart);
      });
    } else {
      !callFromDetail && this.updateObject(productInfo.productId, cartArray);
    }
  };

  /*
    Decrease quantity of a product
  */
  decrement = () => {
    const { quantity } = this.state;
    const { productInfo, primaryProduct } = this.props;
    if (quantity > 0 && quantity <= productInfo.availableQty) {
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DECREASE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: primaryProduct }
      );
      this.setState(
        { disableIncrement: false, quantity: parseInt(quantity) - 1 },
        () => {
          this.updateQuantity();
        }
      );
    }
  };

  /*
    Increase quantity of a product
  */
  increment = () => {
    const { quantity, warningMsg } = this.state;
    const { productInfo, warningMsgFunc, primaryProduct } = this.props;
    if (quantity >= 0 && quantity < productInfo.availableQty) {
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_INCREASE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: primaryProduct }
      );
      this.setState({ quantity: parseInt(quantity) + 1 }, () => {
        this.updateQuantity();
      });
    }
    if (productInfo.availableQty <= quantity) {
      this.setState(
        { warningMsg: UrlConstants.qtyAvailableMsg, disableIncrement: true },
        () => {
          trackEvent(
            EventNames.Action.ACTION_PRODUCT_MAX_WARN,
            EventNames.Event.EVENT_BUTTON_PRESS,
            window.location.origin,
            window.location.pathname,
            { product: primaryProduct }
          );
          warningMsgFunc(warningMsg, productInfo.availableQty);
        }
      );
    }
  };

  render() {
    const re = /^[0-9\b]+$/;
    const { quantity, disableIncrement } = this.state;
    const { productInfo, callFromDetail, sizeName, price } = this.props;

    return (
      <div className="quantity-count-contain">
        {sizeName && (
          <div className="size-wrapper">
            <div aria-label={sizeName} className="size">
              {sizeName}
            </div>
            <div className="price">{price}</div>
          </div>
        )}
        <div className="quantity-count-wrapper">
          {quantity > 1 && (
            <Button
              aria-label="on clicking on this button Product Quantity will be decremented by 1"
              onClick={this.decrement}
              disabled={quantity === 0}
            >
              <RemoveCircleOutlineIcon className="qty-icon" />
            </Button>
          )}
          {quantity === 1 && (
            <DeleteIcon className="trash-icon" />
          )}
          {quantity > 0 && (
            <>
              <label
                htmlFor={"productListInput" + productInfo.productId}
                className="label-hidden"
              >
                Product Quantity
              </label>
              <input
                type="text"
                id={"productListInput" + productInfo.productId}
                value={re.test(quantity) ? quantity : 0}
                onChange={(e) => this.updateQuantity(e.target.value)}
                ref="quantityBox"
              />
            </>
          )}

          <Button
            aria-label="on clicking on this button Product Quantity will be incremented by 1"
            onClick={this.increment}
            disabled={disableIncrement}
          >
            <ControlPointIcon className="qty-icon" />
          </Button>
          {/* {callFromDetail && (
              <Button
                disabled={quantity <= 0 ? true : false}
                id="addToCart"
                aria-label="add To Cart"
                onClick={() =>
                  this.updateQuantity(this.refs.quantityBox.value, 'addtocart')
                }
                className="btn-mui btn-yellow counter-grid-button"
              >
                Add to Cart
              </Button>
            )} */}
        </div>
      </div>
    );
  }
}
