import React, { Component } from 'react'
import {
  Button,
  Typography,
  Snackbar,
  Grid,
  Container
} from '@material-ui/core'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import {
  actionGetMinAmount,
  actionGetCustomer,
  actionZipcodeVerification
} from '../../actions/ProductAction'
import ZipCode from '../zipcode/ZipCode'
import PopularProducts from '../popularProducts/PopularProducts'
import Spinner from '../Spinner'
import HeroBanner from '../commonComponents/heroBanner/HeroBanner'
import MuiAlert from '@material-ui/lab/Alert'
import { isIE } from 'react-device-detect'
import { trackEvent, EventNames } from '../../appinsights/EventTrack'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import './Home.scss'

class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      clickedBtn: '',
      dailogOpen: false,
      minOrder:
        !!this.props.minPrice &&
        Object.keys(this.props.minPrice.minimumOrderAmount).length !== 0
          ? this.props.minPrice.minimumOrderAmount
          : sessionStorage.getItem('minimumOrderAmount') === undefined ||
            sessionStorage.getItem('minimumOrderAmount') === null
          ? ''
          : sessionStorage.getItem('minimumOrderAmount'),
      isHomeContentLoading: false
    }
  }

  zipValidation = (e) => {
    trackEvent(
      EventNames.Action.ACTION_GET_STARTED,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    )
    e.preventDefault()
    let zipcodeVerified = sessionStorage.getItem('Zipcode')
    if (zipcodeVerified) {
      this.props.history.push('/productlist')
    } else {
      this.setState({ dailogOpen: true, clickedBtn: 'getStarted' })
    }
  }

  hideDialog() {
    this.setState({ dailogOpen: false })
  }

  async UNSAFE_componentWillMount() {
    this.setState({
      isHomeContentLoading: true
    })
    await this.props.actionGetCustomer()
    let customerDetails = this.props.customerDetails
    if (!!customerDetails && !sessionStorage.getItem('Zipcode')) {
      let primaryZipCode = customerDetails.shippingZipcode || ''
      let locationId = customerDetails.locationId || ''

      if (!!primaryZipCode && !!locationId) {
        sessionStorage.setItem('Zipcode', primaryZipCode)
        sessionStorage.setItem('_lo_No', locationId)
      }
    }

    setTimeout(() => {
      if (!sessionStorage.getItem('minimumOrderAmount')) {
        this.actionGetMinAmount()
      } else {
        this.setState({
          isHomeContentLoading: false
        })
      }
    }, 1500)
    let loginClicked = localStorage.getItem('loginClicked')
    if (loginClicked === 'checkout') {
      localStorage.removeItem('loginClicked')
    }
    if (!localStorage.getItem('accessToken')) {
      document.getElementById('body').classList.add('is-home')
    }
  }

  componentDidMount() {
    if (isIE) {
      document.getElementById('body').classList.add('has-toast')
    }
  }

  componentWillUnmount() {
    document.getElementById('body').classList.remove('is-home')
    document.getElementById('body').classList.remove('has-toast')
  }

  actionGetMinAmount = () => {
    try {
      this.props.actionGetMinAmount().then(() => {
        const { minPrice } = this.props
        if (!!minPrice && Object.keys(minPrice).length !== 0) {
          this.setState({
            minOrder: minPrice.minimumOrderAmount,
            isHomeContentLoading: false
          })
          sessionStorage.setItem(
            'minimumOrderAmount',
            minPrice.minimumOrderAmount || null
          )
          sessionStorage.setItem(
            'shippingCharges',
            minPrice.shippingCharges || null
          )
        } else {
          this.setState({
            isHomeContentLoading: false
          })
        }
      })
    } catch (error) {
      this.setState({
        isHomeContentLoading: false
      })
      console.error('someThing Went wrong while fetching min order value')
    }
  }

  render() {
    const { customerDetails } = this.props
    let minimumOrderAmount = sessionStorage.getItem('minimumOrderAmount')
      ? sessionStorage.getItem('minimumOrderAmount')
      : this.state.minOrder

    if (this.state.isHomeContentLoading) {
      return <Spinner />
    }

    if (localStorage.getItem('accessToken')) {
      return (
        <>
          <Snackbar
            open={true}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            className="snackbar snackbar-full IE-only"
          >
            <MuiAlert variant="filled" severity="error">
              Our site works best in all modern bowsers, except Internet
              Explorer.
            </MuiAlert>
          </Snackbar>
          <div className="homepage-authenticated-user">
            <HeroBanner customerDetails={customerDetails} />
            <PopularProducts customerDetails={customerDetails} />
          </div>
        </>
      )
    }

    return (
      <React.Fragment>
        <Snackbar
          open={true}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          className="snackbar snackbar-full IE-only"
        >
          <MuiAlert variant="filled" severity="error">
            Our site works best in all modern bowsers, except Internet Explorer.
          </MuiAlert>
        </Snackbar>
        <div className="home-wrapper">
          <div className="hero">
            <Grid
              container
              spacing={0}
              alignItems="center"
              className="hero-banner"
            >
              <Grid item xs={6}>
                <NavLink
                  to="/productlist"
                  aria-label="shop Lays"
                  onClick={this.zipValidation}
                >
                  <img
                    className="hero-background"
                    src="./images/Lays-banner.png"
                    alt="shop Lays"
                    role="presentation"
                  />
                </NavLink>
              </Grid>
              <Grid item xs={6}>
                <NavLink
                  to="/productlist"
                  aria-label="shop Doritos"
                  onClick={this.zipValidation}
                >
                  <img
                    className="hero-background"
                    src="./images/Doritos-banner.png"
                    alt="shop Doritos"
                    role="presentation"
                  />
                </NavLink>
              </Grid>
              <Grid item xs={12}>
                <a
                  id="scrollToContent"
                  className="scroll-to"
                  aria-label="scroll to content"
                  href="#content"
                >
                  <ExpandMoreIcon />
                </a>
              </Grid>
            </Grid>
          </div>
          <div id="content">
            <Container className="container-root">
              <div className="hero-text">
                <Typography variant="h1" className="hero-header">
                  your favorite snacks are here
                </Typography>
                <p className="hero-content">
                  Welcome to your one stop shop for all your Frito-Lay favs.
                  Personalize orders, sample the hottest new flavors, and more.
                  Plus free shipping on orders over $15! Its fun, easy, and
                  tasty.
                </p>
              </div>
              <div className="home-row-container">
                <Grid
                  container
                  spacing={1}
                  alignItems="center"
                  className="home-row"
                >
                  <Grid item xs={7}>
                    <div className="home-text">
                      <div className="home-title">shop by category</div>
                      <div className="home-content">
                        Looking for a specific flavor? Got a movie night or
                        cookout coming up? Well take you step by step to find
                        the right snacks for what you need, then save your order
                        so next time everything will be ready to go. No fuss,
                        just fun!
                      </div>
                      <NavLink
                        to="/productlist"
                        id="getStarted"
                        className="home-link"
                        onClick={this.zipValidation}
                        aria-label="start shop by category"
                      >
                        get started
                      </NavLink>
                    </div>
                  </Grid>
                  <Grid item xs={5}>
                    <img
                      src="./images/shop-by-category.png"
                      alt="shop by category"
                    />
                  </Grid>
                </Grid>
                <Grid
                  container
                  spacing={1}
                  alignItems="center"
                  className="home-row"
                >
                  <Grid item xs={7}>
                    <div className="home-text">
                      <div className="home-title">explore all snacks</div>
                      <div className="home-content">
                        Just jump in. All your favs (even the hard-to-find ones)
                        are waiting. And who knows, maybe you'll find some new
                        favs while you're at it.
                      </div>
                      <NavLink
                        to="/productlist"
                        id="getStarted"
                        className="home-link"
                        onClick={this.zipValidation}
                        aria-label="shop now"
                      >
                        shop now
                      </NavLink>
                    </div>
                  </Grid>
                  <Grid item xs={5}>
                    <img
                      src="./images/shop-by-category.png"
                      alt="explore all snacks"
                    />
                  </Grid>
                </Grid>
              </div>
              <div className="brought-to-you">
                <div>
                  <img
                    className="hero-background"
                    src="https://share.snacks.com/images/logo.png"
                    alt="shop Lays"
                    role="presentation"
                  />
                </div>
                <div className="text">Brought to you by Frito-Lay and Quaker</div>
                <div>
                  <img
                    className="hero-background"
                    src="./images/quaker-logo.png"
                    alt="shop Lays"
                    role="presentation"
                  />
                </div>
              </div>
            </Container>
          </div>
        </div>

        <ZipCode
          onClose={() => this.hideDialog()}
          dailogOpen={this.state.dailogOpen}
          clickedBtn={this.state.clickedBtn}
        />
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    minPrice: state.reducer.minAmount,
    customerDetails: state.reducer.customerDetails,
    zipResponse: state.reducer.zipResponse
  }
}
export default connect(mapStateToProps, {
  actionGetMinAmount,
  actionGetCustomer,
  actionZipcodeVerification
})(Home)
