import React from 'react';
import APIUtil from '../../config/APIUtil';
import UrlConstants from '../../config/UrlConstants';
import Spinner from '../Spinner';
import CustomEvent from 'custom-event';
// import { setInterval } from "timers";
class Payment extends React.Component {
	instance;
	constructor(props) {
		document.title = 'Payment | ' + UrlConstants.titleTag;
		super(props);
		this.state = {
			payload: '',
			intervalID: 0,
			accountId: 2,
			url: UrlConstants.PaymentStatus,
			APIUtil: APIUtil,
			spinner: true,
		};
	}

	/**
	 *
	 * @description For  setting and getting the client token via props on page load.
	 * @memberof Payment
	 */
	componentWillMount() {
		setTimeout(() => {
			this.setState({ spinner: false });
		}, 3000);
	}
	componentDidMount() {
		var that = this;
		window.document.addEventListener('paymentResponse', function (e) {
			var intervalID = setInterval(() => {
				that.getPaymentResponse(that);
			}, 2000);
			that.setState({ intervalID: intervalID });
		});
	}
	componentWillUnmount() {
		clearInterval(this.state.intervalID);
		this.setState({ intervalID: 0 });
	}
	getPaymentResponse(that) {
		if (that.state.intervalID <= 0) {
			return false;
		}
		if (sessionStorage.getItem('orderNumber')) {
			let data = { orderNumber: sessionStorage.getItem('orderNumber') };
			return that.state.APIUtil.postMethod(that.state.url, data, true)
				.then((response) => {
					let data = response.data.data;
					if (data.cybersourceResponse) {
						if (data.cyberSourcePaymentComplete) {
							clearInterval(that.state.intervalID);
							sessionStorage.setItem('_sc_re', JSON.stringify(data));
							sessionStorage.removeItem('total');
							sessionStorage.removeItem('orderNumber');
							sessionStorage.removeItem('selectedOrder');
							sessionStorage.removeItem('cartItems');
							sessionStorage.removeItem('subtotal');
							sessionStorage.removeItem('tax');
							sessionStorage.removeItem('isthankyou');
							sessionStorage.removeItem('regData');
							sessionStorage.removeItem('formValues');
							sessionStorage.removeItem('formChangeValues');
							sessionStorage.removeItem('formAddValues');
							
							sessionStorage.removeItem('chk_tabIndex');	
							sessionStorage.removeItem('billingValues');
							sessionStorage.removeItem('errorMSG');
							sessionStorage.removeItem('shippingCharge');
							sessionStorage.removeItem('billingSameAsShipping');
							sessionStorage.removeItem('billingInvalid');
							sessionStorage.removeItem('shippingInvalid');
							sessionStorage.removeItem('checkoutMode')

							localStorage.removeItem('accountId');
							localStorage.removeItem('shippingId');
							localStorage.removeItem('billingId');
							localStorage.removeItem('contactId');
							sessionStorage.removeItem('selectedShippingAddress')

							var event = new CustomEvent('cartitem');
							document.dispatchEvent(event);
							window.top.location.href = UrlConstants.SiteUrl + '/thankyou';
						} else {
							clearInterval(that.state.intervalID);
							sessionStorage.removeItem('orderNumber');
							sessionStorage.removeItem('isthankyou');
							sessionStorage.setItem('errorPaymentMsg', data.cybersourceResponseMsg);
							window.top.location.reload();
						}
					}
				})
				.catch((response) => {
					sessionStorage.setItem('errorMsg', UrlConstants.errorMsg);
					window.top.location.reload();
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.spinner ? <Spinner /> : ''}
				{/* <iframe width="" height="" src={UrlConstants.SiteUrl + "/cybersource"}
                    frameBorder='0' 
                    className="iframestyle" 
                    sandbox="allow-top-navigation allow-scripts allow-forms allow-same-origin"
                    allowFullScreen
          title='payment'/> */}
				<iframe
					width=""
					height=""
					src={UrlConstants.SiteUrl + '/cybersource'}
					frameBorder="0"
					className="iframeembedded"
					sandbox="allow-top-navigation allow-scripts allow-forms allow-same-origin"
					allowFullScreen
					title="payment"
				/>
			</div>
		);
	}
}

export default Payment;
