import React, { Component } from "react";
import {
  Button,
  FormControl,
  Card,
  Grid,
  InputLabel,
  OutlinedInput,
  Typography,
} from "@material-ui/core";
import { ErrorMessage, Formik, Form } from "formik";
import * as Yup from "yup";
import UrlConstants from "config/UrlConstants";
import APIUtil from "config/APIUtil";
import ReactSelect from "react-select";
import usStateData from "../../data/states_list.json";
import AddressSuggestionDialog from "./AddressSuggestionDialog";
import AutoCompleteSelect from "../../commonComponents/autocompletetext/AutoCompleteSelect";

class EditBilling extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ZipfailMessage: null,
      submitDisabled: true,
      initialValues: null,
    };
  }
  componentDidMount() {
    this.setState({ initialValues: this.props.customerDetails });
  }
  getRequest(values) {
    var billingZip = !!values.billingZip ? values.billingZip : null;
    var billingState = !!values.billingState ? values.billingState : null;
    var billingCity = !!values.billingCity ? values.billingCity : null;
    var billingCountry = UrlConstants.defaultCountry;
    var billingAddressline1 = !!values.billingAddressline1
      ? values.billingAddressline1
      : "";
    var billingAddressline2 = !!values.billingAddressline2
      ? values.billingAddressline2
      : "";
    //Destination Data
    let addObj = {};
    addObj["postalArea"] = billingZip;
    addObj["stateOrProvinceName"] = billingState;
    //Address Data
    let addressData = {};
    addressData["Region"] =
      (!!billingCity ? billingCity : "") +
      (!!billingState ? ", " + billingState : "") +
      (!!billingZip ? ", " + billingZip : "");
    addressData["CountryCode"] = billingCountry;
    addressData["AddressLine"] = [
      billingAddressline1 + " " + billingAddressline2,
    ];
    // Tax post data Object
    let objData = {};
    objData["destination"] = addObj;
    objData["isTaxFalse"] = true;
    objData["isBillingAddress"] = true;
    objData["isAddress"] = true;
    objData["Address"] = addressData;
    objData["documentDate"] = new Date();
    objData["transactionType"] = "SALE";
    return objData;
  }

  hideDialog() {
    this.setState({
      ["AddressSuggestionDialog"]: null,
    });
  }

  cancelDialog() {
    this.setState({
      ["AddressSuggestionDialog"]: null,
      submitDisabled: true,
    });
  }

  addressValidate(values) {
    if (!!values.billingZip && values.billingZip.length == "5") {
      var objData = this.getRequest(values);
      let url = UrlConstants.orderTax;
      this.props.isLoading(true);
      APIUtil.postMethod(url, objData, true)
        .then((response) => {
          if (
            response.data.success === true &&
            response.data.zipSuccess === true
          ) {
            if (
              response.data.addressSuccess === false &&
              response.data.candidateFound === false
            ) {
              this.props.isLoading(false);
              this.setState({
                submitDisabled: true,
                ZipfailMessage: response.data.addressErrorMessage,
              });
              return false;
            }
            if (!response.data.candidateFound) {
              this.setState({ submitDisabled: false, ZipfailMessage: "" });
              this.props.isLoading(false);
              return false;
            } else {
              this.setState({ submitDisabled: false });
              if (!!response.data.candidateFound) {
                this.props.isLoading(false);
                var addrArray =
                  !!response.data.data &&
                  !!response.data.data.address &&
                  !!response.data.data.address.data &&
                  !!response.data.data.address.data.XAVResponse
                    ? response.data.data.address.data.XAVResponse.Candidate
                    : [];
                var finalAdata = addrArray;
                if (!Array.isArray(addrArray)) {
                  finalAdata = [addrArray];
                }
                this.setState({
                  ["AddressSuggestionDialog"]: (
                    <AddressSuggestionDialog
                      onSubmit={(values) =>
                        this.setState({ initialValues: values }, () =>
                          this.hideDialog()
                        )
                      }
                      isAddress="billing"
                      initialValues={values}
                      manageSuggestedAddress={() =>
                        this.manageSuggestedAddress()
                      }
                      hideDialog={() => this.hideDialog()}
                      cancelDialog={() => this.cancelDialog()}
                      addressVerifyData={finalAdata}
                    />
                  ),
                });
                return false;
              }
            }
          } else {
            setTimeout(() => {
              this.props.isLoading(false);
            }, 100);
            this.setState({
              submitDisabled: true,
              ZipfailMessage: response.data.zipErrorMessage,
            });
          }
        })
        .catch((response) => {
          this.setState({
            ZipfailMessage: UrlConstants.errorMsg,
            submitDisabled: true,
          });
        });
    } else {
      this.setState({ submitDisabled: true });
    }
  }

  render() {
    const addressPattern = /\bP(ost|ostal)?([ \.]*(O|0)(ffice)?)?([ \.]*Box)\b/i;
    const zipRegex = /^[0-9\b]+$/;
    const alphaNumericRegix = /^[a-zA-Z0-9 '.#,/'-]*$/;
    return (
      <div className="profile-info">
        {/* Contact info */}
        {!!this.state.initialValues ? (
          <Formik
            //key={this.state.initialValues.shipping.length}
            enableReinitialize
            initialValues={this.state.initialValues}
            validationSchema={Yup.object().shape({
              billingFirstname: Yup.string()
                .trim()
                .required("First Name is required.")
                .min(2, "Name must be at least 2 characters.")
                .test(
                  "billingFirstname",
                  "First Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.& ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),

              billingLastname: Yup.string()
                .trim()
                .min(2, "Name must be at least 2 characters.")
                .required("Last Name is required.")
                .test(
                  "billingLastname",
                  "Last Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.& ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              billingAddressline1: Yup.string()
                .trim()
                .required("Address is Required."),
              // .test("billingAddressline1",
              //   UrlConstants.poBoxErrorMsg.replace(/##ADDTYPE##/g, function () { return 'billing'}),
              //   function (value) {
              //     if (addressPattern.test(value)) {
              //       return false;
              //     }
              //     return true;
              //   }
              // ),
              billingAddressline2: Yup.string().nullable().notRequired(),
              // .test("billingAddressline2",
              //   UrlConstants.poBoxErrorMsg.replace(/##ADDTYPE##/g, function () { return 'billing'}),
              //   function (value) {
              //     if (addressPattern.test(value)) {
              //       return false;
              //     }
              //     return true;
              // }),
              billingZip: Yup.string()
                .trim()
                .required("Zip is required")
                .matches(/^[0-9]{5}$/, "Zip code must be exactly 5 digits."),
              billingState: Yup.string().trim().required("State is required."),
              billingCity: Yup.string().trim().required("City is required."),
            })}
            onSubmit={(values, { resetForm }) => {
              this.props.handleSubmit(values);
            }}
          >
            {({
              resetForm,
              setFieldValue,
              setFieldTouched,
              errors,
              values,
              touched,
            }) => (
              <Form
                id="startSubscriptionForm"
                name="form1"
                autoComplete="startSubscriptionForm"
              >
                <Grid container spacing={6} justify="space-between">
                  <Grid item xs={12} sm={12}>
                    <Grid
                      container
                      spacing={2}
                      alignItems="center"
                      className="title-mini"
                    >
                      <Grid item xs container>
                        <Typography variant="h2" component="h2">
                          Billing Address
                        </Typography>
                      </Grid>
                    </Grid>
                    <label
                      htmlFor="prevent_autofill_billing"
                      className="heading-hidden"
                    >
                      Auto Fill
                    </label>
                    <input
                      type="text"
                      name="prevent_autofill_billing"
                      readOnly
                      id="prevent_autofill_billing"
                      value=""
                      style={{ display: "none" }}
                    />
                    {/* Contact info */}
                    <Card className="profile-card profile-card-form">
                      <Grid
                        container
                        spacing={6}
                        className="form-card-container"
                      >
                        <Grid item xs={12} sm={12}>
                          <span style={{ color: "red" }}>
                            {this.state.ZipfailMessage}
                          </span>
                        </Grid>
                        {/* First Name */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingFirstname"
                            >
                              First Name<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingFirstname"
                              id="billingFirstname"
                              name="billingFirstname"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.billingFirstname}
                              onChange={(e) =>
                                setFieldValue(
                                  `billingFirstname`,
                                  e.target.value
                                )
                              }
                              onBlur={(e) => {
                                setFieldTouched(`billingFirstname`, true);
                              }}
                              error={Boolean(
                                touched.billingFirstname &&
                                  errors.billingFirstname
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingFirstname`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>
                        {/* Last Name */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingLastname"
                            >
                              Last Name<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingLastname"
                              id="billingLastname"
                              name="billingLastname"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.billingLastname}
                              onChange={(e) =>
                                setFieldValue(`billingLastname`, e.target.value)
                              }
                              onBlur={(e) => {
                                setFieldTouched(`billingLastname`, true);
                              }}
                              error={Boolean(
                                touched.billingLastname &&
                                  errors.billingLastname
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingLastname`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>
                        {/* Address */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingAddressline1"
                            >
                              Address<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingAddressline1"
                              id="billingAddressline1"
                              name="billingAddressline1"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.billingAddressline1}
                              onChange={(e) => {
                                if (alphaNumericRegix.test(e.target.value)) {
                                  setFieldValue(
                                    `billingAddressline1`,
                                    e.target.value
                                  );
                                }
                              }}
                              onBlur={(e) => {
                                setFieldTouched(`billingAddressline1`, true);
                                if (
                                  !!values.billingAddressline1 &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZip
                                ) {
                                  this.addressValidate(values);
                                }
                              }}
                              error={Boolean(
                                touched.billingAddressline1 &&
                                  errors.billingAddressline1
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingAddressline1`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>

                        {/* Address 2 */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingAddressline2"
                            >
                              Address 2 (optional)
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingAddressline2"
                              id="billingAddressline2"
                              name="billingAddressline2"
                              onChange={(e) => {
                                if (alphaNumericRegix.test(e.target.value)) {
                                  setFieldValue(
                                    `billingAddressline2`,
                                    e.target.value
                                  );
                                }
                              }}
                              onBlur={() => {
                                setFieldTouched(`billingAddressline2`, true);
                                if (
                                  !!values.billingAddressline1 &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZip
                                ) {
                                  this.addressValidate(values);
                                }
                              }}
                              value={values.billingAddressline2}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingAddressline2`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>

                        {/* Email */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingCity"
                            >
                              City<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingCity"
                              id="billingCity"
                              name="billingCity"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              onChange={(e) => {
                                if (alphaNumericRegix.test(e.target.value)) {
                                  setFieldValue(`billingCity`, e.target.value);
                                  this.addressValidate(values);
                                }
                              }}
                              onBlur={(e) => {
                                setFieldTouched(`billingCity`, true);
                                if (
                                  !!values.billingAddressline1 &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZip
                                ) {
                                  this.addressValidate(values);
                                }
                              }}
                              error={Boolean(
                                touched.billingCity && errors.billingCity
                              )}
                              value={values.billingCity}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingCity`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>

                        {/* Phone */}
                        <Grid item xs={12} sm={4}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <label htmlFor="billingState">
                              <AutoCompleteSelect
                                id="billingState"
                                label="State"
                                touched={touched.billingState}
                                options={usStateData}
                                defaultValue={usStateData.find(
                                  (option) =>
                                    option.value === values.billingState
                                )}
                                value={usStateData.find(
                                  (option) =>
                                    option.value === values.billingState
                                )}
                                onChange={(e, value) => {
                                  if (value !== null) {
                                    setFieldValue("billingState", value.value);
                                  }
                                }}
                                onBlur={(e, value) => {
                                  setFieldTouched(`billingState`, true);
                                  if (
                                    !!values.billingAddressline1 &&
                                    !!values.billingState &&
                                    !!values.billingCity &&
                                    !!values.billingZip
                                  ) {
                                    this.addressValidate(values);
                                  }
                                }}
                                error={Boolean(
                                  touched.billingState && errors.billingState
                                )}
                              />
                              {/* <ReactSelect
                                id="billingState"
                                value={usStateData.find(
                                  (option) =>
                                    option.value === values.billingState
                                )}
                                onChange={(e) => {
                                  setFieldValue(`billingState`, e.value);
                                }}
                                onBlur={(e) => {
                                  setFieldTouched(`billingState`, true);
                                  if (
                                    !!values.billingAddressline1 &&
                                    !!values.billingState &&
                                    !!values.billingCity &&
                                    !!values.billingZip
                                  ) {
                                    this.addressValidate(values);
                                  }
                                }}
                                options={usStateData}
                                className="react-select-theme react-select-big required"
                                placeholder="State"
                                error={Boolean(
                                  touched.billingState && errors.billingState
                                )}
                                aria-labelledby="billingState"
                              /> */}
                            </label>
                            <ErrorMessage
                              component="div"
                              name={`billingState`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>

                        {/* Zip Code */}
                        <Grid item xs={12} sm={2}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="billingZip"
                            >
                              Zip Code<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="billingZip"
                              name={`billingZip`}
                              id="billingZip"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              onChange={(event) => {
                                if (
                                  zipRegex.test(event.target.value) ||
                                  event.target.value === ""
                                ) {
                                  if (event.target.value.length <= 5) {
                                    setFieldValue(
                                      `billingZip`,
                                      event.target.value
                                    );
                                  }
                                }
                              }}
                              onBlur={(e) => {
                                setFieldTouched(`billingZip`, true);
                                if (
                                  !!values.billingAddressline1 &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZip
                                ) {
                                  this.addressValidate(values);
                                }
                              }}
                              error={Boolean(
                                touched.billingZip && errors.billingZip
                              )}
                              value={values.billingZip}
                            />
                            <ErrorMessage
                              component="div"
                              name={`billingZip`}
                              className="text-danger"
                            />
                          </FormControl>
                        </Grid>
                      </Grid>
                    </Card>
                    <Grid item xs={12} sm={12}>
                      <Grid item>
                        <div className="text-right">
                          <Button
                            type="button"
                            onClick={() => this.props.setBilling(true)}
                            variant="contained"
                            color="primary"
                            className="btn-mui btn-outlined btn-sm"
                            id="cancelBilling"
                            aria-label="Cancel Billing Address"
                          >
                            {" "}
                            Cancel{" "}
                          </Button>
                          <Button
                            disabled={this.state.submitDisabled}
                            type="submit"
                            variant="contained"
                            color="primary"
                            className="btn-mui btn-blue btn-sm"
                            id="saveBilling"
                            aria-label="Save Billing Address"
                          >
                            {" "}
                            Save{" "}
                          </Button>
                        </div>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            )}
          </Formik>
        ) : (
          ""
        )}
        {this.state["AddressSuggestionDialog"]}
      </div>
    );
  }
}
export default EditBilling;
