import React, { Component } from 'react'
import { Grid, Button } from '@material-ui/core';
import EditCustomer from './EditCustomer';

export default class ViewCustomer extends Component {

    onEditContact = () => {
        let accountId = this.props.userInfo.id;
        let contactId;
        if (this.props.userInfo.accountContacts.length > 0) {
            contactId = this.props.userInfo.accountContacts[0].id;
        }
        return <EditCustomer
        fromRegister={this.props.fromRegister}
            intialAddress={this.props.intialAddress}
            cancelEditCustomer={() => this.props.updateEditContact()}
            saveCustomer={(values) => {
               
                this.props.createCustomer(values)
                setTimeout(() => {
                    if (this.props.userInfo
                        && this.props.userInfo.accountContacts
                        && this.props.userInfo.accountContacts.length > 0) {
                        this.props.updateEditContact()
                        let accountContact = this.props.userInfo.accountContacts[0]
                        var intialAddressNew = {
                            ...this.props.intialAddress,
                            firstname: accountContact.firstName,
                            lastname: accountContact.lastName,
                            contact: accountContact.phone
                        }
                        this.props.updateIntialAddress(intialAddressNew)
                    }
                }, 4000)
            }
            }
            accountId={accountId}
            contactId={contactId}
        />
    }

    render() {
        return (
            <div className="checkout-content-body" >
                
                <div className="checkout-content-body-inner">
                    <div className="form-card">
                        <Grid item xs={12} sm={12} className="userinfo-wrapper userinfo-wrapper-head">
                            <Grid item xs container>
                                <h3>Contact Information</h3>
                            </Grid>
                            {!this.props.editContact &&
                                <Grid item>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        className={'btn-link'}
                                        id="editShipping"
                                        aria-label="edit Contact Information"
                                        onClick={() => this.props.updateEditFields()}
                                    >
                                        Edit
                                    </Button>
                                </Grid>
                            }
                        </Grid>
                        <Grid container spacing={0} className="form-card-container">
                            {this.props.editContact ? (
                                this.onEditContact()
                            ) : (
                                    <>
                                        {/** First Name */}
                                        <Grid item xs={12} sm={12} className="userinfo-wrapper">
                                            <div className="key">Name:</div>
                                            <div className="val">
                                                {this.props.intialAddress.firstname + ' ' + this.props.intialAddress.lastname}
                                            </div>
                                        </Grid>
                                        <Grid item xs={12} sm={12} className="userinfo-wrapper">
                                            <div className="key">Email:</div>
                                            <div className="val">{this.props.intialAddress.email}</div>
                                        </Grid>
                                        {/* <Grid item xs={12} sm={12} className="userinfo-wrapper">
                                            <div className="key">Phone:</div>
                                            <div className="val">{this.props.intialAddress.contact}</div>
                                        </Grid> */}
                                    </>
                                )}
                        </Grid>
                    </div>
                </div>
            </div>
        )
    }
}