import {
  Button,
  Card,
  CardActions,
  CardContent,
  Container,
  Dialog,
  DialogContent,
  Grid,
  Typography,
} from "@material-ui/core";
import React, { Component } from "react";
import BottomScrollListener from "react-bottom-scroll-listener";
import { connect } from "react-redux";
import {
  actionCheckProductAvailability,
  actionGetMinAmount,
  actiongetProduct,
} from "../../../actions/ProductAction";
import UrlConstants from "../../../config/UrlConstants";
import SearchFilter from "../../commonComponents/searchFilter/SearchFilter";
import ProductDetail from "../../getStarted/ProductDetail";
import Spinner from "../../Spinner";
import CustomEvent from "custom-event";
import CircularProgress from "@material-ui/core/CircularProgress";
import ProductTout from "../../ProductTout/ProductTout";
import SnacksAlertMessage from "../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import DialogBox from "../../commonComponents/dialogBox/DialogBox";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import { DISPLAY_HEADING } from "./../../../config/constants/content.constant";
import "./productList.scss";

class ProductList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      counter: 0,
      totalQty: 0,
      products: undefined,
      showSearchBar: false,
      offset: 0,
      page: 0,
      DialougeBoxOpen: false,
      selectedItems: [],
      selectedItemsOpen: false,
      showProductDetail: false,
      minOrder: "",
      currency: "",
      limit: UrlConstants.productsLimit,
      isfilter: false,
      Spinner: false,
      msg: null,
      productScroll: false,
      isProduct: true,
      productInventory: [],
      totalProducts: null,
      productDetailIndex: "",
      filterData: {},
      productInfo: [],
    };
  }

  /**
   * @description updating and calling the api on page load.
   * @memberof ProductList
   */
  componentWillMount() {
    this.actionGetMinAmount();
    this.getFilteredData();
  }

  getFilteredData = () => {
    const selectedCategory = sessionStorage.getItem("selectedCategory")
      ? JSON.parse(sessionStorage.getItem("selectedCategory"))
      : [];
    let payload = {
      limit: this.state.limit,
      locationNumber: sessionStorage.getItem("_lo_No"),
    };
    selectedCategory.map((item) => {
      const { categoryId, subCategoryIdList } = item;
      payload = {
        ...payload,
        [categoryId]: subCategoryIdList,
      };
    });
    if (selectedCategory && selectedCategory.length > 0) {
      this.setState(
        {
          filterData: payload,
        },
        () => this.getProductList(this.state.filterData, true)
      );
    } else {
      this.getProductList(this.state.filterData, true);
    }
  };

  /**
   * @description For loading the data from the categories api.
   * @memberof ProductList
   */
  componentDidMount() {
    window.scrollTo(0, 0);
    this.setState({ showSearchBar: false });
    setTimeout(() => {
      let productGridContainer = document.getElementById(
        "productGridContainer"
      );
      if (!!productGridContainer && !!document.getElementById("mixCount")) {
        window.onscroll = function () {
          myFunction();
        };
        let header = document.getElementById("mixCount");
        let sticky = 280;
        function myFunction() {
          if (window.pageYOffset > sticky) {
            let top = window.pageYOffset - sticky;
            let dh = productGridContainer.clientHeight - top;
            if (dh > 470) {
              header.style.cssText = `top: ${top}px`;
            }
          } else {
            header.style.cssText = "";
          }
        }
      }
    }, 1000);
  }

  /**
   * @description function for lazzyloading
   * @memberof ProductList
   */
  handleOnDocumentBottom() {
    if (this.state.isscroll) {
      let offset = this.state.offset + 1;
      this.setState(
        { offset: offset, isscroll: false, productScroll: true },
        () => {
          this.getProductList(this.state.filterData, false);
        }
      );
    }
  }

  /**
   *
   * @description to get the product list from the api.
   * @param {*} postData : keeping the product.
   * @param {*} isItemUpdate : to check updated item(quantity) with respect to the location
   * @memberof ProductList
   */
  getProductList(postData, isItemUpdate) {
    var objData = !!postData ? postData : {};
    objData["offset"] = this.state.offset * this.state.limit;
    objData["limit"] = this.state.limit;
    objData["locationNumber"] = !!sessionStorage.getItem("_lo_No")
      ? sessionStorage.getItem("_lo_No")
      : null;
    let prodData = !!this.state.products ? this.state.products : [];
    if (
      this.state.isProduct &&
      (this.state.totalProducts == 0 ||
        prodData.length != this.state.totalProducts)
    ) {
      if (!this.state.productScroll) {
        this.setState({ Spinner: true });
      }
      setTimeout(() => {
        this.manageProducts(objData, isItemUpdate);
      }, 1000);
    } else {
      this.setState({ productScroll: false });
    }
  }

  /**
   *
   * @description function used for managing the product with respect to the location.
   * @param {*} objData : for keeping the item/product available.
   * @param {*} isItemUpdate : to check updated item(quantity) with respect to the location
   * @memberof ProductList
   */
  manageProducts(objData, isItemUpdate) {
    let data =
      JSON.parse(sessionStorage.getItem("cartItems")) !== null
        ? JSON.parse(sessionStorage.getItem("cartItems")).length > 0
          ? [...JSON.parse(sessionStorage.getItem("cartItems"))]
          : []
        : [];
    const productsData =
      !!this.state.products && this.state.products.length > 0
        ? this.state.products
        : [];
    this.props
      .actiongetProduct(objData)
      .then((productData) => {
        let products = productData.Groups || [];
        if (products.length > 0) {
          products.map((e) => {
            let objData = data.find((o) => o.id === e.id);
            e.quantity = !!objData ? objData.quantity : 0;
            productsData.push(e);
            return true;
          });

          this.setState({
            isscroll: true,
            Spinner: false,
            productScroll: false,
            products: productsData,
            totalProducts: productData.totalProducts || 0,
            currency: products.length > 0 ? products[0].currency || "$" : "",
            showSearchBar: true,
            selectedItems: isItemUpdate ? data : this.state.selectedItems,
          });
        } else {
          this.setState({
            isscroll: true,
            isProduct: false,
            Spinner: false,
            productScroll: false,
            products: [],
            totalProducts: 0,
            currency: "",
            showSearchBar: true,
            selectedItems: isItemUpdate ? data : this.state.selectedItems,
          });
        }
      })
      .catch((productData) => {
        this.setState({
          Spinner: false,
          productScroll: false,
          DialougeBoxOpen: true,
        });
      });
  }

  /**
   * @description for getting the min maount/ limit that is to considered during shopping.
   * @memberof ProductList
   */
  actionGetMinAmount() {
    const { minPrice } = this.props;
    if (
      sessionStorage.getItem("minimumOrderAmount") === undefined ||
      sessionStorage.getItem("minimumOrderAmount") === null
    ) {
      this.props.actionGetMinAmount("", "", "", "").then(() => {
        this.setState({
          minOrder: minPrice ? minPrice.minimumOrderAmount : "",
        });
        if (minPrice && Object.keys(minPrice).length > 0) {
          sessionStorage.setItem(
            "minimumOrderAmount",
            minPrice.minimumOrderAmount || null
          );
          sessionStorage.setItem(
            "shippingCharges",
            minPrice.shippingCharges || null
          );
        }
      });
    } else {
      this.setState({
        minOrder: sessionStorage.getItem("minimumOrderAmount") || "",
      });
    }
  }

  /**
   * @description to update the cart data/item count.
   * @memberof ProductList
   */
  updateCartData() {
    let cart = this.state.selectedItems;
    sessionStorage.setItem("cartItems", JSON.stringify(cart));
    //fire custom event to change cart item count
    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
  }

  /**
   * @description update counter of selected products
   * @memberof ProductList
   */
  updateCounter() {
    let cnt = 0;
    let selectedItems = this.state.selectedItems;
    for (var i in selectedItems) {
      cnt = !!selectedItems[i] ? cnt + selectedItems[i].quantity : cnt;
    }
    this.setState({ counter: cnt });
  }
  /**
   * @description add selected products to cart
   * @memberof ProductList
   */
  addItemsToCart() {
    if (sessionStorage.getItem("cartItems") !== null) {
      trackEvent(
        EventNames.Action.ACTION_ADD_TO_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { cart: JSON.parse(sessionStorage.getItem("cartItems")) }
      );
    }
    this.props.moveToNextStep(0);
  }

  /**
   * @description for the side card where the added product list is shown.
   * @returns
   * @memberof ProductList
   */
  renderSelectedProductsCard = () => {
    let min = this.state.minOrder;
    let products = [];
    let selectedItems = this.state.selectedItems;
    let dataID =
      JSON.parse(sessionStorage.getItem("cartItems")) !== null
        ? JSON.parse(sessionStorage.getItem("cartItems")).length > 0
          ? [...JSON.parse(sessionStorage.getItem("cartItems"))]
          : []
        : [];
    let total = 0;
    if (Object.keys(selectedItems).length !== 0) {
      Object.keys(selectedItems).map((items) => {
        total =
          total + selectedItems[items].price * selectedItems[items].quantity;
        return true;
      });
    }

    for (var i in selectedItems) {
      products.unshift(
        <Grid container spacing={1} key={"selected-prod-" + i}>
          <Grid item xs={12} sm container>
            <span className="mix-count-product-text">
              {selectedItems[0].productDescription
                ? selectedItems[0].productDescription
                : selectedItems[0].shortDescription
                    .replace(/(<([^>]+)>)/gi, "")
                    .replace("&nbsp;", " ")}
            </span>
          </Grid>
          <Grid item>
            <span className="mix-count-product-text quantity">
              {selectedItems[i].quantity}
            </span>
          </Grid>
        </Grid>
      );
    }

    return (
      <>
        {/* product total in header */}
        {total > 0 ? (
          <Button
            variant="contained"
            color="primary"
            fullWidth
            className="btn-mui btn-yellow cart-total-header"
            id="CartTotalHeader"
            aria-label="Cart Total Header"
            disabled={total >= min ? false : true}
            onClick={() => {
              this.addItemsToCart();
            }}
          >
            <span className="currency">{this.state.currency}</span>
            <span>{total.toFixed(2)}</span>
          </Button>
        ) : (
          ""
        )}
        <Card
          id="mixCount"
          className={total >= min ? "mix-count active" : "mix-count"}
        >
          <div className="mix-count-wrapper">
            <CardContent className="mix-count-content">
              <div
                className="mix-count-title"
                onClick={() => {
                  this.openSelectedItems();
                }}
              >
                <Typography variant="h3" className="text-center">
                  Your Order
                </Typography>
              </div>
              <div
                className={`mix-count-content-inner ${
                  this.state.selectedItemsOpen ? "open" : ""
                }`}
              >
                {products}
              </div>
              <div className="mix-count-total">
                <div className="mix-count-total-text text-center">Total:</div>
                <div className="mix-count-total-price text-center">
                  <span className="currency">{this.state.currency}</span>
                  <span>{total.toFixed(2)}</span>
                </div>
              </div>
              <div className="mix-count-minimum">
                <p
                  className="text-center"
                  onClick={() => {
                    this.openSelectedItems();
                  }}
                >
                  {this.state.minOrder
                    ? this.state.currency + this.state.minOrder
                    : "0"}{" "}
                  Minimum Order Requirement
                </p>
              </div>
            </CardContent>
          </div>
          <CardActions className="mix-count-action">
            <a
              className="btn-mui btn-yellow"
              id="viewCart"
              tabIndex={0}
              aria-label="view Cart"
              disabled={total >= min ? false : true}
              onClick={() => {
                this.addItemsToCart();
              }}
            >
              View Cart
              {total > 0 ? (
                <i className="total-inline">
                  {this.state.currency}
                  {total.toFixed(2)}
                </i>
              ) : (
                <i className="total-inline">$0.00</i>
              )}
            </a>
            <a
              disabled={!!dataID && dataID.length > 0 ? false : true}
              className="btn-mui btn-red"
              onClick={() => this.clearCartConfirmation()}
              tabIndex={0}
              id="clearCart"
              aria-label="Clear Cart"
            >
              Clear Cart
            </a>
          </CardActions>
        </Card>
      </>
    );
  };

  /**
   * @description for removing the item form the cart all at once from storage.
   * @memberof ProductList
   */
  removeToCart() {
    trackEvent(
      EventNames.Action.ACTION_CLEAR_CART,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    );
    const oldData =
      !!this.state.products && this.state.products.length > 0
        ? this.state.products
        : [];
    if (oldData.length > 0) {
      oldData.forEach((e) => {
        e.quantity = 0;
      });
    }
    sessionStorage.removeItem("cartItems");
    sessionStorage.removeItem("total");
    sessionStorage.removeItem("selectedOrder");
    sessionStorage.removeItem("subtotal");
    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
    this.setState({
      ["cartconfirmation"]: null,
      products: oldData,
      selectedItems: [],
    });
  }

  /**
   * @description for opening the pop up screen.
   * @memberof ProductList
   */
  openSelectedItems() {
    this.setState({ selectedItemsOpen: !this.state.selectedItemsOpen });
  }

  /**
   * @description To show product detail popup
   * @memberof ProductLibrary
   */
  productDetail = (productInfo, index) => {
    this.setState({
      showProductDetail: true,
      productInfo: productInfo,
      productDetailIndex: index,
    });
  };

  /**
   * @description to clear the cart item data all at once on button press from cart page.
   * @memberof ProductList
   */
  clearCartConfirmation() {
    this.setState({
      ["cartconfirmation"]: (
        <DialogBox
          dialogClose={() => this.setState({ ["cartconfirmation"]: null })}
          dialogOpen={true}
          dialogHeading={
            "Are you sure you would like to remove all items from the cart?"
          }
          dialogWarning
          dialogCloseConfirm={() => this.removeToCart()}
          ariaLabelYes={"Yes I want to empty my cart"}
          ariaLabelCancel={"No I don't want to empty my cart"}
        />
      ),
    });
  }

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  closeAndUpdate = (filteredCartItems) => {
    this.setState({
      showProductDetail: false,
    });
    this.selectedCartItems(filteredCartItems);
  };

  selectedFilter = (filterValue, filterName) => {
    let filteringData = [];
    if (this.state.filterData[filterName]) {
      filteringData = [...this.state.filterData[filterName], filterValue];
    } else {
      filteringData = [filterValue];
    }

    this.setState(
      {
        filterData: {
          ...this.state.filterData,
          [filterName]: filteringData,
        },
      },
      () => this.manageProducts(this.state.filterData, true)
    );
  };

  removedFilter = (filter) => {
    let filteringData = [];
    const filterName = Object.keys(this.state.filterData).find((key) => {
      if (typeof this.state.filterData[key] === "object")
        return this.state.filterData[key].includes(filter);
    });

    const field = this.state.filterData[filterName];
    const findIndex = field.findIndex((obj) => obj === filter);
    if (field) {
      filteringData = field.filter((item) => field[findIndex] !== item);
    }
    this.setState(
      {
        filterData: {
          ...this.state.filterData,
          [filterName]: filteringData,
        },
      },
      () => this.manageProducts(this.state.filterData, true)
    );
  };

  render() {
    const { products } = this.state;

    return (
      <div className="page-product-listing">
        <SearchFilter
          filterMainHeading={DISPLAY_HEADING.PRODUCT_LIST_TITLE}
          selectedFilter={this.selectedFilter}
          removedFilter={this.removedFilter}
        />
        <Container className="container-root">
          <BottomScrollListener
            offset="950"
            debounce="200"
            onBottom={() => this.handleOnDocumentBottom()}
          />
          {this.state.Spinner ? <Spinner /> : ""}

          {this.state["cartconfirmation"]}
          {this.state.msg && (
            <SnacksAlertMessage fullWidth msg={this.state.msg} />
          )}
          <Grid
            container
            spacing={3}
            id="productGridContainer"
            className="product-grid-container lists"
          >
            {products && products.length > 0 ? (
              products.map((prod, index) => {
                return (
                  <React.Fragment key={`product-${prod.productId}-${index}`}>
                    {prod ? (
                      <Grid item key={prod.id} xs={6} lg={3} sm={3}>
                        <ProductTout
                          productKey={`${prod.productId}-${index}`}
                          productInfo={prod}
                          index={index}
                          selectedItems={this.selectedCartItems}
                          detailedInfo={this.productDetail}
                          warningMsgFunc={this.warningMsgFunc}
                        />
                      </Grid>
                    ) : (
                      <Grid
                        container
                        justify="center"
                        className="noproducts-grid"
                      >
                        <Grid item>
                          <h1 className="noproducts-text">No Products</h1>
                        </Grid>
                      </Grid>
                    )}
                  </React.Fragment>
                );
              })
            ) : (
              <Grid container justify="center" className="noproducts-grid">
                <Grid item>
                  {this.state.products === undefined || this.state.Spinner ? (
                    <Spinner />
                  ) : (
                    <h1 className="noproducts-text">No Products</h1>
                  )}
                </Grid>
              </Grid>
            )}

            {/* {/ mix count card /}	 */}
            {this.renderSelectedProductsCard()}
          </Grid>

          {this.state.productScroll && (
            <Grid container spacing={0}>
              <Grid
                item
                xs={12}
                sm
                container
                justify="center"
                className="loader-inline"
              >
                <CircularProgress disableShrink />
              </Grid>
            </Grid>
          )}

          <Dialog
            open={this.state.DialougeBoxOpen}
            onClose={() => {
              this.setState({ DialougeBoxOpen: false });
            }}
            aria-labelledby="responsive-dialog-title"
            maxWidth={false}
            classes={{
              paper: "modal-container",
              root: "modal-stack-top",
            }}
          >
            <DialogContent>
              <div style={{ textAlign: "center" }}>
                <h2>Something went Wrong</h2>
              </div>
            </DialogContent>
            <Button
              id="errorCatchList"
              aria-label="error Catch List"
              onClick={() => {
                this.setState({ DialougeBoxOpen: false });
              }}
            >
              Close
            </Button>
          </Dialog>

          {/* product detail */}
          <Dialog
            open={this.state.showProductDetail}
            onClose={() => {
              this.setState({ showProductDetail: false });
            }}
            maxWidth={false}
            scroll={"body"}
            classes={{
              paper: "modal-big modal-product-detail modal-paper",
              root: "modal-stack-top",
            }}
          >
            <DialogContent>
              <Button
                className="icn-close"
                id="productDetailClose"
                aria-label="productDetail Close"
                onClick={() => {
                  this.setState({ showProductDetail: false });
                }}
              >
                <i className="fa fa-times"></i>
              </Button>
              <ProductDetail
                productData={this.state.productInfo}
                items={this.state.selectedItems}
                warningMsgFunc={this.warningMsgFunc}
                selectedItems={this.selectedCartItems}
                closeAndUpdate={this.closeAndUpdate}
              />
            </DialogContent>
          </Dialog>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    products: state.reducer.Products || [],
    totalProduct: state.reducer.totalProduct,
    filterProducts: state.reducer.filterProducts,
    minPrice: state.reducer.minAmount,
    productInventory: state.reducer.productInventory,
  };
};

export default connect(mapStateToProps, {
  actiongetProduct,
  actionCheckProductAvailability,
  actionGetMinAmount,
})(ProductList);
