import React, { Component } from "react";
import {
  TextField,
  Container,
  InputAdornment,
  Grid,
  Button,
  Typography,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ClearIcon from "@material-ui/icons/Clear";
import APIUtil from "./../../../config/APIUtil";
import UrlConstants from "./../../../config/UrlConstants";
import "./searchFilter.scss";

export default class SearchFilter extends Component {
  state = {
    isFilterLoading: true,
    filterList: [],
    toggleFilterList: false,
    isSelected: [],
  };

  componentDidMount() {
    this.getFilterList();
    this.checkExistingFilters();
  }

  checkExistingFilters = () => {
    const selectedCategory = sessionStorage.getItem("selectedCategory")
      ? JSON.parse(sessionStorage.getItem("selectedCategory"))
      : [];
    let existingFilters = [];
    if (selectedCategory.length > 0) {
      selectedCategory.map((item) => {
        const { subCategoryIdList } = item;

        subCategoryIdList &&
          (existingFilters = [
            ...new Set([...existingFilters, ...subCategoryIdList]),
          ]);
      });
    }

    this.setState({ isSelected: existingFilters });
  };

  getFilterList = () => {
    const payload = {
      locationNumber: sessionStorage.getItem("_lo_No") || null,
    };

    APIUtil.postMethod(UrlConstants.FilterProducts, payload, true)
      .then((response) =>
        this.setState({
          isFilterLoading: false,
          filterList: response.data.data,
        })
      )
      .catch((error) => {
        console.error("Search Filter Api", error);
      });
  };

  selectFilter = (item, heading) => {
    const { selectedFilter } = this.props;
    const { isSelected } = this.state;
    const index = isSelected.indexOf(item);
    if (index === -1) {
      this.setState({
        isSelected: [...new Set([...this.state.isSelected, item])],
      });
    } else if (index > -1) {
      isSelected.splice(index, 1);
      this.setState({ isSelected });
    }
    selectedFilter(item, heading);
  };

  removeFilter = (filter) => {
    const { removedFilter } = this.props;
    const { isSelected } = this.state;
    const index = isSelected.indexOf(filter);
    if (index > -1) {
      isSelected.splice(index, 1);
      this.setState({ isSelected });
    }
    console.log("remove", isSelected, filter);
    removedFilter(filter);
  };

  renderFilters = (filter, heading, mainIndex) => {
    const { isSelected } = this.state;
    return (
      <div className={`filters ${heading}`} key={mainIndex}>
        <h4 className="filter-heading">{heading}</h4>
        <ul className="filter-list">
          {filter.map((item, index) => (
            <li
              className={`${isSelected.includes(item) ? "selected" : ""}`}
              key={`${item}-${index}`}
            >
              <Button
                onClick={() =>
                  isSelected.includes(item)
                    ? this.removeFilter(item)
                    : this.selectFilter(item, heading)
                }
                aria-label={`${item}`}
              >
                {item}
              </Button>
            </li>
          ))}
        </ul>
      </div>
    );
  };

  render() {
    const { filterMainHeading } = this.props;
    const { filterList, toggleFilterList, isSelected } = this.state;
    const filterKey = Object.keys(filterList);
    const filterValue = Object.values(filterList);

    return (
      <div className="search-filter">
        <Container className="container-root">
          {filterMainHeading && (
            <Grid item xs={12}>
              <Typography
                variant="h1"
                className="page-heading"
                aria-label={filterMainHeading}
              >
                {filterMainHeading}
              </Typography>
            </Grid>
          )}
          <Grid item container spacing={3} className="product-filter-group">
            <Grid item xs={12} lg={4} sm={4}>
              <TextField
                className="search-bar"
                autoFocus
                fullWidth
                onChange={() => console.log("onChange")}
                placeholder="search"
                type="search"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} lg={1} sm={2} className="filter">
              <Button
                id="filters"
                className="filter-dropdown"
                aria-controls="simple-menu"
                aria-haspopup="true"
                onClick={() =>
                  this.setState({
                    toggleFilterList: !this.state.toggleFilterList,
                  })
                }
                aria-label="click here to open filter dropdown"
              >
                filters {!toggleFilterList && <ExpandMoreIcon />}
                {toggleFilterList && <ExpandLessIcon />}
              </Button>
            </Grid>

            <Grid item xs={12} lg={7} sm={6} className="selected-filters">
              {isSelected.map((filter) => (
                <Button key={filter} aria-label={`${filter}`}>
                  {filter}
                  <ClearIcon
                    tabIndex={0}
                    onClick={() => this.removeFilter(filter)}
                    style={{ fontSize: 16 }}
                  />
                </Button>
              ))}
              {isSelected && isSelected.length > 1 && (
                <Button
                  className="filter-clear-all"
                  aria-label={`Clear all selected filters`}
                  onClick={() =>
                    this.setState({
                      isSelected: [],
                      toggleFilterList: false,
                    })
                  }
                >
                  Clear All
                </Button>
              )}
            </Grid>
          </Grid>
          {toggleFilterList && (
            <Grid container spacing={3}>
              {filterKey.map((item, index) =>
                this.renderFilters(filterValue[index], item, `${item}-${index}`)
              )}
            </Grid>
          )}
        </Container>
      </div>
    );
  }
}
