import {
    Button,
    Snackbar,
    FormControl,
    Grid,
    InputLabel,
    OutlinedInput,
    Select,
} from '@material-ui/core';
import { ErrorMessage, Formik } from 'formik';
import React from 'react';
import { withRouter } from 'react-router-dom';
import * as Yup from 'yup';
import MuiAlert from '@material-ui/lab/Alert';

class EditCustomer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isDataFilled: false,
            firstname: '',
            lastname: '',
            intialAddress: this.props.intialAddress
                ? this.props.intialAddress
                : {

                    firstname: JSON.parse(sessionStorage.getItem('formContactValues'))
                        ? JSON.parse(sessionStorage.getItem('formContactValues')).firstname
                        : '',
                    lastname: JSON.parse(sessionStorage.getItem('formContactValues'))
                        ? JSON.parse(sessionStorage.getItem('formContactValues')).lastname
                        : '',
                    contact: JSON.parse(sessionStorage.getItem('formContactValues'))
                        ? JSON.parse(sessionStorage.getItem('formContactValues')).contact
                        : '',
                    email: JSON.parse(sessionStorage.getItem('formContactValues'))
                        ? JSON.parse(sessionStorage.getItem('formContactValues')).email
                        : '',

                }
        };
        this.nameFieldValidation.bind(this);
    }

    componentWillUnmount() {
        document.getElementById('body').classList.remove('has-toast');
    }

    onContactChange = (e, setFieldValue) => {
        if (e.target.value.toString().length <= 10) {
            setFieldValue('contact', e.target.value);
        }
    };

    handleClose = () => {
        this.setState({ ZipfailMessage: '' });
        document.getElementById('body').classList.remove('has-toast');
    };

    nameFieldValidation = (e, setFieldValue) => {
        // handleBlur(e);

        this.setState({ [e.target.id]: '' });
        var re = /^[a-zA-Z '.]*$/;
        if (!re.test(e.target.value)) {
            if (
                e.target.id === 'firstname'
            ) {
                const eventValue = e.target.id;
                this.setState({ [e.target.id]: 'First Name must be only alphabets.' }, () => {
                    setTimeout(() => {
                        this.setState({ [eventValue]: null }, () => { });
                    }, 3000);
                });
            } else {
                const eventValue = e.target.id;
                this.setState({ [e.target.id]: 'Last Name must be only alphabets.' }, () => {
                    setTimeout(() => {
                        this.setState({ [eventValue]: null }, () => { });
                    }, 3000);
                });
            }
            if (e.target.value === '') {
                setFieldValue([e.target.id], e.target.value);
                this.setState({ [e.target.id]: '' });
            }
        } else if (e.target.value || e.target.value === '') {
            setFieldValue([e.target.id], e.target.value);
        }
    };

    render() {
        var schemaValidate = Yup.object().shape({

            firstname: Yup.string().trim().required('First Name is required.').min(2, 'Name must be at least 2 characters.'),
            lastname: Yup.string().trim().required('Last Name is required.').min(2, 'Name must be at least 2 characters.'),
            // contact: Yup.string().trim()
            //     .nullable()
            //     .required('Phone number is required.')
            //     .test('contact', 'Please enter a valid phone number.', function (value) {
            //         if (value === null || value === '') {
            //             return true; // As it is not required.
            //         }
            //         var regexp = /((\([0-9]{3}\))|[0-9]{3})[\s-]?[\0-9]{3}[\s-]?[0-9]{4}$/;
            //         if (!regexp.test(value)) {
            //             return false;
            //         }
            //         return true;
            //     })
            //     .min(10, 'Phone Number must be 10 digits')
            //     .max(10, 'Phone Number must be 10 digits'),
        });
        return (
            <div>
                <Formik
                    key={this.state.addrKey}
                    initialValues={this.state.intialAddress}
                    enableReinitialize={true}
                    validationSchema={schemaValidate}
                    onSubmit={(fields) => {
                        sessionStorage.setItem('formContactValues', JSON.stringify(fields));
                        localStorage.setItem('accountId', this.props.accountId);
                        if (this.props.contactId != null) {
                            localStorage.setItem('contactId', this.props.contactId);
                        }
                        this.props.saveCustomer(fields)
                    }}
                >
                    {(props) => {
                        const {
                            values,
                            touched,
                            errors,
                            handleChange,
                            handleBlur,
                            handleSubmit,
                            setFieldValue,
                            setFieldTouched,
                            isValid,
                        } = props;
                        return (
                            <form
                                id="startSubscriptionForm"
                                name="form1"
                                onSubmit={handleSubmit}
                                onChange={sessionStorage.setItem('formValues', JSON.stringify(values))}
                            >
                                <div className={this.props.fromRegister ? "full-view" : "checkout-content-body-inner full-view"}>
                                    {/* zipcode valication */}
                                    {this.state.ZipfailMessage ? (
                                        <Snackbar
                                            open={true}
                                            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                                            className="snackbar snackbar-full"
                                        >
                                            <MuiAlert variant="filled" onClose={this.handleClose} severity="error">
                                                {this.state.ZipfailMessage}
                                            </MuiAlert>
                                        </Snackbar>
                                    ) : (
                                            ''
                                        )}

                                    {/** Contact information */}
                                    {this.props.editShipping === undefined && (
                                        <fieldset className="form-card">

                                            <Grid container spacing={2} className="form-card-container">
                                                {/** First Name */}
                                                <Grid item xs={12} sm={6}>
                                                    <FormControl fullWidth className="form-group" variant="outlined">
                                                        <InputLabel className="form-label" htmlFor="firstname">
                                                            First Name
															<span className="text-danger">*</span>
                                                        </InputLabel>
                                                        <OutlinedInput
                                                            id="firstname"
                                                            name="firstname"
                                                            inputProps={{ "aria-required": "true",role: "alert","aria-invalid":"false"}}
                                                            value={values.firstname}
                                                            // onChange={handleChange}
                                                            onChange={(e) => {
                                                                this.nameFieldValidation(e, setFieldValue);
                                                            }}
                                                            onBlur={handleBlur}
                                                            error={
                                                                (this.state.isDataFilled === true || touched.firstname) &&
                                                                Boolean(errors.firstname)
                                                            }
                                                        />

                                                        {(this.state.isDataFilled === true || touched.firstname) && errors.firstname ? (
                                                            <div className="text-danger">{errors.firstname}</div>
                                                        ) : (
                                                                <ErrorMessage
                                                                    component="div"
                                                                    name="firstname"
                                                                    className="text-danger"
                                                                />
                                                            )}
                                                    </FormControl>
                                                </Grid>
                                                {/** Last Name */}
                                                <Grid item xs={12} sm={6}>
                                                    <FormControl fullWidth className="form-group" variant="outlined">
                                                        <InputLabel className="form-label" htmlFor="lastname">
                                                            Last Name <span className="text-danger">*</span>
                                                        </InputLabel>
                                                        <OutlinedInput
                                                            id="lastname"
                                                            name="lastname"
                                                            inputProps={{ "aria-required": "true",role: "alert","aria-invalid":"false"}}
                                                            value={values.lastname}
                                                            // onChange={handleChange}
                                                            onChange={(e) => this.nameFieldValidation(e, setFieldValue)}

                                                            onBlur={handleBlur}
                                                            error={
                                                                (this.state.isDataFilled === true || touched.lastname) &&
                                                                Boolean(errors.lastname)
                                                            }
                                                        />
                                                        {(this.state.isDataFilled === true || touched.lastname) && errors.lastname ? (
                                                            <div className="text-danger">{errors.lastname}</div>
                                                        ) : (
                                                                <ErrorMessage
                                                                    component="div"
                                                                    name="lastname"
                                                                    className="text-danger"
                                                                />
                                                            )}
                                                    </FormControl>
                                                </Grid>

                                                {/** Phone Number */}
                                                {/* <Grid item xs={12} sm={12}>
                                                    <Grid container spacing={0}>
                                                        <Grid item>
                                                            <FormControl className="form-group" variant="outlined">
                                                                <InputLabel
                                                                    className="form-label phone-select-label"
                                                                    htmlFor="countryCode"
                                                                >
                                                                    <span>code</span>
                                                                </InputLabel>
                                                                <Select
                                                                    native
                                                                    className="phone-select"
                                                                    id="countryCode"
                                                                >
                                                                    <option value="1">+1</option>
                                                                </Select>
                                                            </FormControl>
                                                        </Grid>
                                                        <Grid item sm xs container>
                                                            <FormControl
                                                                fullWidth
                                                                className="form-group"
                                                                variant="outlined"
                                                            >
                                                                <InputLabel className="form-label" htmlFor="contact">
                                                                    Phone Number
																	<span className="text-danger">*</span>
                                                                </InputLabel>
                                                                <OutlinedInput
                                                                    className="phone-input"
                                                                    id="contact"
                                                                    name="contact"
                                                                    inputProps={{ "aria-required": "true",role: "alert","aria-invalid":"false"}}
                                                                    value={values.contact}
                                                                    onChange={(e) =>
                                                                        this.onContactChange(e, setFieldValue)
                                                                    }
                                                                    onBlur={handleBlur}
                                                                    error={
                                                                        (this.state.isDataFilled === true || touched.contact) &&
                                                                        Boolean(errors.contact)
                                                                    }
                                                                />
                                                                {(this.state.isDataFilled === true || touched.contact) && errors.contact ? (
                                                                    <div className="text-danger">{errors.contact}</div>
                                                                ) : (
                                                                        <ErrorMessage
                                                                            component="div"
                                                                            name="contact"
                                                                            className="text-danger"
                                                                        />
                                                                    )}
                                                            </FormControl>
                                                        </Grid>
                                                    </Grid>
                                                </Grid> */}
                                            </Grid>
                                        </fieldset>
                                    )}

                                    <div className="button-group text-right">
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            className={' btn-mui btn-red'}
                                            id="cancelEditCustomer"
                                            aria-label="cancel Edit Customer"
                                            onClick={() => {
                                                this.props.cancelEditCustomer()
                                            }}
                                        >
                                            Cancel
										</Button>

                                        <Button
                                            type="submit"
                                            disabled={
                                                !isValid ||
                                                values.firstname.trim().length < 2 ||
                                                values.lastname.trim().length < 2 
                                                //values.contact.length !== 10
                                            }
                                            variant="contained"
                                            color="primary"
                                            className="btn-mui btn-blue"
                                            id="saveEditCustomer"
                                            aria-label="save Edit Customer"
                                        >
                                            Save
										</Button>
                                    </div>
                                </div>
                            </form>
                        );
                    }}
                </Formik>

            </div>
        );
    }
}
export default (withRouter(EditCustomer));
