import React, { Component } from 'react';
import UrlConstants from "config/UrlConstants";
import APIUtil from "config/APIUtil";
import OktaApiUtil from '../../config/OktaApiUtil';
import {
    trackEvent,
    EventNames
} from '../../appinsights/EventTrack';

//Common  component for all the components in settings page
class MasterDataCommon extends Component {
    constructor(props) {
        let locationSearch = false;
        if(props.location.search != ''){
         locationSearch = true;
        }
        else{
         locationSearch = false;
        }
        super(props);
        this.state = {
            accountID: null,
            isViewContact: true,
            isViewBilling: true,
            initialValues: null,
            primaryAddressData: null,
            customerDetails: null,
            isSpinner: false,
            tabIndex: locationSearch ? 1 : 0,
            viewProfile: true,
            msg: null,
            defaultCountry: UrlConstants.defaultCountry,
            isMultiShipDisabled: false,
            msgType: 'success',
            customerContact: {
                customerfirstname: "",
                customerlastname: "",
                customeremail: "",
                customerPhone1: ""
            },
            customerBilling: {},
            customerShipping: [],
            orderHistoryDetails:[],
            shippingValidateMessage: '',
            origin: '',
            pathname: ''
        }
    }

    
    getAddressLastName(str) {
        if (!!str) {
			var n = str.trim().split(" "); //split by space
			
			if (n.length > 1) {
				n.shift()
			}
            return n.join(" ");  //Get last element
		}
		return ""
    }

    getAddressFirstName(str) {
        if (!!str) {
			var res = str.trim().split(" ");  //split by space
			if (res.length >= 1) {
				return res[0];
			}
        }
        return "";
    }

    // { ==== Get Profile Details ====} //
    getCustomerDetails(indexData) {
        let url = UrlConstants.GetCustomer;
        this.setState({ isSpinner: true });
        APIUtil.getMethod(url, true).then(async (response) => {
            this.setState({ isSpinner: false });
            response = response.data;
            if (response.success) {
                const customerDetails = response.data;
                if (Object.keys(customerDetails).length > 0) {
                    let custbillingAddress = [];
                    var custShippingAddress = [];
                    customerDetails.accountAddresses && customerDetails.accountAddresses.forEach(e => {
                        if (e.addressType === "BILLING_ADDRESS") {
                            custbillingAddress = e;
                        } else {
                            custShippingAddress.push(e);
                        }
                    })
                    var customer = customerDetails.accountContacts && customerDetails.accountContacts[0];
                    // Make a Object of Customer contact (which is used same as in edit api)
                    var custObj = {};
                    custObj['accountId'] = customerDetails.id;
                    custObj['id'] = (!!customer.id ? customer.id : null);
                    custObj['customerfirstname'] = (!!customer.firstName ? customer.firstName : "");
                    custObj['customerlastname'] = (!!customer.lastName ? customer.lastName : "");
                    custObj['customeremail'] = (!!customer.email ? customer.email : "");
                    custObj['customerPhone1'] = (!!customer.phone ? customer.phone : "");

                    // Make a Object of Billing Address
                    var billingObj = {};
                    billingObj['id'] = (!!custbillingAddress.id ? custbillingAddress.id : "");
                    billingObj['billingSameAsShipping'] = (custbillingAddress.isPrimary ? true : false);
                    billingObj['billingFirstname'] = (!!custbillingAddress.name ? this.getAddressFirstName(custbillingAddress.name) : "");
                    billingObj['billingLastname'] = (!!custbillingAddress.name ? this.getAddressLastName(custbillingAddress.name) : "");
                    billingObj['billingAddressline1'] = (!!custbillingAddress.addressLine1 ? custbillingAddress.addressLine1 : "");
                    billingObj['billingAddressline2'] = (!!custbillingAddress.addressLine2 ? custbillingAddress.addressLine2 : "");
                    billingObj['billingZip'] = (!!custbillingAddress.zip ? custbillingAddress.zip : "");
                    billingObj['billingCountry'] = this.state.defaultCountry;
                    billingObj['billingState'] = (!!custbillingAddress.state ? custbillingAddress.state : "");
                    billingObj['billingCity'] = (!!custbillingAddress.city ? custbillingAddress.city : "");
                    billingObj['effectiveDt'] = (!!custbillingAddress.effectiveDt ? custbillingAddress.effectiveDt : "");                    

                    // Make a Array of Shipping Address 
                    var shippingArray = [];
                    custShippingAddress.length > 0 && custShippingAddress.forEach((e, index) => {
                        var viewship = true;
                        if (!!this.state.customerShipping[index] && index != indexData) {
                            viewship = this.state.customerShipping[index].isViewShipping;
                        }
                        var shipObj = {};
                        shipObj['id'] = (!!e.id ? e.id : "");
                        shipObj['shippingFirstname'] = (!!e.name ? this.getAddressFirstName(e.name) : "");
                        shipObj['shippingLastname'] = (!!e.name ? this.getAddressLastName(e.name) : "");
                        shipObj['shippingAddressline1'] = (!!e.addressLine1 ? e.addressLine1 : "");
                        shipObj['shippingAddressline2'] = (!!e.addressLine2 ? e.addressLine2 : "");
                        shipObj['shippingZip'] = (!!e.zip ? e.zip : "");
                        shipObj['shippingState'] = (!!e.state ? e.state : "");
                        shipObj['shippingCountry'] = this.state.defaultCountry;
                        shipObj['shippingCity'] = (!!e.city ? e.city : "");
                        shipObj['effectiveDt'] = (!!e.effectiveDt ? e.effectiveDt : "");
                        shipObj['isPrimary'] = e.primaryAddr ? true : false;
                        shipObj['isViewShipping'] = viewship;
                        shippingArray.push(shipObj);
                    });
                    this.setState({
                        accountID: customerDetails.id,
                        customerContact: custObj,
                        customerBilling: billingObj,
                        customerShipping: shippingArray,
                        customerDetails: customerDetails
                    });
                } else {
                    let errorMsg = !!response.message ? response.message : UrlConstants.errorMsg;
                    this.setMsg(errorMsg, "error");
                }
            } else if (!!response.message && response.message === 'Expired token') {
                await OktaApiUtil.renewToken(response1 => {
                    if (response1 === null) {
                        let errorMsg = !!response.message ? response.message : UrlConstants.errorMsg;
                        this.setMsg(errorMsg, "error");
                    } else {
                        this.getCustomerDetails();
                    }
                }).catch(err => {
                    let errorMsg = !!response.message ? response.message : UrlConstants.errorMsg;
                    this.setMsg(errorMsg, "error");
                })
            } else {
                let errorMsg = !!response.message ? response.message : UrlConstants.errorMsg;
                this.setMsg(errorMsg, "error");
            }
        }).catch((data) => {
            let errorMsg = UrlConstants.errorMsg;
            this.setMsg(errorMsg, "error");
            this.setState({ isSpinner: false });
        });
    }

    // { ==== Profile Shipping Address Submission ====} //
    handleShippingSubmit(values, indexData) {
        this.setState({ isSpinner: true });
        var shipObj = {
            customer: { id: this.state.accountID },
            shipping: values
        }
        if ('id' in values) {
            // Update Shipping address API call
            trackEvent(EventNames.Action.ACTION_UPDATE_SHIPPING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { shippingData: shipObj })
            APIUtil.putMethod(UrlConstants.updateAddress, shipObj, true).then(response => {
                this.setState({ isSpinner: false });
                if (!!response.data && !!response.data.success) {
                    this.getCustomerDetails(indexData);
                    this.setMsg(response.data.message, 'success');
                } else {
                    this.setMsg(UrlConstants.errorMsg, 'error');
                }
            }).catch((data) => {
                this.setState({ isSpinner: false });
                let errorMsg = UrlConstants.errorMsg;
                this.setMsg(errorMsg, "error");
                this.setState({ isSpinner: false });
            });
        } else {
            // Add Shipping address API call
            trackEvent(EventNames.Action.ACTION_ADD_SHIPPING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { shippingData: shipObj })
            APIUtil.postMethod(UrlConstants.CreateAddress, shipObj, true).then(response => {
                this.setState({ isSpinner: false });
                if (!!response.data && !!response.data.success) {
                    this.getCustomerDetails(indexData);
                    this.setState({ isMultiShipDisabled: false });
                    this.setMsg(response.data.message, 'success');
                } else {
                    this.setMsg(UrlConstants.errorMsg, 'error');
                }
            }).catch((data) => {
                let errorMsg = UrlConstants.errorMsg;
                this.setMsg(errorMsg, "error");
                this.setState({ isSpinner: false });
            });
        }

    }

    // { ==== Profile Billing Address Submission ====} //
     // { ==== Profile Billing Address Submission ====} //
     handleBillingSubmit(values,isCustomerBillingExist) {
        this.setState({ isSpinner: true });
        var billObj = {
            customer: { id: this.state.accountID },
            billing: values
        }
        
        if (isCustomerBillingExist.billingFirstname === "") {
             this.createBillingAddress(billObj);
			
		} else {
            this.updateBillingAddress(billObj);
		}
      
    }

    // Method to create billing address

    createBillingAddress(billObj) {
        trackEvent(EventNames.Action.ACTION_ADD_BILLING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { billingData: billObj })
        APIUtil.postMethod(UrlConstants.CreateAddress, billObj, true).then(response => {
            //this.setState({ isSpinner: false });
            if (!!response.data && !!response.data.success) {
                this.setState({ isViewBilling: true });
                this.getCustomerDetails();
                this.setMsg(response.data.message, 'success');
            } else {
                this.setMsg(UrlConstants.errorMsg, 'error');
            }
        }).catch((err) => {
            let errorMsg = UrlConstants.errorMsg;
            this.setMsg(errorMsg, "error");
            this.setState({ isSpinner: false });
        });
    }

    // Method to update billing address
    updateBillingAddress(billObj) {
        trackEvent(EventNames.Action.ACTION_UPDATE_BILLING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { billingData: billObj })
        APIUtil.putMethod(UrlConstants.updateAddress, billObj, true).then(response => {
            this.setState({ isSpinner: false });
            if (!!response.data && !!response.data.success) {
                this.setState({ isViewBilling: true });
                this.getCustomerDetails();
                this.setMsg(response.data.message, 'success');
                var address = JSON.parse(sessionStorage.getItem('formChangeValues'))
                if (address && address !== null && !address.billingSameAsShipping) {
                    let newAdd = {
                        ...address,
                        billingFirstname: billObj.billing.billingFirstname,
                        billingLastname: billObj.billing.billingLastname,
                        billingAddressLine1: billObj.billing.billingAddressline1,
                        billingAddressLine2: billObj.billing.billingAddressline2,
                        billingCity: billObj.billing.billingCity,
                        billingState: billObj.billing.billingState,
                        billingCountry: billObj.billing.billingCountry,
                        billingZipCode: billObj.billing.billingZip
                    };
                    sessionStorage.setItem('formChangeValues', JSON.stringify(newAdd))
                }
            } else {
                this.setMsg(UrlConstants.errorMsg, 'error');
            }
        }).catch((data) => {
            alert(data)
            let errorMsg = UrlConstants.errorMsg;
            this.setMsg(errorMsg, "error");
            this.setState({ isSpinner: false });
        });
    }

    // { ==== Profile Contact Submission ====} //
    handleContactSubmit(values) {
        this.setState({ isSpinner: true });
        var contactObj = { "customer": values }
        trackEvent(EventNames.Action.ACTION_UPDATE_CUSTOMER, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, contactObj)
        APIUtil.putMethod(UrlConstants.updateAccount, contactObj, true).then(response => {
            this.setState({ isSpinner: false });
            if (!!response.data && !!response.data.success) {
                this.getCustomerDetails();
                this.setState({ isViewContact: true });
                var address = JSON.parse(sessionStorage.getItem('formChangeValues'))
                if (address && address !== null) {
                    let newAdd = {
                        ...address,
                        firstname: values.firstname,
                        lastname: values.lastname,
                    };
                    sessionStorage.setItem('formChangeValues', JSON.stringify(newAdd))
                }
                this.setMsg(response.data.message, 'success');
            } else {
                this.setMsg(UrlConstants.errorMsg, 'error');
            }
        }).catch((data) => {
            let errorMsg = UrlConstants.errorMsg;
            this.setMsg(errorMsg, "error");
            this.setState({ isSpinner: false });
        });
    }

    deleteShippingAddress(index) {
        this.setState({ isSpinner: true });
        var shipObj = { "id": this.state.customerShipping[index].id }
        trackEvent(EventNames.Action.ACTION_REMOVE_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { shippingData: shipObj })
        APIUtil.deleteMethod(UrlConstants.deleteAddress, true, shipObj).then(response => {
            this.setState({ isSpinner: false });
            if (!!response.data && !!response.data.success) {
                let addressData = this.state.customerShipping;
                addressData = addressData.filter((e, ind) => { return ind !== index });
                this.setState({ customerShipping: addressData });
                this.setMsg(response.data.message, 'success');
            } else {
                this.setMsg(UrlConstants.errorMsg, 'error');
            }
        }).catch((data) => {
            let errorMsg = UrlConstants.errorMsg;
            this.setMsg(errorMsg, "error");
            this.setState({ isSpinner: false });
        });
    }

    // { ==== Remove Profile Error Msg ====} //
    removeMsg() {
        setTimeout(() => {
            this.setState({ msg: null, msgType: 'success' });
            document.getElementById('body').classList.remove("has-toast");
        }, 4000);
    }

    // { ==== Set Profile Error Msg ====} //
    setMsg(msg, msgtype) {
        this.setState({ msg: msg, msgType: msgtype }, () => {
            document.getElementById('body').classList.add("has-toast");
            this.removeMsg();
        })
    }

   // Get Shipping Message

  getShippingMessage(){
	APIUtil.getMethod(UrlConstants.MinAmount, true).then(response => {
        let shippingValidateMessage = ''
		if (response.status === 200) {
			this.setState({shippingValidateMessage:response.data.data.shippingLimitMessage})
		} else {
			this.setState({shippingValidateMessage:''})
		}
		});
     }	
    }

export default MasterDataCommon