import {
  AppBar,
  Button,
  Container,
  Grid,
  IconButton,
  List,
  ListItem,
  Toolbar,
  Dialog,
  DialogContent,
  withStyles,
  Popover,
  MenuList,
  MenuItem,
  Drawer,
  Tooltip,
  ClickAwayListener,
} from "@material-ui/core";
import { connect } from "react-redux";
import ShoppingCart from "@material-ui/icons/ShoppingCart";
import CloseIcon from "@material-ui/icons/Close";
import MenuIcon from "@material-ui/icons/Menu";
import React, { Component } from "react";
import { NavLink, withRouter } from "react-router-dom";
import ZipCode from "../../zipcode/ZipCode";
import Login from "../../login/login.js";
import OktaApiUtil from "config/OktaApiUtil";
import {
  LogOut,
  actionGetCustomer,
  actionZipcodeVerification,
} from "../../../actions/ProductAction";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import RoomIcon from "@material-ui/icons/Room";
import APIUtil from "config/APIUtil";
import UrlConstants from "config/UrlConstants";
import SnacksAlertMessage from "./../snacksAlertMessage/SnacksAlertMessage";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import "./header.scss";

const styles = (theme) => ({
  menuButton: {
    display: "none",
    width: "auto",
    [theme.breakpoints.down("sm")]: {
      display: "block",
    },
  },
  cSelect: {
    position: "relative",
    paddingRight: "15px",
    "&:after": {
      content: '" "',
      right: "5px",
      position: "absolute",
      width: "0",
      height: "0",
      borderLeft: "5px solid transparent",
      borderRight: "5px solid transparent",
      borderTop: "5px solid rgba(0, 0, 0, 0.54)",
      fontSize: "0",
      top: "50%",
      transform: "translateY(-50%)",
      pointerEvents: "none",
    },
  },
  selectMenu: {
    appearance: "none",
    backgroundColor: "transparent",
    border: "nonMenue",
    color: Toolbar,
    withStyles: "#333",
    fontSize: "16px",
    [theme.breakpoints.down("xs")]: {
      fontSize: "14px",
    },
  },
  dropdownList: {
    display: "block",
  },
  customDropdown: {
    position: "absolute",
    border: "1px solid rgba(0, 0, 0, 0.15)",
    top: "100%",
    zIndex: "9",
    backgroundColor: "#fff",
    minWidth: "150px",
  },
  accButton: {
    position: "relative",
    paddingRight: "15px",
    "&:after": {
      content: '" "',
      right: "0px",
      position: "absolute",
      width: "0",
      height: "0",
      borderLeft: "5px solid transparent",
      borderRight: "5px solid transparent",
      borderTop: "5px solid rgba(0, 0, 0, 0.54)",
      fontSize: "0",
      top: "50%",
      transform: "translateY(-50%)",
      pointerEvents: "none",
    },
  },
  closeDrawer: {
    position: "absolute",
    top: "0",
    right: "0",
    zIndex: "9",
  },
});

export class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errorMessage: null,
      zipError: false,
      zipCode: sessionStorage.getItem("Zipcode"),
      showDrawer: false,
      clickAway: false,
      count: 0,
      clickedBtn: "",
      dailogOpen: false,
      showLoginModal: false,
      isAuthenticatedUser: this.props.isAuthenticated,
      userInfo: this.props.customerDetails,
      anchorEl: "",
      menuToggle: false,
      zipPopover: false,
      updatedZipCode: "",
    };
    // window.addEventListener("storage", (e) => console.log(e));
  }

  componentWillMount() {
    this.validateUserSession();
  }

  validateUserSession() {
    if (localStorage.getItem("accessToken") !== null) {
      const expiryTimeStamp = JSON.parse(localStorage.getItem("accessToken"))
        .expiresAt;
      const currentTimeStamp = parseInt(Date.now() / 1000);
      if (currentTimeStamp > expiryTimeStamp) {
        localStorage.clear();
        window.location.assign("/home");
      }
    }
    return null;
  }
  /**
   *
   * @description For the mobile View
   * @memberof Header
   *
   */
  toggleDrawer = () => {
    this.setState({ showDrawer: !this.state.showDrawer });
  };

  /**
   *
   * @description validation for Zipcode dialog
   * @memberof Header
   *
   */
  zipValidation = (e) => {
    trackEvent(
      EventNames.Action.ACTION_HEADER_PRODUCTS,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    );
    e.preventDefault();
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (zipcodeVerified) {
      e.target.text === "explore all snacks" &&
        this.props.history.push("/productlist");
      e.target.text === "shop by category" && this.props.history.push("/shopbycategory");
    } else {
      this.setState({ dailogOpen: true, clickedBtn: "getStarted" });
    }
  };

  /**
   *
   * @description Checking for zipcodeVerified or not and based on verification route to cart page
   * @memberof Header
   *
   */
  cartZipValidation = () => {
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (zipcodeVerified) {
      if (sessionStorage.getItem("cartItems") !== null) {
        trackEvent(
          EventNames.Action.ACTION_HEADER_ADD_TO_CART,
          EventNames.Event.EVENT_BUTTON_PRESS,
          window.location.origin,
          window.location.pathname,
          { cart: JSON.parse(sessionStorage.getItem("cartItems")) }
        );
      }
      this.props.history.push("/cart");
    } else {
      this.setState({ dailogOpen: true, clickedBtn: "cartIcon" });
    }
  };

  /**
   *
   * @description To hide the Zip dialog
   * @memberof Header
   *
   */
  hideDialog() {
    this.setState({ dailogOpen: false });
  }
  /**
   *
   * @description for getting cart items and also the calculating sum of quantities.
   * @memberof Header
   *
   */
  getCount() {
    let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
    let count = 0;
    cartItems &&
      cartItems.map((item) => {
        count = count + parseInt(item.quantity);
        return count;
      });
    this.setState({ count: count });
  }

  showLoginModal(events) {
    events.preventDefault();
    let loginClicked = localStorage.getItem("loginClicked");

    if (loginClicked === "checkout") {
      localStorage.setItem("showLogin", 1);
      var event = new CustomEvent("showLogin");
      document.dispatchEvent(event);
      if (!!document.getElementById("okta-signin-username")) {
        document.getElementById("okta-signin-username").focus();
      }
    } else {
      this.setState({ showLoginModal: true });
    }
  }

  hideUserDropdown = (event) => {
    if (event.keyCode === 27) {
      // ESC
      this.setState({ menuToggle: false });
    }
  };
  componentDidUpdate(prevProps) {
    if (
      this.props.customerDetails !== prevProps.customerDetails &&
      this.props.match.path === "/home"
    ) {
      setTimeout(() => {
        let loggedInUser = localStorage.getItem("accessToken");
        let zipcodeVerified = sessionStorage.getItem("Zipcode");
        if (
          !zipcodeVerified &&
          loggedInUser &&
          this.state.updatedZipCode === ""
        ) {
          this.setState({
            dailogOpen: true,
            spinner: false,
            clickedBtn: "getStarted",
          });
        }
      }, 2000);
    }
  }

  /**
   * @description For loading the the data & api on page load and reload.
   * @memberof Header
   */
  componentDidMount() {
    // function called for the calculation of cartcount
    this.getCount();
    var that = this;
    let token = localStorage.getItem("token");
    if (token != null && token.length > 0) {
      this.setState({
        isAuthenticated: true,
        showLoginModal: false,
      });
    }
    // event listener for the show login Dialog.
    document.addEventListener("showLoginDialog", function (e) {
      that.setState({ showLoginModal: true });
    });
    // event listener for the change in local storage
    document.addEventListener("cartitem", function (e) {
      let cartItems = sessionStorage.getItem("cartItems")
        ? JSON.parse(sessionStorage.getItem("cartItems"))
        : 0;
      let count = 0;
      if (cartItems.length > 0) {
        cartItems.map((items) => {
          count = count + parseInt(items.quantity);
          return count;
        });
        that.setState({ count: count });
      } else {
        that.setState({ count: 0 });
      }
      that.forceUpdate();
      // Rerendering the component
    });
    if (
      !this.props.customerDetails ||
      !this.props.customerDetails.accountContacts
    ) {
      this.getUserDetails();
    }

    document.addEventListener("keydown", this.hideUserDropdown);
  }

  onLoginSuccess = (response) => {
    if (!this.state.isAuthenticatedUser) {
      this.setState({ isAuthenticated: true, showLoginModal: false });
    }
    this.getUserDetails();
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    let loggedInUser = localStorage.getItem("accessToken");
    if (zipcodeVerified && loggedInUser) {
      this.props.history.push("/home");
    } else {
      this.setState({ dailogOpen: true, clickedBtn: "getStarted" });
    }
  };

  getUserDetails = () => {
    if (
      localStorage.getItem("token") !== null &&
      localStorage.getItem("accessToken") !== null
    ) {
      this.props.actionGetCustomer();
    }
  };

  userLogout = (event) => {
    if (localStorage.getItem("token") !== null) {
      event.preventDefault();
      OktaApiUtil.userTokenRevoke()
        .then((res) => {
          const apiData = res;
          // if (res === undefined) {

          if (apiData.status === 200) {
            event.preventDefault();
            this.props.LogOut(res);
            //  setTimeout(function () {
            OktaApiUtil.userLogout().then((res) => {
              sessionStorage.removeItem('taxZipCode')
              // if (res === undefined) {
              //   event.preventDefault();
              //   this.props.LogOut();
              // }
            });
            //  }, 2000);

            // setTimeout(() => {
            //   window.location.assign("/home");
            // }, 2000)
          } else if (res.message) {
            event.preventDefault();
            alert(res.message);
          }
        })
        .catch((err) => {
          console.log("Found an error", err);
        });
    }
  };

  zipPopoverOpen = (event) => {
    this.setState({ anchorEl: event.currentTarget });
    this.setState({ zipPopover: true });
  };

  zipPopoverClose = () => {
    this.setState(
      {
        zipPopover: false,
        zipError: true,
        errorMessage: "",
      },
      () => document.getElementById("body").classList.remove("has-toast")
    );
  };

  zipPopoverUpdate = () => {
    trackEvent(
      EventNames.Action.ACTION_UPDATE_ZIPCODE,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    );
    const regexp = /^[0-9\b]+$/;
    const zipcode = this.refs.updateZipCode && this.refs.updateZipCode.value;
    if (zipcode && regexp.test(zipcode) && zipcode.length === 5) {
      APIUtil.postMethod(UrlConstants.ZipValidation, { zipcode: zipcode }, true)
        .then((response) => {
          response.data.success && this.redirect(zipcode, response.data);

          !response.data.success &&
            response.data.message &&
            this.setState(
              { zipError: true, errorMessage: response.data.message },
              () => document.getElementById("body").classList.add("has-toast")
            );
        })
        .catch((error) => {
          console.error("validate ZIP Code Api Failed", error);
        });
    } else if (zipcode.length < 5) {
      this.setState(
        { zipError: true, errorMessage: "Zip code must be exactly 5 digits." },
        () => document.getElementById("body").classList.add("has-toast")
      );
    }
  };

  checkZipcode = (e) => {
    const regexp = /^[0-9\b]+$/;
    var zipcode = this.refs.updateZipCode && this.refs.updateZipCode.value;
    if (!!zipcode && !regexp.test(zipcode)) {
      this.setState(
        { zipError: false, errorMessage: "Zip Code must be a number type." },
        () => document.getElementById("body").classList.add("has-toast"),
        (this.refs.updateZipCode.value = "")
      );
    }
  };

  handleClickAway = () => {
    this.setState({ menuToggle: false });
  };

  handleClick = () => {
    this.setState({ menuToggle: !this.state.menuToggle });
  };
  redirect(zipcode, response) {
    if (response.success) {
      if (!!response.data) {
        sessionStorage.setItem("_lo_No", response.data.locationId);
      }
    }
    this.setState({ updatedZipCode: zipcode }, () => {
      sessionStorage.setItem("Zipcode", zipcode);
      window.location.reload();
    });
  }

  navigationList = () => {
    return (
      <>
        <ListItem className="menu-list-single">
          <NavLink
            className="menu-link"
            to="/home"
            aria-label="home"
            onClick={() =>
              trackEvent(
                EventNames.Action.ACTION_HEADER_HOME,
                EventNames.Event.EVENT_BUTTON_PRESS,
                window.location.origin,
                window.location.pathname
              )
            }
          >
            Home
          </NavLink>
        </ListItem>
        <ListItem className="menu-list-single">
          <NavLink
            className="menu-link"
            to="/shopbycategory"
            aria-label="shop by category"
            onClick={this.zipValidation}
          >
            shop by category
          </NavLink>
        </ListItem>
        <ListItem className="menu-list-single">
          <NavLink
            className="menu-link"
            to="/productlist"
            aria-label="products"
            onClick={this.zipValidation}
          >
            explore all snacks
          </NavLink>
        </ListItem>
      </>
    );
  };

  render() {
    let isShipto =
      window.location.pathname === "/" ||
      window.location.pathname === "/home" ||
      window.location.pathname === "/productlist" ||
      window.location.pathname === "/cart";
    const { classes, customerDetails, isAuthenticated } = this.props;
    const {
      showDrawer,
      zipPopover,
      updatedZipCode,
      errorMessage,
      showLoginModal,
      anchorEl,
      dailogOpen,
      menuToggle,
      clickedBtn,
      count,
    } = this.state;

    const profileUserName =
      customerDetails &&
      customerDetails.accountContacts &&
      customerDetails.accountContacts.length > 0
        ? customerDetails.accountContacts[0].firstName +
          " " +
          customerDetails.accountContacts[0].lastName
        : "";
    return (
      /* Header Section */

      <AppBar
        color="inherit"
        className={showDrawer === true ? "header open" : "header"}
      >
        <Toolbar className={classes.Toolbar}>
          <Container className="container-root">
            {/* <a
              className="skip-link sr-only sr-only-focusable"
              href={window.location}
            >
              Skip to content
            </a> */}
            <Grid container spacing={0} alignItems="center">
              <Grid item className="logo-wrapper">
                <IconButton
                  onClick={this.toggleDrawer}
                  className="menu-link menu-icon"
                  //aria-label="menu-link"
                  id="Menu"
                  aria-label="Menu"
                >
                  {showDrawer ? <CloseIcon /> : <MenuIcon />}
                </IconButton>
                <NavLink
                  to="/home"
                  aria-label="Frito lay Header Logo"
                  onClick={() =>
                    trackEvent(
                      EventNames.Action.ACTION_HEADER_LOGO,
                      EventNames.Event.EVENT_BUTTON_PRESS,
                      window.location.origin,
                      window.location.pathname
                    )
                  }
                >
                  <img
                    src="./images/HeaderLogo.png"
                    alt="Frito lay"
                    className="logo"
                  />
                </NavLink>
              </Grid>
              <Grid
                item
                xs
                //className="pl-0 menu-link-wrapper menu-link-web loggedin"
              >
                <nav className="pl-0 menu-link-wrapper menu-link-web loggedin">
                  <List className="menu-list hide-959">
                    {this.navigationList()}
                  </List>
                  {errorMessage && (
                    <SnacksAlertMessage fullWidth msg={errorMessage} />
                  )}
                  <List className="menu-list">
                    {sessionStorage.getItem("Zipcode") ? (
                      <ListItem className="menu-list-single">
                        <Tooltip placeholder="top" title="Location">
                          <Button
                            aria-controls="shipto-zip"
                            aria-haspopup="true"
                            className={
                              zipPopover
                                ? "btn-mui btn-outlined shipto-btn open"
                                : "btn-mui btn-outlined shipto-btn"
                            }
                            disabled={!isShipto ? true : false}
                            onClick={this.zipPopoverOpen}
                          >
                            {window.innerWidth > 767 ? (
                              <>
                                <span className="shipto-text">Ship to</span>
                                <span id="shipto-zip" className="shipto-zip">
                                  {updatedZipCode ||
                                    sessionStorage.getItem("Zipcode")}
                                </span>
                              </>
                            ) : (
                              <RoomIcon />
                            )}
                          </Button>
                        </Tooltip>
                        <Popover
                          id="shipto"
                          className="popover-zip"
                          classes={{
                            paper: "popover-zip-paper",
                          }}
                          anchorEl={anchorEl}
                          keepMounted
                          open={zipPopover}
                          onClose={this.zipPopoverClose}
                        >
                          <div>
                            <label htmlFor="zipcode" className="label-hidden">
                              change zipcode
                            </label>
                            <input
                              type="text"
                              onChange={(e) => this.checkZipcode(e)}
                              id="zipcode"
                              defaultValue={sessionStorage.getItem("Zipcode")}
                              maxLength="5"
                              ref="updateZipCode"
                            />
                          </div>
                          <div className="button-group">
                            <Button
                              className="btn-mui btn-sm btn-blue"
                              onClick={this.zipPopoverUpdate}
                            >
                              Update
                            </Button>
                            <Button
                              className="btn-mui btn-sm btn-outlined"
                              onClick={this.zipPopoverClose}
                            >
                              Cancel
                            </Button>
                          </div>
                        </Popover>
                      </ListItem>
                    ) : (
                      ""
                    )}
                    {!isAuthenticated ? (
                      <ListItem className="menu-list-single">
                        <NavLink
                          className="menu-link no-active"
                          to="/"
                          aria-label="login"
                          onClick={(event) => {
                            trackEvent(
                              EventNames.Action.ACTION_HEADER_LOGIN,
                              EventNames.Event.EVENT_BUTTON_PRESS,
                              window.location.origin,
                              window.location.pathname
                            );
                            sessionStorage.removeItem("prevUrl");
                            this.showLoginModal(event);
                          }}
                        >
                          login
                        </NavLink>
                        <ClickAwayListener onClickAway={this.handleClickAway}>
                          <div className="reg-options">
                            {customerDetails !== null && (
                              <Tooltip
                                placement="bottom"
                                title={"create account"}
                              >
                                <Button
                                  id="accountDropdown"
                                  aria-controls="simple-menu"
                                  aria-haspopup="true"
                                  onClick={this.handleClick}
                                  aria-label="click here to open dropdown"
                                >
                                  <i className="fa fa-caret-down"></i>
                                </Button>
                              </Tooltip>
                            )}
                            {menuToggle ? (
                              <MenuList>
                                <MenuItem
                                  aria-label="Create Account"
                                  onClick={() => {
                                    trackEvent(
                                      EventNames.Action
                                        .ACTION_HEADER_CREATE_ACCOUNT,
                                      EventNames.Event.EVENT_BUTTON_PRESS,
                                      window.location.origin,
                                      window.location.pathname
                                    );
                                    sessionStorage.setItem(
                                      "prevUrl",
                                      window.location.pathname
                                    );
                                    this.props.history.push("/create-account");
                                    this.setState({ menuToggle: false });
                                  }}
                                >
                                  <span>
                                    <AccountCircleIcon />
                                  </span>
                                  Create Account
                                </MenuItem>
                              </MenuList>
                            ) : null}
                          </div>
                        </ClickAwayListener>
                      </ListItem>
                    ) : (
                      ""
                    )}

                    <ListItem className="menu-list-single cart-list">
                      <Tooltip placement="top" title={"cart"}>
                        <IconButton
                          className="menu-link"
                          disabled={count > 0 ? false : true}
                          onClick={() => this.cartZipValidation()}
                          id="cartButton"
                          aria-label={`${count} items in cart`}
                        >
                          {/* <ShoppingCart aria-hidden="false" /> */}
                          <img
                            src="./images/cart.png"
                            alt="cart icon"
                          />
                          {count > 0 && (
                            <span className="cart-count">{count}</span>
                          )}
                        </IconButton>
                      </Tooltip>
                    </ListItem>

                    {isAuthenticated ? (
                      <ClickAwayListener onClickAway={this.handleClickAway}>
                        <ListItem className="menu-list-single user-list">
                          <div
                            className={
                              menuToggle
                                ? "user-list-wrapper open"
                                : "user-list-wrapper"
                            }
                          >
                            {customerDetails !== null && (
                              <Button
                                id="userDropdown"
                                aria-controls="simple-menu"
                                aria-haspopup="true"
                                onClick={this.handleClick}
                                className="user-button"
                                aria-label={`Username: ${profileUserName}`}
                              >
                                <span className="user-icon">
                                  <AccountCircleIcon />
                                </span>
                                <span id="simple-menu" className="user-name">
                                  {profileUserName}
                                </span>
                              </Button>
                            )}
                            {menuToggle ? (
                              <MenuList>
                                <MenuItem
                                  aria-label="profile"
                                  onClick={(event) => {
                                    this.props.history.push("/profile");
                                    this.setState({ menuToggle: false });
                                  }}
                                >
                                  <i>
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="12"
                                      height="9.601"
                                    >
                                      <path
                                        d="M4.2 4.8a2.4 2.4 0 10-2.4-2.4 2.4 2.4 0 002.4 2.4zm1.68.6h-.313a3.264 3.264 0 01-2.734 0H2.52A2.521 2.521 0 000 7.92v.78a.9.9 0 00.9.9h5.155a.9.9 0 01-.049-.4l.128-1.142.023-.208.143-.15 1.453-1.447A2.494 2.494 0 005.88 5.4zm.85 2.725L6.6 9.268a.3.3 0 00.33.33l1.142-.128 2.588-2.585-1.345-1.344zm5.14-3.083l-.711-.711a.449.449 0 00-.634 0l-.709.709-.077.077 1.346 1.344.784-.784a.452.452 0 000-.635z"
                                        fill="#333"
                                      />
                                    </svg>
                                  </i>
                                  My Profile
                                </MenuItem>
                                <MenuItem
                                  onClick={(event) => {
                                    this.userLogout(event);
                                    this.setState({ menuToggle: false });
                                  }}
                                >
                                  <i>
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="8.418"
                                      height="9.621"
                                    >
                                      <path
                                        d="M.9 4.209h.451V2.856a2.856 2.856 0 115.713 0v1.353h.451a.9.9 0 01.9.9v3.61a.9.9 0 01-.9.9H.9a.9.9 0 01-.9-.9V5.111a.9.9 0 01.9-.902zm1.954 0h2.708V2.856a1.353 1.353 0 10-2.706 0z"
                                        fill="#333"
                                      />
                                    </svg>
                                  </i>
                                  Logout
                                </MenuItem>
                              </MenuList>
                            ) : null}
                          </div>
                        </ListItem>
                      </ClickAwayListener>
                    ) : (
                      ""
                    )}
                  </List>

                  <ZipCode
                    onClose={() => {
                      this.setState({
                        zipCode: sessionStorage.getItem("Zipcode"),
                      });
                      this.hideDialog();
                    }}
                    dailogOpen={dailogOpen}
                    clickedBtn={clickedBtn}
                  />

                  <Drawer
                    anchor="left"
                    open={showDrawer}
                    onClick={this.toggleDrawer}
                    className="drawer-wrapper"
                    classes={{ paper: "drawer-mobile" }}
                  >
                    <List className="menu-list-mobile menu-link-wrapper">
                      {!isAuthenticated ? (
                        <ListItem className="menu-list-single login-list">
                          <NavLink
                            className="menu-link no-active"
                            to="#"
                            aria-label="login"
                            onClick={(event) => {
                              sessionStorage.removeItem("prevUrl");
                              this.showLoginModal(event);
                            }}
                          >
                            login
                          </NavLink>
                        </ListItem>
                      ) : (
                        <>
                          <ListItem className="menu-list-single login-list">
                            {customerDetails !== null && (
                              <Button
                                aria-controls="User"
                                className="user-button"
                                onClick={() => {
                                  trackEvent(
                                    EventNames.Action.ACTION_HEADER_PROFILE,
                                    EventNames.Event.EVENT_BUTTON_PRESS,
                                    window.location.origin,
                                    window.location.pathname
                                  );
                                  this.props.history.push("/profile");
                                }}
                                aria-label={`Username: ${profileUserName}`}
                              >
                                <span className="user-icon">
                                  <AccountCircleIcon />
                                </span>
                                <span className="user-name">
                                  {profileUserName}
                                </span>
                              </Button>
                            )}
                          </ListItem>
                        </>
                      )}
                      {this.navigationList()}
                      {isAuthenticated ? (
                        <ListItem className="menu-list-single">
                          <NavLink
                            className="menu-link"
                            to="#"
                            aria-label="Logout"
                            onClick={(event) => {
                              trackEvent(
                                EventNames.Action.ACTION_HEADER_LOGOUT,
                                EventNames.Event.EVENT_BUTTON_PRESS,
                                window.location.origin,
                                window.location.pathname
                              );
                              this.userLogout(event);
                            }}
                          >
                            Logout
                          </NavLink>
                        </ListItem>
                      ) : (
                        ""
                      )}
                    </List>
                    <IconButton
                      onClick={this.toggleDrawer}
                      className="menu-link menu-icon-close"
                    >
                      <CloseIcon />
                    </IconButton>
                  </Drawer>
                </nav>
              </Grid>
            </Grid>
          </Container>

          <Dialog
            // fullScreen={fullScreen}
            open={showLoginModal}
            onClose={() => {
              this.setState({ showLoginModal: false });
            }}
            maxWidth={false}
            scroll={"body"}
            classes={{
              paper: "modal-container modal-paper modal-login",
              root: "modal-stack-top",
            }}
            aria-labelledby="signIn-dialogBox"
          >
            <DialogContent>
              <Login
                loginClicked={(response) => this.onLoginSuccess(response)}
                changeForgotToLogin={() => {}}
                fromForgot={false}
                closeLoginDialog={() =>
                  this.setState({
                    showLoginModal: false,
                  })
                }
              />
              <Button
                className="icn-close"
                id="LoginClose"
                aria-label="Login Dialog Close"
                onClick={() => {
                  this.setState({ showLoginModal: false });
                }}
              >
                <i className="fa fa-times"></i>
              </Button>
            </DialogContent>
          </Dialog>
        </Toolbar>
      </AppBar>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.reducer.isAuthenticated,
    customerDetails: state.reducer.customerDetails,
    zipResponse: state.reducer.zipResponse,
    validationMessage: state.reducer.validationMessage,
  };
};

export default connect(mapStateToProps, {
  LogOut,
  actionGetCustomer,
  actionZipcodeVerification,
})(withRouter(withStyles(styles)(Header)));
