import React, { Component } from "react";
import {
    Button,
    Grid,
    Typography,
    Card
} from "@material-ui/core";


class ViewBilling extends Component {
    constructor(props) {
        super(props);
    }

    getAddress(billingAddressData){
        var billingAddress = "";
        billingAddress += (!!billingAddressData && !!billingAddressData.billingAddressline1?billingAddressData.billingAddressline1:"");
        billingAddress += (!!billingAddressData && !!billingAddressData.billingAddressline2?", "+billingAddressData.billingAddressline2:"");
        billingAddress += !!billingAddressData && !!billingAddressData.billingCity?", "+billingAddressData.billingCity:"";
        billingAddress += !!billingAddressData && !!billingAddressData.billingState?", "+billingAddressData.billingState:"";
        billingAddress += !!billingAddressData && billingAddressData.billingZip?", "+billingAddressData.billingZip:"";
        return billingAddress;
    }
    render() {
        const customerDetails = this.props.customerDetails;
        let billingTxt = customerDetails.billingFirstname ? 'Edit' : 'Add Billing Address';
        let billingHeading = customerDetails.billingFirstname ? 'Billing Address' : '';

        return (
            <div className="profile-info">
                <>
                <Grid container spacing={6} justify="space-between">
                {/* Biling Address */}
                    <Grid item xs={12} sm={12}>
                        <Grid container spacing={2} alignItems="center" className="title-mini">
                            <Grid item xs container>
                                <Typography variant="h2" component="h2">{billingHeading}</Typography>
                            </Grid>
                            <Grid item className="text-right">
                                <Button 
                                    variant="contained"
                                    color="primary"
                                    className='btn-link'
                                    id="editBillingAddress"
                                    aria-label="edit Billing Address"
                                    onClick={() => this.props.setBilling(false)}
                                >
                                    {billingTxt}
                                </Button>
                            </Grid>
                        </Grid>
                        {
                            customerDetails.billingFirstname ? <Card className="profile-card">
                                <div className="profile-card-single">
                                    <div className="key">{(!!customerDetails?(!!customerDetails.billingFirstname?customerDetails.billingFirstname:"")+" "+(!!customerDetails.billingLastname?customerDetails.billingLastname:""):"-")}</div>
                                <div className="val">{this.getAddress(customerDetails)}</div>
                                </div>
                            </Card> : ''
                        }
                    </Grid>
                </Grid>
                </>
            </div>
        );
    }
}
export default ViewBilling;