// Material imports
import {
  Button,
  Card,
  Grid,
  IconButton,
  Typography,
  Tooltip,
} from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import { classes } from "istanbul-lib-coverage";
import React, { Component } from "react";
import { connect } from "react-redux";
import { actionGetMinAmount } from "./../../../actions/ProductAction";
import SnacksAlertMessage from "./../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import CustomEvent from "custom-event";
import Login from "./../../login/login";
import DialogBox from "./../../commonComponents/dialogBox/DialogBox";
import { trackEvent, EventNames } from "./../../../appinsights/EventTrack";
import QuantityCount from "./../../commonComponents/quantityCount/QuantityCount";
import { DISPLAY_HEADING } from "./../../../config/constants/content.constant";
import "./cartSummary.scss";

class CartSummary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cartItems: JSON.parse(sessionStorage.getItem("cartItems")) || [],
      open: false,
      message: "The cart is empty",
      showDeletPrompt: false,
      itemIndex: undefined,
      cartData: [],
      totalPrice: 0,
      minPrice: "",
      inventoryInvalidData: [],
      deleteClicked: false,
      snackOpen: true,
      msg: "",
      selectedItems: [],
    };
  }

  componentDidMount() {
    this.props.actionGetMinAmount("", "", "", "").then(() => {
      if (
        !!this.props.minPrice &&
        Object.keys(this.props.minPrice).length > 0
      ) {
        this.setState(
          { minPrice: this.props.minPrice.minimumOrderAmount },
          () => this.getData(true)
        );
      }
    });
    setTimeout(function () {
      window.scrollTo(0, 0);
    }, 10);
  }
  componentWillReceiveProps() {
    if (!!this.props.inventoryData) {
      this.setState({
        inventoryInvalidData: !!this.props.inventoryData
          ? this.props.inventoryData
          : [],
      });
    }
  }

  componentWillUnmount() {
    document.getElementById("body").classList.remove("has-toast");
  }
  //clear the cart Data
  clearCart = () => {
    trackEvent(
      EventNames.Action.ACTION_CLEAR_CART,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    );
    sessionStorage.removeItem("total");
    sessionStorage.removeItem("cartItems");
    sessionStorage.removeItem("selectedOrder");
    this.setState(
      {
        cartData: [],
        cartItems: [],
        totalPrice: "0.00",
        showDeletPrompt: false,
      },
      () => {
        this.getData();
        //fire custom event to change cart item count
        var event = new CustomEvent("cartitem");
        document.dispatchEvent(event);
      }
    );
  };
  getData = (isLoad) => {
    let cartItems = this.state.cartItems;
    var totalPrice = 0;
    var error = null;
    if (cartItems.length > 0) {
      cartItems.map((items) => {
        var total = items.price * items.quantity;
        totalPrice = totalPrice + total;
        return true;
      });
      if (totalPrice >= this.state.minPrice) {
        if (!isLoad) {
          this.props.handleclose();
        }
      } else {
        error =
          "$" +
          parseFloat(this.state.minPrice).toFixed(2) +
          " " +
          "Minimum Order Requirement.";
        document.getElementById("body").classList.add("has-toast");
      }
    } else {
      this.props.handleclose();
    }
    this.props.cartDetails(totalPrice, this.state.minPrice, error);
    sessionStorage.setItem("total", parseFloat(totalPrice).toFixed(2));
    sessionStorage.setItem("tax", 0);
    this.setState({ totalPrice: parseFloat(totalPrice).toFixed(2) });
  };
  /**
   *
   * @desc remove item from cart
   */
  removeCartItem() {
    let index = this.state.itemIndex;
    if (index !== undefined && index !== null) {
      let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DELETE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: cartItems[index] }
      );
      cartItems.splice(index, 1);
      sessionStorage.setItem("cartItems", JSON.stringify(cartItems));
      this.setState(
        { cartItems: cartItems, showDeletPrompt: false, cartData: cartItems },
        () => {
          sessionStorage.setItem("cartItems", JSON.stringify(cartItems));
          this.getData();
          this.props.isOrderInventory(false);
        }
      );
      this.props.setLength(cartItems.length);
      if (cartItems.length <= 0) {
        sessionStorage.removeItem("total");
        sessionStorage.removeItem("selectedOrder");
        sessionStorage.removeItem("cartItems");
        this.setState({ cartData: [], cartItems: [], totalPrice: "0.00" });
        this.getData();
      }
    }
    //fire custom event to change cart item count
    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
  }

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  /**
   * @desc Render function for cart item data with actions
   * @param {object} cartItem
   * @param {int} index
   */
  renderItem(cartItem, index) {
    //const { inventoryInvalidData } = this.state;
    const {
      price,
      quantity,
      productId,
      image,
      size,
      productDescription,
      shortDescription,
    } = cartItem;
    /** total price calculation based on product count group */
    let totalPrice = price * quantity;
    let inventoryInvalidData = this.state.inventoryInvalidData[productId] || [];
    const productDesc =
      `${productDescription} - ${size}` || `${shortDescription} - ${size}`;

    return (
      <Card key={index} className="bundle-card-summary">
        {/* <ProductList /> */}
        <Grid container spacing={2} alignItems="center">
          <Grid item className="summary-row-img">
            <div className="summary-img">
              {typeof image === "string" ? (
                <img alt={productDesc} src={image} />
              ) : (
                <img alt={productDesc} src={image.Product_Image} />
              )}
            </div>
          </Grid>
          <Grid item className="summary-row-info">
            <Grid container spacing={0} alignItems="center">
              <Grid item xs={6} sm={6} md={6}>
                <div className="title-product">{productDesc}</div>
              </Grid>
              <Grid item xs={3} sm={3} md={3}>
                <Grid container spacing={0} className="counter-grid mx-auto">
                  <QuantityCount
                    primaryProduct={cartItem}
                    productInfo={cartItem}
                    warningMsgFunc={this.warningMsgFunc}
                    selectedItems={this.selectedCartItems}
                  />
                </Grid>
              </Grid>
              <Grid item xs={3} sm={3} md={3} align="start">
                <Grid container spacing={2}>
                  <Grid
                    item
                    xs={12}
                    sm
                    container
                    alignItems="center"
                    justify="center"
                    className="price-wrapper"
                  >
                    <div className="price">
                      ${totalPrice > 0 ? totalPrice.toFixed(2) : 0}
                    </div>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid item className="card-summary-action summary-row-action">
            <Tooltip placement="top" title={"delete"}>
              <IconButton
                id={"summaryDelete" + index}
                aria-label="Product Delete dialog box will be open"
                className={classes.margin + " btn-trash"}
                onClick={() => {
                  this.setState({
                    showDeletPrompt: true,
                    itemIndex: index,
                    deleteClicked: true,
                  });
                }}
              >
                <i className="fa fa-trash-alt"></i>
              </IconButton>
            </Tooltip>
          </Grid>
        </Grid>
        {inventoryInvalidData.msg && (
          <div className="font-14 text-danger cart-summary-error">
            {inventoryInvalidData.msg}
          </div>
        )}
      </Card>
    );
  }

  render() {
    let cartItems = this.state.cartItems;

    const deleteMsg = this.state.deleteClicked
      ? " Are you sure you would like to remove this item from the cart?"
      : "Are you sure you would like to remove all items from the cart?";
    return (
      <>
        <div className="card-summary-wrapper">
          <SnacksAlertMessage fullWidth msg={this.props.errorMsg} />

          <div className="card-summary-header">
            <Typography variant="h2">{DISPLAY_HEADING.YOUR_ORDER}</Typography>
            {cartItems.length > 0 ? (
              <a
                variant="contained"
                color="primary"
                className="btn-mui btn-blue btn-outlined clear-cart"
                id="emptyCart"
                aria-label={DISPLAY_HEADING.CLEAR_CART}
                tabIndex={0}
                onClick={() =>
                  this.setState({ showDeletPrompt: true, deleteClicked: false })
                }
              >
                {DISPLAY_HEADING.CLEAR_CART}
              </a>
            ) : (
              ""
            )}
          </div>
          <div className="cart-summary-container">
            {!cartItems.length === 0 ? (
              <Grid
                container
                spacing={2}
                alignItems="center"
                className="cart-summary-head"
              >
                <Grid item>
                  <div className="summary-img"></div>
                </Grid>
                <Grid item xs={12} sm container alignItems="center">
                  <Grid item xs={5} sm={5} md={5}>
                    <Typography variant="h4" className="cart-summary-title">
                      Product Name
                    </Typography>
                  </Grid>
                  <Grid item xs={3} sm={3} md={3} className="text-center">
                    <Typography variant="h4" className="cart-summary-title">
                      Quantity
                    </Typography>
                  </Grid>
                  <Grid
                    item
                    xs={3}
                    sm={3}
                    md={3}
                    align="start"
                    className="card-summary-action"
                  >
                    <Grid container spacing={2}>
                      <Grid
                        item
                        xs={12}
                        sm
                        container
                        alignItems="center"
                        justify="center"
                      >
                        <Typography
                          variant="h4"
                          className="cart-summary-title cost-title"
                        >
                          Cost
                        </Typography>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid item xs={1} sm={1} md={1}></Grid>
                </Grid>
              </Grid>
            ) : (
              ""
            )}
            <div className="cart-summary-loop">
              {cartItems.length === 0 ? (
                <div className="cart-no-item">{this.state.message}</div>
              ) : (
                cartItems.map((item, index) => {
                  if (item) {
                    return this.renderItem(item, index);
                  } else {
                    return "";
                  }
                })
              )}
              {cartItems.length === 0 ? (
                ""
              ) : (
                <div className="cart-summary-total">
                  <span className="title">Total:</span>
                  <span className="amount">
                    <i>$</i>
                    {this.state.totalPrice}
                  </span>
                </div>
              )}
            </div>

            {/* Dialog for delete confirmation */}
            <DialogBox
              dialogClose={() =>
                this.setState({ showDeletPrompt: false, deleteClicked: false })
              }
              dialogOpen={this.state.showDeletPrompt}
              dialogHeading={deleteMsg}
              dialogWarning
              dialogCloseConfirm={() => {
                this.state.deleteClicked
                  ? this.removeCartItem()
                  : this.clearCart();
              }}
              ariaLabelYes={
                this.state.deleteClicked
                  ? "Yes I want to remove the product"
                  : "Yes I want to empty my cart"
              }
              ariaLabelCancel={
                this.state.deleteClicked
                  ? "No I don't want to remove the product"
                  : "No I don't want to empty my cart"
              }
            />

            {/* Login modal */}

            <Dialog
              // fullScreen={fullScreen}
              open={this.props.showLoginModal}
              onClose={() => {
                this.props.hideLoginModal();
              }}
              maxWidth={false}
              scroll={"body"}
              classes={{
                paper: "modal-container modal-paper modal-login is-cart",
                root: "modal-stack-top",
              }}
              aria-labelledby="signIn-dialogBox"
            >
              <DialogContent>
                <Login
                  changeForgotToLogin={() => {}}
                  fromForgot={false}
                  closeLoginDialog={() => this.props.hideLoginModal()}
                />
                <Button
                  className="icn-close"
                  id="LoginClose"
                  aria-label="Login Dialog Close"
                  onClick={() => {
                    this.props.hideLoginModal();
                  }}
                >
                  <i className="fa fa-times"></i>
                </Button>
              </DialogContent>
            </Dialog>
          </div>
        </div>
        <div className="summary-text-info">All sales are final.</div>
        <div className="thankyou-note summary-text-info">
          Note: We are currently experiencing shipping delays due to COVID-19.
          All orders are processed in the order that they are received, and we
          are doing our best to ship all orders on time. Thank you for your
          patience.
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    minPrice: state.reducer.minAmount,
  };
};

export default connect(mapStateToProps, { actionGetMinAmount })(CartSummary);
