import React, { Component } from "react";
import { Button, Modal } from "@material-ui/core";
import "./dialogBox.scss";

export default class DialogBox extends Component {
  render() {
    const {
      dialogWarning,
      dialogOpen,
      dialogClose,
      dialogHeading,
      dialogCloseConfirm,
      ariaLabelCancel,
      ariaLabelYes,
    } = this.props;
    return (
      <Modal
        open={dialogOpen}
        onClose={() => dialogClose()}
        aria-labelledby="simple-modal-title"
        className={"modal-wrapper  modal-wrapper-container"}
        scroll={"body"}
      >
        <div className={"modal-wrapper modal-wrapper-container modal-paper"}>
          <h2 id="simple-modal-title" className="heading">
            {dialogWarning && <i className="fa fa-exclamation-circle"></i>}
            {dialogHeading}
          </h2>
          <Button
            aria-label={ariaLabelYes || "yes"}
            className="accept"
            onClick={() => dialogCloseConfirm()}
          >
            Yes
          </Button>
          <Button
            aria-label={ariaLabelCancel || "cancel"}
            className="reject"
            onClick={() => dialogClose()}
          >
            Cancel
          </Button>
        </div>
      </Modal>
    );
  }
}
