import React, { Component } from 'react';
import { Button, Checkbox, FormControlLabel, Grid, Radio, RadioGroup, Snackbar } from '@material-ui/core';
import { withRouter } from 'react-router-dom';
import MuiAlert from '@material-ui/lab/Alert';
import AddNewAddress from './AddNewAddress';
import ChangeNewAddress from './ChangeNewAddress';
import APIUtil from '../../../config/APIUtil'
import UrlConstants from '../../../config/UrlConstants'
import {
	trackEvent,
	EventNames
} from '../../../appinsights/EventTrack';
import { connect } from 'react-redux';
import ViewCustomer from './ViewCustomer';
import {updatePrice} from "./../../Utils";
	

const countryOptions = [{ value: 'US', label: 'United States' }];

class CheckoutDetails extends Component {
	totalShippingAddress = 0

	constructor(props) {
		super(props);
		if (sessionStorage.getItem('checkoutMode') !== null && sessionStorage.getItem('checkoutMode') === 'Guest') {
			sessionStorage.removeItem('billingSameAsShipping')
			sessionStorage.setItem('checkoutMode', "Login")
		}

		var {
			userInfo: { accountContacts },
		} = this.props;
		this.primaryAddress = undefined;
		this.addressBilling = sessionStorage.getItem('formChangeValues') !== null ? JSON.parse(sessionStorage.getItem('formChangeValues')) : null
		this.state = {
			changedAddress: sessionStorage.getItem('formChangeValues') !== null ? true : false,
			accountAddress: this.props.userInfo !== null && this.props.userInfo.accountAddresses !== null ? this.props.userInfo.accountAddresses : [],
			editContact: false,
			addNewAddress: this.props.userInfo === null || this.props.userInfo.accountAddresses === null || this.props.userInfo.accountAddresses.length === 0 ? true : false,
			changeBillingAddress: false,
			isError: false,
			errorMessage: '',
			selectedShippingAddress: -1,
			selectedBillingAddress: -1,
			editShipping: false,
			changesShippingAddress:'',
			billingSameAsShipping: JSON.parse(sessionStorage.getItem('billingSameAsShipping'))
				? JSON.parse(sessionStorage.getItem('billingSameAsShipping'))
				: false,

			intialAddress: {
				guestCheckout: this.props.guestCheckout,

				firstname: accountContacts && accountContacts.length > 0 ? accountContacts[0].firstName : '',
				lastname: accountContacts && accountContacts.length > 0 ? accountContacts[0].lastName : '',
				email: accountContacts && accountContacts.length > 0 ? accountContacts[0].email : '',
				contact: accountContacts && accountContacts.length > 0 ? accountContacts[0].phone : '',

				billingSameAsShipping: JSON.parse(sessionStorage.getItem('billingSameAsShipping'))
					? JSON.parse(sessionStorage.getItem('billingSameAsShipping'))
					: false,

				shippingFirstname: '',
				shippingLastname: '',
				shippingAddressLine1: '',
				shippingAddressLine2: '',
				shippingCity: '',
				shippingState: '',
				shippingCountry: countryOptions[0].value,
				shippingZipCode: '',

				billingFirstname: this.addressBilling !== null ? this.addressBilling.billingFirstname + " " + this.addressBilling.billingLastname : '',
				billingLastname: this.addressBilling !== null ? this.addressBilling.billingFirstname + " " + this.addressBilling.billingLastname : '',
				billingAddressLine1: this.addressBilling !== null ? this.addressBilling.billingAddressLine1 : '',
				billingAddressLine2: this.addressBilling !== null ? this.addressBilling.billingAddressLine2 : '',
				billingCity: this.addressBilling !== null ? this.addressBilling.billingCity : '',
				billingState: this.addressBilling !== null ? this.addressBilling.billingState : '',
				billingCountry: this.addressBilling !== null ? this.addressBilling.billingCountry : countryOptions[0].value,
				billingZipCode: this.addressBilling !== null ? this.addressBilling.billingZipCode : '',
			},
		};
	}

	componentDidMount = async () => {
		if (
			this.props.userInfo &&
			this.props.userInfo.accountContacts &&
			this.props.userInfo.accountContacts.length > 0
		) {
			this.primaryAddress = await this.getPrimaryAddress();
			this.onAddressChange(this.state.selectedShippingAddress, 'SHIPPING_ADDRESS',false);
			this.onAddressChange(this.state.selectedBillingAddress, 'BILLING_ADDRESS',false);
		}
		let loggedInUser = localStorage.getItem("accessToken");
		if(loggedInUser && sessionStorage.getItem('taxZipCode') === null){
			let primaryShippingAddress = this.state.accountAddress.filter(data => data.primaryAddr === true);
			sessionStorage.setItem('taxZipCode',primaryShippingAddress[0].zip);
		}
		this.checkTaxDetails(this.state.intialAddress,true,'shipping',false);
	};

	checkTaxDetails(values, isInput, isAddress,isShipingChange=false) {
		if (!!values.shippingZipCode && values.shippingZipCode.length == '5') {
			if(isShipingChange){
				var objData = isInput;
			}	
			else{
				var objData = this.getTaxRequest(values, isInput, isAddress);
			}
			//if(isInput){
			objData['isAddress'] = true;
			if (isAddress == 'billing') {
				objData['isBillingAddress'] = true;
			}
			//}
			let url = UrlConstants.orderTax;
			let defaultErrorMsg = UrlConstants.errorMsg;
			this.setState({ submitDisabled: true, isDataFilled: true });
			
			//Validate Zipcode & Adress. Fetch Tax Amount
			APIUtil.postMethod(url, objData, true)
				.then((response) => {
					if (response.data.success === true && response.data.zipSuccess === true) {
						this.setState({ ZipfailMessage: response.data.zipErrorMessage });
						document.getElementById('body').classList.remove('has-toast');
						//sessionStorage.setItem("taxFree", response.data.taxFree);
						if(isShipingChange){
							sessionStorage.setItem("taxZipCode", values.shippingZipCode);
						}
						// If the isTaxFree true - Then we have to calculate tree otherwise stop calculating tax
						if (!response.data.taxFree) {
							if (response.data.taxSuccess === true) {
								updatePrice(response);
								response.data.zipcode = values.shippingZipCode;
								response.data.totalTaxAmount = response.data.data.tax
									? response.data.data.tax.totalTaxAmount
									: '';
								setTimeout(() => {
									this.props.getTaxtLoader(false, response);
								}, 100);
								
								//this.setState({ submitDisabled: false })
							} 
						} else {
							this.setState({
								ZipfailMessage: response.data.taxErrorMessage,
							});
							if(response.data.taxErrorMessage){
								this.setState({
									submitDisabled: true,
								});
							}
							response.data.zipcode = values.shippingZipCode;
							response.data.totalTaxAmount = 0;
							this.props.getTaxtLoader(false, response);
							setTimeout(() => {
								this.setState({ ZipfailMessage: null });
							}, 3000);
						}
					} else {
						response.data.zipcode = values.shippingZipCode;
						response.data.totalTaxAmount = 0;
						setTimeout(() => {
							this.props.getTaxtLoader(false, response);
						}, 100);
						this.setState(
							{
								isDisabledBillingfield: true,
								submitDisabled: true,
								ZipfailMessage: response.data.zipErrorMessage,
							},
							() => {
								/* setTimeout(() => {
                this.setState({ ZipfailMessage: null });
              }, 3000 )*/
								document.getElementById('body').classList.add('has-toast');
							}
						);
					}
				})
				.catch((response) => {
					response.data.zipcode = values.shippingZipCode;
					response.data.totalTaxAmount = 0;
					this.setState(
						{
							ZipfailMessage: 'Something went to wrong on response!!',
							submitDisabled: true,
						},
						() => {
							document.getElementById('body').classList.add('has-toast');
						}
					);
					this.props.getTaxtLoader(false, response);
					/* setTimeout(() => {
          this.setState({ ZipfailMessage: null });
        }, 3000) */
				});
		} else {
			this.setState({ submitDisabled: true });
			//var response = {};
			//response.zipcode = !!sessionStorage.getItem("Zipcode")?parseInt(sessionStorage.getItem("Zipcode")):null;
			//this.props.getTaxtLoader(false,response);
		}
	}

	prepareData = (shippingAddressData) => {
		var cartItems = !!sessionStorage.getItem('cartItems') ? JSON.parse(sessionStorage.getItem('cartItems')) : [];
		var zipCode = shippingAddressData.zip;
		var shippingState = shippingAddressData.state;
		var shippingCity = shippingAddressData.city;
		var shippingCountry = shippingAddressData.country;
		var shippingAddressLine1 = shippingAddressData.addressLine1;
		var shippingAddressLine2 = shippingAddressData.addressLine2;
		
		//Destination Data
		let addObj = {};
		addObj['postalArea'] = zipCode;
		addObj['stateOrProvinceName'] = shippingState;
		//Line Items
		this.props.getTaxtLoader(true, null);
		let arrData = [];
		cartItems &&
			cartItems.length > 0 &&
			cartItems.forEach((e) => {
				let lineItemsObj = {};
				lineItemsObj['lineItemNumber'] = cartItems.length;
				// lineItemsObj['orderLocationId'] = !!sessionStorage.getItem('_lo_No') ? sessionStorage.getItem('_lo_No') : null;

				lineItemsObj['product'] = {
					productClass: !!e.productClass ? e.productClass : null,
					productID: !!e.productId ? e.productId : null,
				};
				lineItemsObj['quantity'] = !!e.quantity ? parseInt(e.quantity) : 0;
				lineItemsObj['unitPrice'] = !!e.price ? parseFloat(e.price) : 0;
				arrData.push(lineItemsObj);
			});
		//Address Data
		let addressData = {};
		addressData['Region'] =
			(!!shippingCity ? shippingCity : '') +
			(!!shippingState ? ', ' + shippingState : '') +
			(!!zipCode ? ', ' + zipCode : '');
		addressData['CountryCode'] = !!shippingCountry ? shippingCountry : '';
		addressData['AddressLine'] = [
			(!!shippingAddressLine1 ? shippingAddressLine1 : '') +
			' ' +
			(!!shippingAddressLine2 ? shippingAddressLine2 : ''),
		];
		// Tax post data Object
		let shippingData = {};
		shippingData['destination'] = addObj;
		shippingData['Address'] = addressData;
		shippingData['documentDate'] = new Date();
		shippingData['lineItems'] = arrData;
		shippingData['transactionType'] = 'SALE';
		shippingData['sourceLocationId'] = !!sessionStorage.getItem('_lo_No') ? sessionStorage.getItem('_lo_No') : null;
		return shippingData;
	}

	changeShippingAddress = () => {
		this.setState({
			editShipping: !this.state.editShipping,
			editContact: false,
			changeBillingAddress: false
		});
		let selectedShippingAddress = this.state.selectedShippingAddress;
		let addressData = this.prepareData(this.state.accountAddress[selectedShippingAddress]);
		this.setState({changesShippingAddress:addressData});
		this.checkTaxDetails(this.state.intialAddress,addressData,'shipping',true);
	}



	getTaxRequest(values, isInput, isAddress) {
		var cartItems = !!sessionStorage.getItem('cartItems') ? JSON.parse(sessionStorage.getItem('cartItems')) : [];
		var zipCode = !!values.shippingZipCode ? values.shippingZipCode : null;
		var shippingState = !!values.shippingState ? values.shippingState : null;
		var shippingCity = !!values.shippingCity ? values.shippingCity : null;
		var shippingCountry = !!values.shippingCountry ? values.shippingCountry : null;
		var shippingAddressLine1 = !!values.shippingAddressLine1 ? values.shippingAddressLine1 : null;
		var shippingAddressLine2 = !!values.shippingAddressLine2 ? values.shippingAddressLine2 : null;
		if (isAddress == 'billing') {
			zipCode = !!values.billingZipCode ? values.billingZipCode : null;
			shippingState = !!values.billingState ? values.billingState : null;
			shippingCity = !!values.billingCity ? values.billingCity : null;
			shippingCountry = !!values.billingCountry ? values.billingCountry : null;
			shippingAddressLine1 = !!values.billingAddressLine1 ? values.billingAddressLine1 : null;
			shippingAddressLine2 = !!values.billingAddressLine2 ? values.billingAddressLine2 : null;
		}
		//Destination Data
		let addObj = {};
		addObj['postalArea'] = zipCode;
		addObj['stateOrProvinceName'] = shippingState;
		//Line Items
		this.props.getTaxtLoader(true, null);
		let arrData = [];
		cartItems &&
			cartItems.length > 0 &&
			cartItems.forEach((e) => {
				let lineItemsObj = {};
				lineItemsObj['lineItemNumber'] = cartItems.length;
				// lineItemsObj['orderLocationId'] = !!sessionStorage.getItem('_lo_No') ? sessionStorage.getItem('_lo_No') : null;

				lineItemsObj['product'] = {
					productClass: !!e.productClass ? e.productClass : null,
					productID: !!e.productId ? e.productId : null,
				};
				lineItemsObj['quantity'] = !!e.quantity ? parseInt(e.quantity) : 0;
				lineItemsObj['unitPrice'] = !!e.price ? parseFloat(e.price) : 0;
				arrData.push(lineItemsObj);
			});
		//Address Data
		let addressData = {};
		addressData['Region'] =
			(!!shippingCity ? shippingCity : '') +
			(!!shippingState ? ', ' + shippingState : '') +
			(!!zipCode ? ', ' + zipCode : '');
		addressData['CountryCode'] = !!shippingCountry ? shippingCountry : '';
		addressData['AddressLine'] = [
			(!!shippingAddressLine1 ? shippingAddressLine1 : '') +
			' ' +
			(!!shippingAddressLine2 ? values.shippingAddressLine2 : ''),
		];
		// Tax post data Object
		let objData = {};
		objData['destination'] = addObj;
		objData['Address'] = addressData;
		objData['documentDate'] = new Date();
		objData['lineItems'] = arrData;
		objData['transactionType'] = 'SALE';
		objData['sourceLocationId'] = !!sessionStorage.getItem('_lo_No') ? sessionStorage.getItem('_lo_No') : null;
		return objData;
	}

	componentDidUpdate = async (prevProps) => {
		if (prevProps.userInfo !== this.props.userInfo) {
			this.setState({
				accountAddress: this.props.userInfo !== null && this.props.userInfo.accountAddresses !== null ? this.props.userInfo.accountAddresses : [],
			}, async () => {
				if (
					this.props.userInfo &&
					this.props.userInfo.accountContacts &&
					this.props.userInfo.accountContacts.length > 0
				) {
					this.primaryAddress = await this.getPrimaryAddress();
					this.onAddressChange(this.state.selectedShippingAddress, 'SHIPPING_ADDRESS',false);
					this.onAddressChange(this.state.selectedBillingAddress, 'BILLING_ADDRESS',false);
				}
			})
		}
	}

	getPrimaryAddress = () => {
		let indexS = -1;
		let foundShipping = -1;
		let address = this.state.accountAddress
			?
			this.state.accountAddress.filter((address, index) => {
				if (address.addressType === 'SHIPPING_ADDRESS') {
					if (address.primaryAddr) {
						indexS = index;
					} else if (address.id === sessionStorage.getItem('selectedShippingAddress')) {
						foundShipping = address.id
						this.setState({
							selectedShippingAddress: index,
							selectedBillingAddress: !this.state.changedAddress || this.state.billingSameAsShipping ? index : -1,
						});
						return address
					}
				}
			}).shift()
			: undefined;
		if (this.state.accountAddress && address === undefined && indexS !== -1 && (foundShipping === -1 || this.state.selectedShippingAddress < this.state.accountAddress.length)) {
			address = this.state.accountAddress[indexS]
			this.setState({
				selectedShippingAddress: indexS,
				selectedBillingAddress: !this.state.changedAddress || this.state.billingSameAsShipping ? indexS : -1,
			});
		}
		if (address === undefined && this.state.accountAddress && this.state.accountAddress.length > 0) {
			address = this.state.accountAddress
				? this.state.accountAddress
					.filter((address, index) => {
						if (address.addressType === 'SHIPPING_ADDRESS') {
							this.setState({
								selectedShippingAddress: index,
								selectedBillingAddress: !this.state.changedAddress || this.state.billingSameAsShipping ? index : -1,
							});
							return address;
						}
					})
					.shift()
				: undefined;
		}

		if (this.state.accountAddress && this.state.accountAddress.length > 0 && !this.state.billingSameAsShipping) {
			let add = this.state.accountAddress
				? this.state.accountAddress.filter((address, index) => {
					if (address.addressType === 'BILLING_ADDRESS') {

						this.setState({
							selectedBillingAddress: !this.state.changedAddress || this.state.billingSameAsShipping ? index : -1,
						});
						return address;
					}
				})
					.shift() : undefined

			if (add === undefined) {
				this.setState({
					selectedBillingAddress: -1,
				});
			}
		}
		return address;
	};

	componentWillUnmount() {
		if (!this.state.changedAddress) {
			sessionStorage.removeItem('formChangeValues')
		}
	}

	getAllAddress = (type) => {
		if (type === 'SHIPPING_ADDRESS') {
			this.totalShippingAddress = 0
		}
		var i = 0;

		return this.state.accountAddress ? (
			<Grid item xs={12} sm={12}>
				<RadioGroup
					aria-label="Select another address to ship"
					name="SelectAnotherAddressToShip"
					className="radio-group-shipping"
					defaultValue={
						type === 'SHIPPING_ADDRESS'
							? this.state.selectedShippingAddress
							: this.state.selectedBillingAddress
					}
					value={
						type === 'SHIPPING_ADDRESS'
							? this.state.selectedShippingAddress
							: this.state.selectedBillingAddress
					}
				>
					{this.state.accountAddress.map((address, index) => {

						if (address.addressType === type) {
							i++;

							if (type === 'SHIPPING_ADDRESS') this.totalShippingAddress++;

							return (
								<Grid key={address.id}>
									{i !== 1 && type === 'SHIPPING_ADDRESS' && <hr />}
									<Grid item xs={12} sm={12} className="userinfo-wrapper">
										<FormControlLabel
											onChange={(e) => {
												this.onAddressChange(index, type, true);
											}}
											value={index}
											key={`${type + index}`}
											control={<Radio />}
											label={
												<>
													<div className="key full">{address.name}</div>
													<div className="value full">{this.getAddress(address)}</div>
												</>
											}
										/>
									</Grid>
								</Grid>
							);
						} else {
							return <div key={address.id} />;
						}
					})}
				</RadioGroup>
			</Grid>
		) : undefined;
	};

	onAddNewAddress = () => {
		return <AddNewAddress
			cancelAddAddress={() => this.cancelAddAddress()}
			saveAddAddress={(values) => this.createShippingAddress(values)}
			getTaxtLoader={(data, summuryData) => this.props.getTaxtLoader(data, summuryData)}
		/>
	};

	onChangeBillingAddress = () => {
		return <ChangeNewAddress
			intialAddress={this.state.intialAddress}
			cancelChangeAddress={() => this.cancelChangeAddress()}
			saveBillingAddress={(values) => this.createBillingAddress(values)}
			getTaxtLoader={(data, summuryData) => this.props.getTaxtLoader(data, summuryData)}
		/>
	};

	createShippingAddress = (values) => {
		var shipObj = {
			customer: { id: this.props.userInfo.id },
			shipping: values
		}
		trackEvent(EventNames.Action.ACTION_ADD_SHIPPING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { shippingData: shipObj })
		APIUtil.postMethod(UrlConstants.CreateAddress, shipObj, true).then(response => {
			if (!!response.data && !!response.data.success) {
				sessionStorage.removeItem('formAddValues')
				this.props.getCustomer()
				this.setState({
					addNewAddress: false,
					isError: false,
					errorMessage: ''
				})
			} else {
				this.setState({
					isError: true,
					errorMessage: UrlConstants.errorMsg
				})
			}
		}).catch((err) => {
			this.setState({
				isError: true,
				errorMessage: UrlConstants.errorMsg
			})
		});
	}

	getBillingAddress = () => {
		this.state.accountAddress.map(address => {
			if (address.addressType === 'BILLING_ADDRESS') return address;
		})
		return null;
	}

	createBillingAddress = (values) => {
		var totalBillingAddress = 0
		this.state.accountAddress.map(address => {
			if (address.addressType === 'BILLING_ADDRESS') totalBillingAddress++
		})
		if (totalBillingAddress === 0) {
			var billObj = {
				customer: { id: this.props.userInfo.id },
				billing: {
					billingAddressline1: values.billingAddressLine1,
					billingAddressline2: values.billingAddressLine2,
					billingCity: values.billingCity,
					billingCountry: values.billingCountry,
					billingFirstname: values.billingFirstname,
					billingLastname: values.billingLastname,
					billingSameAsShipping: values.billingSameAsShipping,
					billingState: values.billingState,
					billingZip: values.billingZipCode
				}
			}
			trackEvent(EventNames.Action.ACTION_ADD_BILLING_ADDRESS, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { billingData: billObj })
			APIUtil.postMethod(UrlConstants.CreateAddress, billObj, true).then(response => {
				if (!!response.data && !!response.data.success) {
					sessionStorage.removeItem('formChangeValues')
					this.props.getCustomer()
					this.setState({
						changeBillingAddress: false,
						isError: false,
						errorMessage: ''
					})
				} else {
					this.setState({
						isError: true,
						errorMessage: UrlConstants.errorMsg
					})
				}
			}).catch((err) => {
				this.setState({
					isError: true,
					errorMessage: UrlConstants.errorMsg
				})
			}).finally(() => {
				this.saveBillingAddress(values)
			});
		} else {
			this.saveBillingAddress(values)
		}
	}

	onAddressChange = (index, type,isChanged=false) => {
		if (index !== -1) {
			let currentShippingAddress = '';
			if(!isChanged){
				let selectedShippingAddress = sessionStorage.getItem('selectedShippingAddress');
				if(selectedShippingAddress === null){
					currentShippingAddress = this.state.accountAddress[index];
				}
				else{
					let filteredShippingAddress = this.state.accountAddress.filter(data => data.id === selectedShippingAddress); 
					currentShippingAddress = filteredShippingAddress[0];
				}
			}
			else{
				currentShippingAddress = this.state.accountAddress[index];
			}
			let address = this.state.accountAddress[index];
			let newAdd = { ...this.state.intialAddress };
			if (type === 'SHIPPING_ADDRESS') {
				newAdd = {
					shippingFirstname: address.name,
					shippingLastname: address.name,
					shippingAddressLine1: address.addressLine1,
					shippingAddressLine2: address.addressLine2,
					shippingCity: address.city,
					shippingState: address.state,
					shippingCountry: address.country,
					shippingZipCode: address.zip,
				};
			}
			if (this.state.billingSameAsShipping || type === 'BILLING_ADDRESS') {
				newAdd = {
					...newAdd,
					billingFirstname: address.name,
					billingLastname: address.name,
					billingAddressLine1: address.addressLine1,
					billingAddressLine2: address.addressLine2,
					billingCity: address.city,
					billingState: address.state,
					billingCountry: address.country,
					billingZipCode: address.zip,
				};
			}
			var error = false;
			var errorMessage = '';
			if (address !== null) {
				if (address?.name?.length === 0)
					errorMessage = 'Address name is missing, please update your address from profile section';
				else if (address?.addresLine1?.length === 0)
					errorMessage = 'Address address is missing, please update your address from profile section';
				else if (address?.city?.length === 0)
					errorMessage = 'Address city is missing, please update your address from profile section';
				else if (address?.state?.length === 0)
					errorMessage = 'Address state is missing, please update your address from profile section';
				else if (address?.country?.length === 0)
					errorMessage = 'Address country is missing, please update your address from profile section';
				else if (address?.zip?.length === 0)
					errorMessage = 'Address zipcode is missing, please update your address from profile section';
				if (errorMessage.length > 0) error = true;
			}
			// console.log(address)
			let shipAddress = (type === 'SHIPPING_ADDRESS' ? index : this.state.selectedShippingAddress);
			sessionStorage.setItem('selectedShippingAddress', currentShippingAddress.id)
			this.setState({
				selectedShippingAddress: shipAddress,
				selectedBillingAddress:
					this.state.billingSameAsShipping || type === 'BILLING_ADDRESS'
						? index
						: this.state.selectedBillingAddress,
				intialAddress: {
					...this.state.intialAddress,
					...newAdd,
				},
				isError: error,
				errorMessage,
			});
			// if(isChanged){
			// 	sessionStorage.setItem("taxZipCode", newAdd.shippingZipCode);
			// 	this.checkTaxDetails(newAdd,true,'shipping');
			// }
				
		}
	};

	getAddress = (addr) => {
		var address = '';
		address += !!addr && !!addr.addressLine1 ? addr.addressLine1 + ', ' : '';
		address += !!addr && !!addr.addressLine2 ? addr.addressLine2 + ', ' : '';
		address += !!addr && !!addr.city ? addr.city + ', ' : '';
		address += !!addr && !!addr.state ? addr.state + ', ' : '';
		address += !!addr && !!addr.zip ? addr.zip : '';
		return address;
	};

	getInitialShippingAddress = (addr) => {
		var address = '';
		address += !!addr && !!addr.shippingAddressLine1 ? addr.shippingAddressLine1 + ', ' : '';
		address += !!addr && !!addr.shippingAddressLine2 ? addr.shippingAddressLine2 + ', ' : '';
		address += !!addr && !!addr.shippingCity ? addr.shippingCity + ', ' : '';
		address += !!addr && !!addr.shippingState ? addr.shippingState + ', ' : '';
		address += !!addr && !!addr.shippingZipCode ? addr.shippingZipCode : '';
		return address;
	};

	getInitialBillingAddress = (addr) => {
		var address = '';
		address += !!addr && !!addr.billingAddressLine1 ? addr.billingAddressLine1 + ', ' : '';
		address += !!addr && !!addr.billingAddressLine2 ? addr.billingAddressLine2 + ', ' : '';
		address += !!addr && !!addr.billingCity ? addr.billingCity + ', ' : '';
		address += !!addr && !!addr.billingState ? addr.billingState + ', ' : '';
		address += !!addr && !!addr.billingZipCode ? addr.billingZipCode : '';
		return address;
	};

	getShippingAddressDetail(address) {
		return (
			<>
				<Grid item xs={12} sm={12} className="userinfo-wrapper view-only">
					<div className="key full">{address.shippingFirstname}</div>
				</Grid>
				<Grid item xs={12} sm={12} className="userinfo-wrapper view-only">
					<div className="value full">{this.getInitialShippingAddress(address)}</div>
				</Grid>
			</>
		);
	}

	getBillingAddressDetail(address) {
		return (
			<>
				<Grid item xs={12} sm={12} className="userinfo-wrapper view-only">
					<div className="key full">{address.billingFirstname}</div>
				</Grid>
				<Grid item xs={12} sm={12} className="userinfo-wrapper view-only">
					<div className="value full">{this.getInitialBillingAddress(address)}</div>
				</Grid>
			</>
		);
	}

	cancelAddAddress = () => {
		sessionStorage.removeItem('formAddValues');
		this.setState({
			addNewAddress: false
		})
	}

	saveAddAddress = (values) => {
		this.createAddress(values)
	}

	getLastName(str) {
		if (!!str) {
			var n = str.trim().split(" "); //split by space

			if (n.length > 1) {
				n.shift() // remove first element
			}
			return n.join(" ");  //Get last element
		}
		return ""
	}

	getFirstName(str) {
		if (!!str) {
			var res = str.trim().split(" ");  //split by space
			if (res.length >= 1) {
				return res[0]; // take first element
			}
		}
		return "";
	}

	cancelChangeAddress = () => {
		if (!this.state.changedAddress)
			sessionStorage.removeItem('formChangeValues');
		else {
			var address = this.state.intialAddress
			var newAdd = {
				billingFirstname: this.getFirstName(address.billingFirstname),
				billingLastname: this.getLastName(address.billingLastname),
				billingAddressLine1: address.billingAddressLine1,
				billingAddressLine2: address.billingAddressLine2,
				billingCity: address.billingCity,
				billingState: address.billingState,
				billingCountry: address.billingCountry,
				billingZipCode: address.billingZipCode,
			};
			sessionStorage.setItem('formChangeValues', JSON.stringify(newAdd));
		}
		this.setState({
			changeBillingAddress: false
		})
	}

	saveBillingAddress = (address) => {
		if (this.state.billingSameAsShipping) {
			this.setState({
				changedAddress: true,
				changeBillingAddress: false
			});
		} else {
			let newAdd = { ...this.state.intialAddress };

			newAdd = {
				...newAdd,
				billingFirstname: address.billingFirstname + " " + address.billingLastname,
				billingLastname: address.billingFirstname + " " + address.billingLastname,
				billingAddressLine1: address.billingAddressLine1,
				billingAddressLine2: address.billingAddressLine2,
				billingCity: address.billingCity,
				billingState: address.billingState,
				billingCountry: address.billingCountry,
				billingZipCode: address.billingZipCode,
			};

			this.setState({
				intialAddress: {
					...this.state.intialAddress,
					...newAdd,
				},
				changedAddress: true,
				changeBillingAddress: false
			}, () => {
				sessionStorage.setItem('regData', JSON.stringify(this.state.intialAddress));
			});
		}
	}

	onCheckChange = (checked) => {
		var newAdd = { ...this.state.intialAddress };
		var pos = 0;
		if (checked) {
			pos = this.state.selectedShippingAddress;
			newAdd = {
				...newAdd,
				billingFirstname: newAdd.shippingFirstname,
				billingLastname: newAdd.shippingLastname,
				billingAddressLine1: newAdd.shippingAddressLine1,
				billingAddressLine2: newAdd.shippingAddressLine2,
				billingCity: newAdd.shippingCity,
				billingState: newAdd.shippingState,
				billingCountry: newAdd.shippingCountry,
				billingZipCode: newAdd.shippingZipCode,
				billingSameAsShipping: checked
			};
		} else if (this.state.changedAddress && sessionStorage.getItem('formChangeValues') !== null) {
			pos = -1;
			var address = JSON.parse(sessionStorage.getItem('formChangeValues'))
			if (address && address !== null) {
				newAdd = {
					...newAdd,
					billingFirstname: address.billingFirstname + " " + address.billingLastname,
					billingLastname: address.billingFirstname + " " + address.billingLastname,
					billingAddressLine1: address.billingAddressLine1,
					billingAddressLine2: address.billingAddressLine2,
					billingCity: address.billingCity,
					billingState: address.billingState,
					billingCountry: address.billingCountry,
					billingZipCode: address.billingZipCode,
					billingSameAsShipping: checked
				};
			}
		} else {
			pos = -1;
			var address = this.state.accountAddress
				.filter((add, index) => {
					if (add.addressType === 'BILLING_ADDRESS') {
						pos = index;
						return add;
					}
				})
				.shift();
			if (address && address !== null) {
				newAdd = {
					...newAdd,
					billingFirstname: address.name,
					billingLastname: address.name,
					billingAddressLine1: address.addressLine1,
					billingAddressLine2: address.addressLine2,
					billingCity: address.city,
					billingState: address.state,
					billingCountry: address.country,
					billingZipCode: address.zip,
					billingSameAsShipping: checked
				};
			} else {
				newAdd = {
					...newAdd,
					billingFirstname: '',
					billingLastname: '',
					billingAddressLine1: '',
					billingAddressLine2: '',
					billingCity: '',
					billingState: '',
					billingCountry: countryOptions[0].value,
					billingZipCode: '',
					billingSameAsShipping: checked,

				};
			}
		}

		this.setState({
			billingSameAsShipping: checked,
			intialAddress: {
				...this.state.intialAddress,
				...newAdd,
			},
			selectedBillingAddress: pos,
		}, () => {
			sessionStorage.setItem('billingSameAsShipping', checked);
		});
	};

	handleClose = () => {
		this.setState({ errorMessage: '', isError: false });
		document.getElementById('body').classList.remove('has-toast');
	};

	render() {
		return (
			<div className="checkout-view">
				<h2>Checkout information</h2>
				{this.state.isError && this.state.errorMessage.length > 0 ? (
					<Snackbar
						open={true}
						anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
						className="snackbar snackbar-full"
					>
						<MuiAlert variant="filled" onClose={this.handleClose} severity="error">
							{this.state.errorMessage}
						</MuiAlert>
					</Snackbar>
				) : (
						''
					)}
				<ViewCustomer
					fromRegister={false}
					editContact={this.state.editContact}
					intialAddress={this.state.intialAddress}
					userInfo={this.props.userInfo}
					createCustomer={this.props.createCustomer}
					updateIntialAddress={(intialAddressNew) =>
						this.setState({
							intialAddress: intialAddressNew
						})
					}
					updateEditFields={() => this.setState({
						editContact: true,
						editShipping: false,
						changeBillingAddress: false
					})}
					updateEditContact={() => this.setState({
						editContact: false
					})}
				/>

				<div className="checkout-content-body">
					<div className="checkout-content-body-inner">
						<div className="form-card">
							<Grid item xs={12} sm={12} className="userinfo-wrapper userinfo-wrapper-head">
								<Grid item xs container>
									<h3>Shipping Address</h3>
								</Grid>

								{!this.state.editShipping && <Grid item>
									<Button
										variant="contained"
										color="primary"
										className={'btn-link'}
										id="changeShipping"
										aria-label="change Shipping Address"
										onClick={() => {
											this.setState({
												editShipping: !this.state.editShipping,
												editContact: false,
												changeBillingAddress: false
											});
										}}
									>
										Change
									</Button>
								</Grid>}
							</Grid>
							<Grid container spacing={0} className="form-card-container">
								{this.state.editShipping ? (
									<Grid item xs={12} sm={12}>
										<div className="val">Select another address to ship</div>
										{this.getAllAddress('SHIPPING_ADDRESS')}
									</Grid>
								) : (
										this.getShippingAddressDetail(this.state.intialAddress)
									)}
								{this.state.editShipping && <Grid item xs={12} sm={12}>
									<div className="text-right">
										<Button
											variant="contained"
											color="primary"
											className={'btn-mui btn-blue'}
											id="changeShipping"
											aria-label="change Shipping Address"
											onClick={() => {
												this.changeShippingAddress()
												this.setState({
													editShipping: !this.state.editShipping,
													editContact: false,
													changeBillingAddress: false
												});
											}}
										>
											Save
										</Button>
									</div>
								</Grid>}
								{(!this.primaryAddress ||
									this.primaryAddress?.addresLine1 === null ||
									this.state.editShipping) && (
										<Grid item xs={12} sm={12} className="userinfo-wrapper">
											{this.state.addNewAddress ? (
												this.onAddNewAddress()
											) : (
													<Button
														variant="contained"
														color="primary"
														className={'btn-link'}
														id="addNewAddress"
														aria-label="Add a New Address"
														onClick={(e) => {
															e.preventDefault()
															if (this.totalShippingAddress >= (
																this.props.shippingMessage
																	&& this.props.shippingMessage.maxShippingAddress
																	&& this.props.shippingMessage.maxShippingAddress.length > 0
																	? this.props.shippingMessage.maxShippingAddress : 5
															)) {
																this.setState({
																	errorMessage: this.props.shippingMessage
																		&& this.props.shippingMessage.shippingLimitMessage
																		&& this.props.shippingMessage.shippingLimitMessage.length > 0
																		? this.props.shippingMessage.shippingLimitMessage : 'Maximum 5 shipping addresses. Please update an existing address.',
																	isError: true
																}, () => {
																	document.getElementById('body').classList.add('has-toast')
																	setTimeout(() => {
																		this.setState({
																			errorMessage: '',
																			isError: false
																		}, () => document.getElementById('body').classList.remove('has-toast'))
																	}, 5000);
																})
															} else {
																this.setState({
																	addNewAddress: true,
																});
															}
														}}
													>
														Add a New Address
													</Button>
												)}
										</Grid>
									)}
							</Grid>

							<Grid
								container
								spacing={0}
								justify="space-between"
								alignItems="center"
								className="mb-15"
							/>
							<Grid
								container
								spacing={0}
								justify="space-between"
								alignItems="center"
								className="mb-15"
							/>
							<Grid item xs={12} sm={12} className="userinfo-wrapper userinfo-wrapper-head">
								<Grid item xs container>
									<h3>Billing Address</h3>
								</Grid>
								{!this.state.changeBillingAddress &&
									<Grid item>
										<Button
											variant="contained"
											color="primary"
											className={'btn-link'}
											id="changeBilling"
											aria-label="Change Billing Address"
											onClick={() => {
												this.setState({
													changeBillingAddress: true,
													editContact: false,
													editShipping: false
												});
											}}
										>
											Change
									</Button>
									</Grid>}
							</Grid>

							<Grid container spacing={0} className="form-card-container has-billing">
								{/* <!-- Billing --> */}
								{!this.state.changeBillingAddress &&
									<Grid item xs={12} sm={12} className="userinfo-wrapper">
										<Grid item xs={12} sm={12} className="grid-checkbox">
											<FormControlLabel
												className="rememberCheck"
												style={{
													marginBottom: '0px',
												}}
												htmlFor="isBilling"
												control={
													<Checkbox
														className="checkBx"
														id="isBilling"
														name="isBilling"
														checked={this.state.billingSameAsShipping}
														disabled={this.state.isDisabledBillingfield}
														color="primary"
														onChange={(event) => this.onCheckChange(event.target.checked)}
													/>
												}
												label="Billing Information same as Shipping information"
											/>
										</Grid>
									</Grid>}

								{this.state.changeBillingAddress ? (
									<Grid item xs={12} sm={12} className="userinfo-wrapper">
										{this.onChangeBillingAddress()}
									</Grid>
								) :
									!this.state.billingSameAsShipping &&
									this.getBillingAddressDetail(this.state.intialAddress)}
							</Grid>

						</div>
					</div>
				</div>

				<div className="button-group text-center">
					<a
						className={'btn-mui btn-red'}
						id="keepShopping"
						aria-label="keep Shopping"
						onClick={() => {
							trackEvent(EventNames.Action.ACTION_KEEP_SHOPPING, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname)
							this.setState({ activeStep: 0 }, () => this.props.history.push('/productlist'));
						}}
					>
						Keep Shopping
					</a>

					<Button
						type="submit"
						disabled={
							!this.props.hasMinTotal ||
							this.state.selectedShippingAddress === -1 ||
							(this.state.selectedBillingAddress === -1 && !this.state.changedAddress && !this.state.billingSameAsShipping) ||
							this.state.isError ||
							this.state.addNewAddress ||
							this.state.editShipping ||
							this.state.editContact ||
							this.state.changeBillingAddress
						}
						variant="contained"
						color="primary"
						className="btn-mui btn-blue"
						id="ContinueRegistration"
						aria-label="Continue Registration"
						onClick={() => {
							sessionStorage.setItem('regData', JSON.stringify(this.state.intialAddress));
							trackEvent(EventNames.Action.ACTION_CHECKOUT, EventNames.Event.EVENT_BUTTON_PRESS, window.location.origin, window.location.pathname, { checkoutData: this.state.intialAddress, userLoggedIn: true})
							this.props.showPayment();
						}}
					>
						Continue
					</Button>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		shippingMessage: state.reducer.minAmount
	};
};
export default connect(mapStateToProps)(withRouter(CheckoutDetails));