import React, { Component } from "react";
import {
  Button,
  FormControl,
  Card,
  Grid,
  InputLabel,
  OutlinedInput,
  Typography,
} from "@material-ui/core";
import { ErrorMessage, Formik, Form } from "formik";
import * as Yup from "yup";
import UrlConstants from "config/UrlConstants";
import ReactSelect from "react-select";
import usStateData from "../../data/states_list.json";
import AddressSuggestionDialog from "./AddressSuggestionDialog";
import APIUtil from "config/APIUtil";
import { updatePrice } from "./../../Utils";
import AutoCompleteSelect from "../../commonComponents/autocompletetext/AutoCompleteSelect";

class EditShipping extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ZipfailMessage: null,
      submitDisabled: true,
      initialValues: null,
    };
  }
  componentDidMount() {
    this.setState({ initialValues: this.props.customerDetails });
  }

  hideDialog(val) {
    this.setState({
      ["AddressSuggestionDialog"]: null,
    });
  }

  cancelDialog() {
    this.setState({
      ["AddressSuggestionDialog"]: null,
      submitDisabled: true,
    });
  }

  getRequest(values) {
    var shippingZip = !!values.shippingZip ? values.shippingZip : null;
    var shippingState = !!values.shippingState ? values.shippingState : null;
    var shippingCity = !!values.shippingCity ? values.shippingCity : null;
    var shippingCountry = UrlConstants.defaultCountry;
    var shippingAddressline1 = !!values.shippingAddressline1
      ? values.shippingAddressline1
      : "";
    var shippingAddressline2 = !!values.shippingAddressline2
      ? values.shippingAddressline2
      : "";
    //Destination Data
    let addObj = {};
    addObj["postalArea"] = shippingZip;
    addObj["stateOrProvinceName"] = shippingState;
    //Address Data
    let addressData = {};
    addressData["Region"] =
      (!!shippingCity ? shippingCity : "") +
      (!!shippingState ? ", " + shippingState : "") +
      (!!shippingZip ? ", " + shippingZip : "");
    addressData["CountryCode"] = shippingCountry;
    addressData["AddressLine"] = [
      shippingAddressline1 + " " + shippingAddressline2,
    ];
    // Tax post data Object
    let objData = {};
    objData["destination"] = addObj;
    objData["isTaxFalse"] = true;
    objData["isAddress"] = true;
    objData["Address"] = addressData;
    objData["documentDate"] = new Date();
    objData["transactionType"] = "SALE";
    return objData;
  }

  addressValidate(values) {
    // Texts for Address Validation
    let inValidShippingAddress = ["P O BOX", "P.O BOX", "P.O.BOX", "PO BOX"];
    if (
      !!values.shippingZip &&
      values.shippingZip.length == "5" &&
      inValidShippingAddress.indexOf(values.shippingAddressline1) === -1 &&
      inValidShippingAddress.indexOf(values.shippingAddressline2) === -1
    ) {
      var objData = this.getRequest(values);
      let url = UrlConstants.orderTax;
      this.props.isLoading(true);
      APIUtil.postMethod(url, objData, true)
        .then((response) => {
          if (
            response.data.success === true &&
            response.data.zipSuccess === true
          ) {
            if (
              response.data.addressSuccess === false &&
              response.data.candidateFound === false
            ) {
              this.props.isLoading(false);
              this.setState({
                submitDisabled: true,
                ZipfailMessage: response.data.addressErrorMessage,
              });
              return false;
            } else if (!response.data.candidateFound) {
              this.setState({ submitDisabled: false, ZipfailMessage: "" });
              this.props.isLoading(false);
              return false;
            } else {
              this.setState({ submitDisabled: false });
              if (!!response.data.candidateFound) {
                // Remove the error validation message first
                this.setState({ ZipfailMessage: "" });
                this.props.isLoading(false);
                var addrArray =
                  !!response.data.data &&
                  !!response.data.data.address &&
                  !!response.data.data.address.data &&
                  !!response.data.data.address.data.XAVResponse
                    ? response.data.data.address.data.XAVResponse.Candidate
                    : [];
                var finalAdata = addrArray;
                if (!Array.isArray(addrArray)) {
                  finalAdata = [addrArray];
                }
                this.setState({
                  ["AddressSuggestionDialog"]: (
                    <AddressSuggestionDialog
                      onSubmit={(values) =>
                        this.setState({ initialValues: values }, () =>
                          this.hideDialog()
                        )
                      }
                      isAddress="shipping"
                      initialValues={values}
                      manageSuggestedAddress={() =>
                        this.manageSuggestedAddress()
                      }
                      hideDialog={() => this.hideDialog()}
                      cancelDialog={() => this.cancelDialog()}
                      addressVerifyData={finalAdata}
                    />
                  ),
                });
                return false;
              }
            }
          } else {
            setTimeout(() => {
              this.props.isLoading(false);
            }, 100);
            this.setState({
              submitDisabled: true,
              ZipfailMessage: response.data.zipErrorMessage,
            });
          }
        })
        .catch((response) => {
          this.setState({
            ZipfailMessage: UrlConstants.errorMsg,
            submitDisabled: true,
          });
        });
    } else {
      this.setState({ submitDisabled: true });
    }
  }

  render() {
    const addressPattern = /\bP(ost|ostal)?([ \.]*(O|0)(ffice)?)?([ \.]*Box)\b/i;
    const zipRegex = /^[0-9\b]+$/;
    const alphaNumericRegix = /^[a-zA-Z0-9 '.#,/'-]*$/;
    var index = this.props.index;
    return (
      <div className="profile-info">
        {/* Contact info */}
        {!!this.state.initialValues ? (
          <Formik
            //key={this.state.initialValues.shipping.length}
            enableReinitialize
            initialValues={this.state.initialValues}
            validationSchema={Yup.object().shape({
              shippingFirstname: Yup.string()
                .trim()
                .required("First Name is required.")
                .min(2, "Name must be at least 2 characters.")
                .test(
                  "shippingFirstname",
                  "First Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.& ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              shippingLastname: Yup.string()
                .trim()
                .min(2, "Name must be at least 2 characters.")
                .required("Last Name is required.")
                .test(
                  "shippingLastname",
                  "Last Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.& ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              shippingAddressline1: Yup.string()
                .trim()
                .required("Address is Required.")
                .test(
                  "shippingAddressline1",
                  UrlConstants.poBoxErrorMsg.replace(
                    /##ADDTYPE##/g,
                    function () {
                      return "shipping";
                    }
                  ),
                  function (value) {
                    if (addressPattern.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              shippingAddressline2: Yup.string()
                .nullable()
                .notRequired()
                .test(
                  "shippingAddressline2",
                  UrlConstants.poBoxErrorMsg.replace(
                    /##ADDTYPE##/g,
                    function () {
                      return "shipping";
                    }
                  ),
                  function (value) {
                    if (addressPattern.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              shippingZip: Yup.string()
                .trim()
                .matches(/^[0-9]{5}$/, "Zip code must be exactly 5 digits.")
                .required("Zip is required"),
              shippingState: Yup.string().trim().required("State is required."),
              shippingCity: Yup.string().trim().required("City is required."),
            })}
            onSubmit={(values, { resetForm }) => {
              this.props.handleSubmit(values, index);
            }}
          >
            {({
              resetForm,
              setFieldValue,
              setFieldTouched,
              errors,
              values,
              touched,
            }) => (
              <Form
                id="startSubscriptionForm"
                name="form1"
                autoComplete="startSubscriptionForm"
              >
                <Grid container spacing={6} justify="space-between">
                  <Grid item xs={12} sm={12}>
                    <Grid
                      container
                      spacing={2}
                      alignItems="center"
                      className="title-mini title-mini-shipping"
                    >
                      <Grid item xs container>
                        <Typography variant="h2" component="h2">
                          Shipping Address {index > 0 ? index + 1 : ""}
                        </Typography>
                      </Grid>
                    </Grid>
                    <label
                      htmlFor={"prevent_autofill_shipping" + index}
                      className="heading-hidden"
                    >
                      Auto Fill
                    </label>
                    <input
                      type="text"
                      name="prevent_autofill_shipping"
                      readOnly
                      id={"prevent_autofill_shipping" + index}
                      value=""
                      style={{ display: "none" }}
                    />
                    {/* Contact info */}
                    <Card className="profile-card profile-card-form">
                      <div className="shipping-form">
                        <Grid
                          container
                          spacing={6}
                          className="form-card-container"
                          key={index}
                          style={{ marginTop: "20px !important" }}
                        >
                          <Grid item xs={12} sm={12}>
                            <span style={{ color: "red" }}>
                              {this.state.ZipfailMessage}
                            </span>
                          </Grid>
                          {/* First Name */}
                          <Grid item xs={12} sm={6}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingFirstname` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                First Name<span className="text-danger">*</span>
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingFirstname`}
                                id={
                                  `shippingFirstname` +
                                  (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingFirstname` +
                                  (index === 0 ? "" : index + 1)
                                }
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                value={values.shippingFirstname}
                                onChange={(e) =>
                                  setFieldValue(
                                    `shippingFirstname`,
                                    e.target.value
                                  )
                                }
                                onBlur={(e) => {
                                  setFieldTouched(`shippingFirstname`, true);
                                }}
                                error={Boolean(
                                  touched.shippingFirstname &&
                                    errors.shippingFirstname
                                )}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingFirstname`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>
                          {/* Last Name */}
                          <Grid item xs={12} sm={6}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingLastname` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                Last Name<span className="text-danger">*</span>
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingLastname`}
                                id={
                                  `shippingLastname` +
                                  (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingLastname` +
                                  (index === 0 ? "" : index + 1)
                                }
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                value={values.shippingLastname}
                                onChange={(e) =>
                                  setFieldValue(
                                    `shippingLastname`,
                                    e.target.value
                                  )
                                }
                                onBlur={(e) => {
                                  setFieldTouched(`shippingLastname`, true);
                                }}
                                error={Boolean(
                                  touched.shippingLastname &&
                                    errors.shippingLastname
                                )}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingLastname`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>
                          {/* Address Line 1 */}
                          <Grid item xs={12} sm={6}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingAddressline1` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                Address<span className="text-danger">*</span>
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingAddressline1`}
                                id={
                                  `shippingAddressline1` +
                                  (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingAddressline1` +
                                  (index === 0 ? "" : index + 1)
                                }
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                value={values.shippingAddressline1}
                                onChange={(e) => {
                                  if (alphaNumericRegix.test(e.target.value)) {
                                    setFieldValue(
                                      `shippingAddressline1`,
                                      e.target.value
                                    );
                                  }
                                }}
                                onBlur={(e) => {
                                  setFieldTouched(`shippingAddressline1`, true);

                                  if (
                                    !!values.shippingAddressline1 &&
                                    !!values.shippingState &&
                                    !!values.shippingCity &&
                                    !!values.shippingZip
                                  ) {
                                    this.addressValidate(values);
                                  }
                                }}
                                error={Boolean(
                                  touched.shippingAddressline1 &&
                                    errors.shippingAddressline1
                                )}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingAddressline1`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>
                          {/* Address 2 */}
                          <Grid item xs={12} sm={6}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingAddressline2` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                Address 2 (optional)
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingAddressline2`}
                                id={
                                  `shippingAddressline2` +
                                  (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingAddressline2` +
                                  (index === 0 ? "" : index + 1)
                                }
                                value={values.shippingAddressline2}
                                onChange={(e) => {
                                  if (alphaNumericRegix.test(e.target.value)) {
                                    setFieldValue(
                                      `shippingAddressline2`,
                                      e.target.value
                                    );
                                  }
                                }}
                                onBlur={(e) => {
                                  setFieldTouched(`shippingAddressline2`, true);
                                }}
                                error={Boolean(
                                  touched.shippingAddressline2 &&
                                    errors.shippingAddressline2
                                )}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingAddressline2`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>

                          {/* City */}
                          <Grid item xs={12} sm={6}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingCity` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                {" "}
                                City<span className="text-danger">*</span>{" "}
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingCity`}
                                id={
                                  `shippingCity` +
                                  (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingCity` +
                                  (index === 0 ? "" : index + 1)
                                }
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                onChange={(e) => {
                                  if (alphaNumericRegix.test(e.target.value)) {
                                    setFieldValue(
                                      `shippingCity`,
                                      e.target.value
                                    );
                                    this.addressValidate(values);
                                  }
                                }}
                                onBlur={(e) => {
                                  setFieldTouched(`shippingCity`, true);

                                  if (
                                    !!values.shippingAddressline1 &&
                                    !!values.shippingState &&
                                    !!values.shippingCity &&
                                    !!values.shippingZip
                                  ) {
                                    this.addressValidate(values);
                                  }
                                }}
                                value={values.shippingCity}
                                error={Boolean(
                                  touched.shippingCity && errors.shippingCity
                                )}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingCity`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>

                          {/* State */}
                          <Grid item xs={12} sm={4}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <label
                                htmlFor={
                                  `shippingState` +
                                  (index === 0 ? "" : index + 1)
                                }
                              >
                                <AutoCompleteSelect
                                  id={
                                    `shippingState` +
                                    (index === 0 ? "" : index + 1)
                                  }
                                  label="State"
                                  touched={touched.shippingState}
                                  options={usStateData}
                                  defaultValue={usStateData.find(
                                    (option) =>
                                      option.value === values.shippingState
                                  )}
                                  value={usStateData.find(
                                    (option) =>
                                      option.value === values.shippingState
                                  )}
                                  onChange={(e, value) => {
                                    if (value !== null) {
                                      setFieldValue(
                                        "shippingState",
                                        value.value
                                      );
                                    }
                                  }}
                                  onBlur={(e, value) => {
                                    setFieldTouched(`shippingState`, true);
                                    if (
                                      !!values.shippingAddressline1 &&
                                      !!values.shippingState &&
                                      !!values.shippingCity &&
                                      !!values.shippingZip
                                    ) {
                                      this.addressValidate(values);
                                    }
                                  }}
                                  error={Boolean(
                                    touched.shippingState &&
                                      errors.shippingState
                                  )}
                                />
                                {/* <ReactSelect
                                  id={
                                    `shippingState` +
                                    (index === 0 ? "" : index + 1)
                                  }
                                  name={`shippingState`}
                                  value={usStateData.find(
                                    (option) =>
                                      option.value === values.shippingState
                                  )}
                                  onChange={(e) => {
                                    setFieldValue(`shippingState`, e.value);
                                  }}
                                  onBlur={(e) => {
                                    setFieldTouched(`shippingState`, true);

                                    if (
                                      !!values.shippingAddressline1 &&
                                      !!values.shippingState &&
                                      !!values.shippingCity &&
                                      !!values.shippingZip
                                    ) {
                                      this.addressValidate(values);
                                    }
                                  }}
                                  touched={touched.shippingState}
                                  options={usStateData}
                                  className="react-select-theme react-select-big required"
                                  placeholder="State"
                                  error={Boolean(
                                    touched.shippingState &&
                                      errors.shippingState
                                  )}
                                  aria-labelledby={
                                    `shippingState` +
                                    (index === 0 ? "" : index + 1)
                                  }
                                /> */}
                              </label>
                              <ErrorMessage
                                component="div"
                                name={`shippingState`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>

                          {/* Zip Code */}
                          <Grid item xs={12} sm={2}>
                            <FormControl
                              fullWidth
                              className="form-group"
                              variant="outlined"
                            >
                              <InputLabel
                                className="form-label"
                                htmlFor={
                                  `shippingZip` + (index === 0 ? "" : index + 1)
                                }
                              >
                                Zip Code<span className="text-danger">*</span>
                              </InputLabel>
                              <OutlinedInput
                                autoComplete={`shippingZip`}
                                id={
                                  `shippingZip` + (index === 0 ? "" : index + 1)
                                }
                                name={
                                  `shippingZip` + (index === 0 ? "" : index + 1)
                                }
                                inputProps={{
                                  "aria-required": "true",
                                  role: "alert",
                                  "aria-invalid": "false",
                                }}
                                onChange={(e) => {
                                  if (
                                    zipRegex.test(e.target.value) ||
                                    e.target.value === ""
                                  ) {
                                    if (e.target.value.length <= 5) {
                                      setFieldValue(
                                        `shippingZip`,
                                        e.target.value
                                      );
                                    }
                                  }
                                }}
                                onBlur={(e) => {
                                  setFieldTouched(`shippingZip`, true);

                                  if (
                                    !!values.shippingAddressline1 &&
                                    !!values.shippingState &&
                                    !!values.shippingCity &&
                                    !!values.shippingZip
                                  ) {
                                    this.addressValidate(values);
                                  }
                                }}
                                error={Boolean(
                                  touched.shippingZip && errors.shippingZip
                                )}
                                value={values.shippingZip}
                              />
                              <ErrorMessage
                                component="div"
                                name={`shippingZip`}
                                className="text-danger"
                              />
                            </FormControl>
                          </Grid>
                        </Grid>
                      </div>
                    </Card>
                    <Grid item xs={12} sm={12}>
                      <Grid item>
                        <div className="text-right">
                          <Button
                            type="button"
                            onClick={() => {
                              if (!!values.id) {
                                this.props.setShipping(true, index);
                              } else {
                                this.props.removeShippingAddress(index);
                              }
                            }}
                            variant="contained"
                            color="primary"
                            className="btn-mui btn-outlined btn-sm"
                            id={`${
                              !!values.id
                                ? "cancelshippingaddress"
                                : "removeshippingaddress"
                            } ${index === 0 ? "" : index + 1}`}
                            aria-label={`${
                              !!values.id
                                ? "Cancel shipping address"
                                : "Remove shipping address"
                            } ${index === 0 ? "" : index + 1}`}
                          >
                            {" "}
                            {!!values.id ? "Cancel" : "Cancel"}{" "}
                          </Button>
                          <Button
                            type="submit"
                            variant="contained"
                            color="primary"
                            disabled={this.state.submitDisabled}
                            className="btn-mui btn-blue btn-sm"
                            id={
                              "saveshippingaddress" +
                              (index === 0 ? "" : index + 1)
                            }
                            aria-label={
                              "save shipping address" +
                              (index === 0 ? "" : index + 1)
                            }
                          >
                            {" "}
                            Save{" "}
                          </Button>
                        </div>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            )}
          </Formik>
        ) : (
          ""
        )}
        {this.state["AddressSuggestionDialog"]}
      </div>
    );
  }
}
export default EditShipping;
